import * as React from 'react';
import ReactDOM from 'react-dom';
import * as PropTypes from 'prop-types';
import { ImageLoadCycle, IPreviewProps, IPreviewState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';
import { emptyFn } from '../../utils/helper';
import ua from '@tencent/docs-user-agent';
import Loading from '../Loading';
import show from './show';
import { decideKeepDom } from '../../utils/keepDom';
import LoadTask from './LoadTask';

export const PREVIEW_TRANSITION_DURATION = 300;
const PREVIEW_CLOSE_GAP_DURATION = 500;

export default class Preview extends React.Component<IPreviewProps, IPreviewState> {
    static defaultProps: TDefaultProps<IPreviewProps>;
    static propTypes: TPropTypes<IPreviewProps>;
    static show: typeof show;


    /* 整个查看器的容器，与视口大小相等 */
    containerRef = React.createRef<HTMLDivElement>();

    /* 图片包裹的容器，大小等于图片当前大小 */
    imageWrapperRef = React.createRef<HTMLDivElement>();

    /* 图片本身 */
    imageRef = React.createRef<HTMLImageElement>();

    /* 保存 html 根元素的 overflow 属性 */
    globalScrollProperty?: string;

    /* 当前是否正在拖动 */
    isMouseDragging = false;

    /* 当前图片缩放系数，以初始时适应屏幕的大小为 1 */
    currentImageScale = 1;

    /* 当前图片左侧移出屏幕的水平位移，负值 */
    currentImageTranslateX = 0;

    /* 当前图片上方移除屏幕的垂直位移，负值 */
    currentImageTranslateY = 0;

    /* 清除 Hover 态的 timeout */
    clearForceHoverTimeout: any;

    /* 图片原本的长宽 */
    naturalWidth: number;
    naturalHeight: number;

    /* 图片最小的放缩比例 */
    minScale = 0.5;

    /* 图片适应显示的比例 */
    adaptScale = 1;

    /* 适应图片时进行图片位置偏移 */
    translateY = 0;

    /* 加载图片的任务数组 */
    loadTasks: LoadTask[] = [];

    /* 已经加载完毕的图片 */
    loadedUrlList: ImageLoadCycle[] = [];

    /* 这次点击操作，是否准备关闭查看器 */
    readyClose: boolean = false;
    /** open 的时间 */
    timer: Date = new Date();
    state: IPreviewState = {
        currentIndex: this.props.initialIndex!,
        imageState: 'loading',
        isWidgetsVisible: true,
        isForceHover: false,
        imageMarginStyle: {},
        imageWrapperStyle: {},
        scaleUpDisable: false,
        scaleDownDisable: false,
        showFitScreenButton: false,
        showActualSizeButton: false,
    };

    componentDidMount() {
        window.addEventListener('keydown', this.handleKeyDown, true);
        this.timer = new Date();
        if (this.containerRef.current) {
            this.containerRef.current.onwheel = (e) => {
                if (e.ctrlKey) {
                    e.stopPropagation();
                    e.preventDefault();
                }
            };
        }
    }

    componentDidUpdate(prevProps: IPreviewProps, prevState: IPreviewState) {
        // 每次打开组件的时候 需要记录打开时间
        if (!prevProps.visible && this.props.visible) {
            this.timer = new Date();
        }
        // 监听页面切换，触发 onChange 事件
        if (prevState.currentIndex !== this.state.currentIndex) {
            this.props.onChange!(this.state.currentIndex);
        }

        // 响应 urlList 或者 initialIndex 的变化
        // 如果 initialIndex 也变了，就跳到新的页码，否则尽可能留在原来的页码
        if (prevProps.initialIndex !== this.props.initialIndex) {
            this.updateCurrentPage(this.props.initialIndex);
        } else if (prevProps.urlList !== this.props.urlList) {
            this.updateCurrentPage();
        }
    }

    /**
     * 如果组件直接被卸载，解除阻止滚动
     */
    componentWillUnmount() {
        window.removeEventListener('keydown', this.handleKeyDown, true);
        this.setPreventGlobalScrollEvents(false);
        clearTimeout(this.clearForceHoverTimeout);

        this.loadTasks.forEach((t) => {
            t.cancel();
        });

        this.loadTasks.length = 0;
    }

    canGoPrevPage() {
        const { urlList, cycle } = this.props;
        const { currentIndex } = this.state;
        return (urlList.length > 1 && cycle) || currentIndex > 0;
    }

    canGoNextPage() {
        const { urlList, cycle } = this.props;
        const { currentIndex } = this.state;
        return (urlList.length > 1 && cycle) || currentIndex < urlList.length - 1;
    }

    /**
     * 设置是否阻止全局滚动
     * 默认直接在 visible 时设置阻止，hidden 时取消阻止
     */
    setPreventGlobalScrollEvents(prevent: boolean = true) {
        if (prevent) {
            if (!this.globalScrollProperty) {
                this.globalScrollProperty = document.documentElement.style.overflow;
                document.documentElement.style.overflow = 'hidden';
            }
        } else {
            document.documentElement.style.overflow = this.globalScrollProperty || 'initial';
            this.globalScrollProperty = undefined;
        }
    }

    /**
     * 对 index 进行边界检查并跳到当前页面
     * - 如果不提供 pageIndex，默认尽可能留在原来的页码
     * - 如果提供 pageIndex，就去新的页码
     */
    updateCurrentPage(pageIndex = this.state.currentIndex, animated = true) {
        let { urlList } = this.props;
        if (!urlList.length) {
            this.requestClose();
        }
        let index = Math.max(0, Math.min(pageIndex, urlList.length - 1));
        this.setState({
            currentIndex: index,
            animationDirection:
                index < this.state.currentIndex ? 'right' : (
                    index > this.state.currentIndex ? 'left' : undefined
                )
        });
    }

    /**
     * 图片加载完毕后针对的进行屏幕适配
     */
    setImageAdapt = () => {
        const adaptScale = this.adaptImageScreenShow(
            this.imageRef.current?.width as number,
            this.imageRef.current?.height as number,
        );
        this.currentImageScale = adaptScale;
        this.adaptScale = adaptScale;
        this.scaleTo(adaptScale);

        this.setState({
            isForceHover: true,
        });
        if (this.clearForceHoverTimeout) {
            clearTimeout(this.clearForceHoverTimeout);
            this.clearForceHoverTimeout = null;
        }
        this.clearForceHoverTimeout = setTimeout(() => {
            this.setState({
                isForceHover: false
            });
            this.clearForceHoverTimeout = null;
        }, 3000);
    }

    /**
     * 图片是否可拖动
     */
    isDraggable() {
        const image = this.imageWrapperRef.current;
        const { currentImageScale } = this;
        if (!image) return false;
        return image.clientWidth * currentImageScale > window.innerWidth || image.clientHeight * currentImageScale > window.innerHeight;
    }

    /**
     * 缩放图片
     */
    scaleTo(scale: number) {
        const { minScale, imageWrapperRef } = this;
        const imageWrapper = imageWrapperRef.current;
        const maxScale = this.props.maxScale || 3;
        if (!imageWrapper) return;
        imageWrapper.style.transform = `scale(${scale}) translate(${this.currentImageTranslateX}px, ${this.currentImageTranslateY}px)`;
        this.currentImageScale = scale;
        if (scale === 1) {
            this.setState({ showActualSizeButton: false });
        } else {
            this.setState({ showActualSizeButton: true });
        }
        if (scale >= maxScale) {
            this.setState({ scaleUpDisable: true });
        } else if (scale <= minScale) {
            this.setState({ scaleDownDisable: true });
        } else {
            this.setState({ scaleUpDisable: false, scaleDownDisable: false });
        }
    }

    /**
     * 移动图片
     * - 会进行边界检查
     * - 参数 x 和 y 均为负值，表示图片的 translate 值
     */
    moveTo(x: number, y: number) {
        const imageWrapper = this.imageWrapperRef.current!;
        this.currentImageTranslateX = x;
        this.currentImageTranslateY = y;
        imageWrapper.style.transform = `scale(${this.currentImageScale}) translate(${x}px, ${y}px) `;
    }

    /**
     * windows 上火狐的滚轮和其他浏览器差异很大，这边单独处理
     */
    handleMouseWheelForFirefox(e: MouseWheelEvent) {
        let ty = e.deltaY;

        ty !== 0 && (ty = ty > 0
            ? Math.max(40, Math.min(ty * 40, 50)) : Math.min(-40, Math.max(ty * 40, -50)));

        this.scaleByWheel(ty);
    }

    handleMouseWheel = (e: React.WheelEvent<HTMLDivElement>) => {
        if (e.ctrlKey) return;
        e.preventDefault();

        // windows 上的 firefox 滚轮比较特殊，针对处理，走通用逻辑的话，滚动缩放会很慢
        if (ua.isFirefox && ua.isWindows) {
            this.handleMouseWheelForFirefox(e.nativeEvent);
            return;
        }

        this.scaleByWheel(e.deltaY);
    }

    scaleByWheel(deltaY: number) {
        const { currentImageScale, minScale } = this;
        let scale = currentImageScale + -1 * deltaY / 1000;

        const maxScale = this.props.maxScale || 3;
        // 浏览器缩放干不掉，只能忽略
        if (scale > maxScale) {
            scale = maxScale;
        } else if (scale < minScale) {
            scale = minScale;
        } else if (scale >= 0.96 && scale < 1) {
            scale = 1;
        } else if (scale <= 1.04 && scale > 1) {
            scale = 1;
        }

        this.scaleTo(scale);
        this.setImagePosition();
    }

    handleMouseStart = (e: React.MouseEvent<HTMLImageElement>) => {
        this.isMouseDragging = true;
        this.readyClose = false;
        e.stopPropagation();
        document.addEventListener('mousemove', this.handleMouseMove);
        document.addEventListener('mouseup', this.handleMouseEnd);
    }

    handleMouseMove = (e: MouseEvent) => {
        e.preventDefault();
        e.stopPropagation();
        // 注意，这里要判断 this.imageWrapperRef.current，因为可能拖拽的时候，按 esc 退出了
        if (this.isMouseDragging && this.imageWrapperRef.current) {
            const { currentImageTranslateX, currentImageTranslateY } = this;
            const imageWrapper = this.imageWrapperRef.current;
            const { left: leftDistance, top: topDistance, right, bottom } = imageWrapper.getBoundingClientRect();
            const bottomDistance = window.innerHeight - bottom;
            const rightDistance = window.innerWidth - right;
            let { movementX, movementY } = e;

            // 计算图片四边是否可以拖动
            // movement<0表示向左拖，此时若元素距离屏幕右边的距离 >=0 时表示已经无法继续拖动。
            if (movementX <= 0) {
                if (rightDistance > 0) movementX = 0;
            }
            if (movementX > 0) {
                if (leftDistance > 0) movementX = 0;
            }
            if (movementY <= 0) {
                if (bottomDistance > 0) movementY = 0;
            }
            if (movementY > 0) {
                if (topDistance > 0) movementY = 0;
            }
            movementX = Number(movementX);
            movementY = Number(movementY);
            let translateX = currentImageTranslateX + movementX / this.currentImageScale;
            let translateY = currentImageTranslateY + movementY / this.currentImageScale;
            this.moveTo(translateX, translateY);
        }
    }

    handleMouseEnd = (e: MouseEvent) => {
        this.isMouseDragging = false;

        document.removeEventListener('mousemove', this.handleMouseMove);
        document.removeEventListener('mouseup', this.handleMouseEnd);
    }

    handleKeyDown = (e: KeyboardEvent) => {
        if (!this.props.visible) return;
        if (['ArrowLeft', 'Left', 'a', 'h'].includes(e.key)) {
            this.prevPage();
        } else if (['ArrowRight', 'Right', 'd', 'l'].includes(e.key)) {
            this.nextPage();
        } else if (['-', 'ArrowDown', 'Down', 's', 'j'].includes(e.key)) {
            this.scaleImageDown();
        } else if (['=', '+', 'ArrowUp', 'Up', 'w', 'k'].includes(e.key)) {
            this.scaleImageUp();
        } else if (['0'].includes(e.key)) {
            this.scaleTo(1);
        } else if (['1'].includes(e.key)) {
            this.realScreenShow();
        } else if (['Escape', 'Esc', 'q'].includes(e.key)) {
            this.requestClose();
        }
        e.stopPropagation();
        e.preventDefault();
    }

    prevPage = () => {
        const { length } = this.props.urlList;
        if (this.canGoPrevPage()) {
            this.setState({
                currentIndex: (this.state.currentIndex - 1 + length) % length,
                imageState: 'loading',
                animationDirection: 'right', // 向左翻页向右滑
                showFitScreenButton: false,
            });
        }
    }

    nextPage = () => {
        const { length } = this.props.urlList;
        if (this.canGoNextPage()) {
            this.setState({
                currentIndex: (this.state.currentIndex + 1) % length,
                imageState: 'loading',
                animationDirection: 'left', // 向右翻页向左滑
                showFitScreenButton: false,
            });
        }
    }

    requestClose = () => {
        if (new Date().getTime() - this.timer.getTime() <= PREVIEW_CLOSE_GAP_DURATION) {
            return;
        }
        this.props.onClose!();
    }

    scaleImageUp = (e?: React.MouseEvent<HTMLSpanElement>) => {
        const maxScale = this.props.maxScale || 3;
        let scale = this.currentImageScale + 0.25;
        if (scale > maxScale) scale = maxScale;
        this.scaleTo(scale);
        if (e) {
            e.stopPropagation();
            e.preventDefault();
        }
    }

    scaleImageDown = (e?: React.MouseEvent<HTMLSpanElement>) => {
        const { minScale } = this;
        let scale = this.currentImageScale - 0.25;
        if (scale < minScale) scale = minScale;
        this.scaleTo(scale);
        this.setImagePosition();
        if (e) {
            e.stopPropagation();
            e.preventDefault();
        }
    }

    setImagePosition = () => {
        const imageWrapper = this.imageWrapperRef.current!;
        const { left: leftDistance, top: topDistance, bottom, right } = imageWrapper.getBoundingClientRect();
        const bottomDistance = window.innerHeight - bottom;
        const rightDistance = window.innerWidth - right;
        // 当元素拖拽后缩放无法回到屏幕中心区域时，将图片恢复至屏幕中心。
        if ((bottomDistance > 0 && topDistance < 0) ||
            (bottomDistance < 0 && topDistance > 0) ||
            (rightDistance > 0 && leftDistance < 0) ||
            (rightDistance < 0 && leftDistance > 0)) {
            this.adaptScreen();
        }
    }

    adaptScreen = (e?: React.MouseEvent<HTMLSpanElement>) => {
        this.currentImageTranslateX = 0;
        this.currentImageTranslateY = 0;
        this.scaleTo(this.adaptScale);
        if (this.adaptScale === 1) {
            this.setState({ showFitScreenButton: false, showActualSizeButton: false });
        } else {
            this.setState({ showFitScreenButton: false });
        }
        if (e) {
            e.stopPropagation();
            e.preventDefault();
        }
    }

    realScreenShow = (e?: React.MouseEvent<HTMLSpanElement>) => {
        const { showActualSizeButton } = this.state;
        if (!showActualSizeButton) return;
        this.currentImageTranslateX = 0;
        this.currentImageTranslateY = 0;
        this.scaleTo(1);
        if (this.adaptScale !== 1) {
            this.setState({ showFitScreenButton: true });
        } else {
            this.setState({ showActualSizeButton: false });
        }
        if (e) {
            e.stopPropagation();
            e.preventDefault();
        }
    }

    /**
     * 下载当前图片
     * @param imageUrl
     */
    downloadImage = (imageUrl: string) => {
        const { imageClipStyle } = this.props;
        const { currentIndex } = this.state;
        const [url, params] = imageUrl.split('?');
        // 让缓存失效
        const timestamp = `timestamp=${Date.now()}`;
        const appendedParams = params ? `${params}&${timestamp}` : timestamp;
        const downloadImageUrl = `${url}?${appendedParams}`;

        if (imageClipStyle && imageClipStyle[currentIndex]) {
            let image = new Image();
            // 解决跨域 Canvas 污染问题
            image.setAttribute('crossOrigin', 'anonymous');
            let canvas = document.createElement('canvas');
            image.onload = () => {
                const { marginLeft, marginTop, height, width } = imageClipStyle[currentIndex];
                canvas.width = width;
                canvas.height = height;
                let context = canvas.getContext('2d');
                if (context) {
                    context.drawImage(image, marginLeft, marginTop, width, height, 0, 0, width, height);
                    let url = canvas.toDataURL('image/png');
                    let a = document.createElement('a');
                    let event = new MouseEvent('click');
                    a.download = 'image';
                    a.href = url;
                    a.dispatchEvent(event);
                }
            };
            image.src = downloadImageUrl;
        } else {
            const xhr = new XMLHttpRequest();
            xhr.open('GET', downloadImageUrl, true);
            xhr.responseType = 'blob';
            xhr.onload = () => {
                if (xhr.status === 200 && xhr.response) {
                    if (xhr.response.type && xhr.response.type.indexOf('image/') >= 0) {
                        const blobLocalPath = (window.URL ? window.URL : window.webkitURL).createObjectURL(xhr.response);
                        const aHtml = document.createElement('a');
                        document.body.appendChild(aHtml);
                        aHtml.style.display = 'none';
                        aHtml.href = blobLocalPath;
                        aHtml.download = 'image';
                        aHtml.click();
                        setTimeout(() => {
                            document.body.removeChild(aHtml);
                            (window.URL ? window.URL : window.webkitURL).revokeObjectURL(blobLocalPath);
                        }, 1000);
                    }
                }
            };
            xhr.send();
        }
    }

    transformImageMargin = (imageStyle: any) => {
        let imageMarginStyle = {};
        if (imageStyle) {
            imageMarginStyle = { marginLeft: -1 * imageStyle.marginLeft + 'px', marginTop: -1 * imageStyle.marginTop + 'px' };
        }
        return imageMarginStyle;
    }

    transformImageWrapper = (imageStyle: any) => {
        let imageWrapperStyle = {};
        if (imageStyle) {
            imageWrapperStyle = { width: imageStyle.width + 'px', height: imageStyle.height + 'px', display: 'inline-block', overflow: 'hidden' };
        }
        return imageWrapperStyle;
    }

    showBottomWidgets = () => {
        const {
            showBottomControl,
            renderBottomWidgets,
        } = this.props;
        if (renderBottomWidgets) return false;
        if (showBottomControl) return true;
        return false;
    }

    adaptImageScreenShow = (argWidth: number, argHeight: number) => {
        let width = argWidth;
        let height = argHeight;
        const { currentIndex } = this.state;
        const { imageClipStyle } = this.props;
        const imageWrapperRef = this.imageWrapperRef.current!;
        if (imageClipStyle && imageClipStyle[currentIndex]) {
            width = imageClipStyle[currentIndex].width;
            height = imageClipStyle[currentIndex].height;
        }
        this.currentImageTranslateX = 0;
        this.currentImageTranslateY = 0;
        if (imageWrapperRef) {
            if (width > window.innerWidth - 96 || height > window.innerHeight - 96) {
                // 设置图片显示的安全区，防止大图顶着屏幕显示
                const adaptScale = Math.min((window.innerWidth - 100) / width, (window.innerHeight - 100) / height);
                this.minScale = adaptScale < 0.5 ? adaptScale : 0.5;
                this.translateY = height * (1 - adaptScale) / 2;
                this.setState({ showActualSizeButton: true });
                return adaptScale;
            } else {
                this.setState({ showActualSizeButton: false });
                this.translateY = 0;
                return 1;
            }
        } else {
            this.setState({ showActualSizeButton: false });
            return 1;
        }
    }

    /**
     * 获取图片查看器初始化时的样式，用来变现过渡动画的，这里规则如下
     * 1、过渡动画都有 scale + opacity
     * 2、没有 initalCoordinateStyle 参数时，从页面中心开始过渡动画（无论进场还是退场）
     * 3、当有 initalCoordinateStyle 参数时
     *  3.1、从参数位置开始进场过渡动画
     *  3.2、如果没有切换过图片，退场退回参数位置
     *  3.3、如果切换过图片，那么退场往页面中心退
     */
    getInitialCoordinateStyle() {
        let initialCoordinateStyle: any = {
            transform: 'scale(0)',
            '--init-coord': 'scale(0)'
        };
        if (this.props.initialCoordinate) {
            const width = window.innerWidth;
            const height = window.innerHeight;
            const left = this.props.initialCoordinate.left - (width / 2);
            const top = this.props.initialCoordinate.top - (height / 2);
            initialCoordinateStyle = {
                transform: `translate(${left}px, ${top}px) scale(0.5)`,
            };

            const initialIndex = this.props.initialIndex === undefined ? 0 : this.props.initialIndex;

            if (initialIndex === this.state.currentIndex) {
                // 图片没有变换过
                initialCoordinateStyle['--init-coord'] = `translate(${left}px, ${top}px) scale(0.5)`;
            } else {
                initialCoordinateStyle['--init-coord'] = `scale(0)`;
            }
        }

        return initialCoordinateStyle;
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    // eslint-disable-next-line complexity
    private themedRender = (classNames: TThemedClassNames) => {
        const {
            visible,
            prefixCls,
            className,
            style,
            urlList,
            maskStyle,
            showPageNumber,
            renderTopWidgets,
            renderBottomWidgets,
            imageClipStyle,
            containerDom,
            renderExtraButtons,
        } = this.props;
        const {
            currentIndex, scaleUpDisable, scaleDownDisable, showActualSizeButton,
            isWidgetsVisible, isForceHover, showFitScreenButton,
        } = this.state;
        let imageWrapperStyle = {};
        let imageMarginStyle = {};
        let bottomButtonVisible = this.showBottomWidgets();
        if (imageClipStyle && imageClipStyle[currentIndex]) {
            imageWrapperStyle = this.transformImageWrapper(imageClipStyle[currentIndex]);
            imageMarginStyle = this.transformImageMargin(imageClipStyle[currentIndex]);
        }

        this.setPreventGlobalScrollEvents(visible);

        if (!decideKeepDom.call(this, PREVIEW_TRANSITION_DURATION, visible)) {
            return null;
        }

        const containerClasses = classNames(`${prefixCls}`, className, `${prefixCls}-${visible ? 'visible' : 'hidden'}`);
        const imageLoadingClass = classNames(`${prefixCls}-image-loading`);
        const imageErrorClass = classNames(`${prefixCls}-image-error`);
        const imageClass = classNames(`${prefixCls}-image`, `${prefixCls}-image-fade-in`);
        const maskClass = classNames(`${prefixCls}-image-mask`);
        const imageContainerClass = classNames(`${prefixCls}-image-container`);
        const hotZoneClasses = classNames(`${prefixCls}-hot-zone`, isForceHover ? `${prefixCls}-force-hover` : '');
        const topWidgetsContainerClasses = classNames(`${prefixCls}-widgets-container-top`, `${prefixCls}-widgets-container-${isWidgetsVisible ? 'visible' : 'hidden'}`);
        const closeButtonClass = classNames(`${prefixCls}-button-close`);
        const prevButtonClass = classNames(`${prefixCls}-button-prev`);
        const nextButtonClass = classNames(`${prefixCls}-button-next`);
        const scaleDownButtonClass = classNames(`${prefixCls}-scale-down-button`, scaleDownDisable ? `${prefixCls}-disable-click` : '');
        const scaleUpButtonClass = classNames(`${prefixCls}-scale-up-button`, scaleUpDisable ? `${prefixCls}-disable-click` : '');
        const adaptScreenButtonClass = classNames(`${prefixCls}-adapt-screen-button`);
        const realScreenButtonClass = classNames(`${prefixCls}-real-screen-button`, showActualSizeButton ? '' : `${prefixCls}-disable-click`);
        const downloadButtonClass = classNames(`${prefixCls}-download-button`);
        const bottomButtonWidgetsContainerClasses = classNames(`${prefixCls}-widgets-button-container-bottom`, `${prefixCls}-widgets-button-container-${isWidgetsVisible ? 'visible' : 'hidden'}`);
        const bottomWidgetsContainerClasses = classNames(`${prefixCls}-widgets-container-bottom`, `${prefixCls}-widgets-container-${isWidgetsVisible ? 'visible' : 'hidden'}`);
        const pageNumberClasses = classNames(`${prefixCls}-page-number`, `${prefixCls}-page-number-${isWidgetsVisible ? 'visible' : 'hidden'}`);
        const bottomContainer = classNames(`${prefixCls}-bottom-container`);
        const imageWrapperContainer = classNames(`${prefixCls}-image-wrapper-container`, this.isDraggable() ? `${prefixCls}-image-wrapper-container-draggable` : '');
        const divideLine = classNames(`${prefixCls}-divide-line`);
        let imageUrl: string = '';
        let showImage: boolean = false;
        let isLoading: boolean = false;
        let isFailed: boolean = false;

        // 图片查看器出现的初始化位置
        const initialCoordinateStyle = this.getInitialCoordinateStyle();

        if (
            this.loadedUrlList[currentIndex] === undefined
            || this.loadedUrlList[currentIndex] === ImageLoadCycle.LOADING
        ) {
            // 这两种都意味着需要展示的图片没准备好
            // 正在加载
            if (this.props.previewList && this.props.previewList[currentIndex]) {
                // 如果有预览图，那先展示预览图
                imageUrl = this.props.previewList[currentIndex];
                showImage = true;
            } else {
                // 如果没有预览图，那就展示不展示图片
                showImage = false;
            }
        }

        switch (this.loadedUrlList[currentIndex]) {
            case undefined: {
                // 没有发起请求，发起请求
                // 真正展示的图片还没有发起请求
                const task = new LoadTask(currentIndex, this.props.urlList[currentIndex]);
                this.loadTasks.push(task);
                task.load().then(
                    (info: { width: number, height: number }) => {
                        this.loadedUrlList[currentIndex] = ImageLoadCycle.LOADED;
                        if (currentIndex === this.state.currentIndex) {
                            // 图片加载完毕，仍然是选择展示的那张，需要调整一下屏幕适配
                            this.forceUpdate();
                        }
                    },
                    (err: Error) => {
                        if (err.message === 'error') {
                            this.loadedUrlList[currentIndex] = ImageLoadCycle.FAILED;
                            if (currentIndex === this.state.currentIndex) {
                                // 强制更新
                                this.forceUpdate();
                            }
                        }
                        // cancel 就不处理了
                    }
                );
                break;
            }
            case ImageLoadCycle.LOADING:
                // 正在加载图片，展示 loading
                isLoading = true;
                break;
            case ImageLoadCycle.LOADED:
                // 展示真实的图片
                imageUrl = this.props.urlList[currentIndex];
                showImage = true;
                break;
            case ImageLoadCycle.FAILED:
                // 图片失败
                isFailed = true;
                showImage = false;
                break;
            default:
                throw new Error('未知的类型');
        }

        return ReactDOM.createPortal(
            // 整个组件，大小与屏幕相等
            <div
                className={containerClasses}
                ref={this.containerRef}
                style={style}
                onWheel={this.handleMouseWheel}
            >
                <div
                    className={maskClass}
                    style={maskStyle}
                    onMouseDown={(e) => {
                        this.readyClose = true;
                    }}
                    onMouseUp={(e) => {
                        if (this.readyClose && e.nativeEvent.target !== this.imageRef.current) {
                            this.requestClose();
                        }

                        this.readyClose = false;
                    }}
                >
                    <div className={imageContainerClass} style={initialCoordinateStyle}>
                        <div className={imageWrapperContainer} style={imageWrapperStyle} ref={this.imageWrapperRef} onClick={(e) => e.stopPropagation()}>
                            {/* 图片本身 */}
                            {
                                showImage && <img
                                    className={imageClass}
                                    style={imageMarginStyle}
                                    src={imageUrl}
                                    key={currentIndex}
                                    ref={this.imageRef}
                                    onLoad={this.setImageAdapt}
                                    onDragStart={(e) => e.preventDefault()}
                                    onMouseDown={this.handleMouseStart}
                                    onContextMenu={(e) => this.props.onContextMenu!(e, currentIndex)}
                                />
                            }
                        </div>
                    </div>
                </div>
                {/* Loading 态 */}
                {isLoading && <Loading className={imageLoadingClass} spinType="light"></Loading>}

                {/* Error 态 */}
                {isFailed && <div className={imageErrorClass}>加载失败</div>}

                {/* 热区，hover 展示按钮 */}
                <div className={hotZoneClasses} onClick={(e) => e.stopPropagation()}>

                    {/* 渲染在顶部的自定义组件 */}
                    <div className={topWidgetsContainerClasses}>
                        {renderTopWidgets!(currentIndex)}
                        <div className={closeButtonClass} onClick={this.requestClose}></div>
                    </div>

                    {/* 上一页 */}
                    {this.canGoPrevPage() && <div className={prevButtonClass} onClick={this.prevPage} />}

                    {/* 下一页 */}
                    {this.canGoNextPage() && <div className={nextButtonClass} onClick={this.nextPage} />}

                    {/* 底部组件容器 */}
                    <div className={bottomContainer}>
                        {/* 页码 */}
                        {showPageNumber && urlList.length > 1 && <div className={pageNumberClasses} onClick={(e) => e.stopPropagation()}>
                            {currentIndex + 1}/{urlList.length}
                        </div>}
                        {/* 渲染在底部组件自带控件 */}
                        {bottomButtonVisible && <div className={bottomButtonWidgetsContainerClasses} onClick={(e) => e.stopPropagation()}>
                            {/* 底部的放大/缩小/适应屏幕/实际尺寸1:1显示按钮 */}
                            {<span className={scaleUpButtonClass} onClick={(e) => this.scaleImageUp(e)} />}
                            {<span className={scaleDownButtonClass} onClick={(e) => this.scaleImageDown(e)} />}
                            {!showFitScreenButton && <span className={realScreenButtonClass} onClick={(e) => this.realScreenShow(e)} />}
                            {showFitScreenButton && <span className={adaptScreenButtonClass} onClick={(e) => this.adaptScreen(e)} />}
                            {<span className={divideLine}></span>}
                            {<span className={downloadButtonClass} onClick={() => this.downloadImage(imageUrl)} />}
                            {renderExtraButtons!()}
                        </div>}
                        {/* 渲染在底部的自定义组件 */}
                        {!bottomButtonVisible && <div className={bottomWidgetsContainerClasses}>
                            {renderBottomWidgets!(currentIndex)}
                        </div>}
                    </div>
                </div>
            </div>,
            containerDom || document.body
        );
    }
}

Preview.defaultProps = {
    visible: true,
    prefixCls: 'dui-preview',
    initialIndex: 0,
    maskStyle: {},
    showPageNumber: true,
    renderTopWidgets: () => null,
    renderBottomWidgets: undefined,
    maxScale: 3,
    onChange: emptyFn,
    onContextMenu: emptyFn,
    onClose: emptyFn,
    showBottomControl: true,
    imageClipStyle: undefined,
    containerDom: typeof document !== 'undefined' ? document.body : undefined,
    renderExtraButtons: () => null,
    cycle: false,
};

Preview.propTypes = {
    visible: PropTypes.bool,
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    urlList: PropTypes.arrayOf(PropTypes.string),
    previewList: PropTypes.arrayOf(PropTypes.string),
    initialIndex: PropTypes.number,
    maskStyle: PropTypes.object,
    showPageNumber: PropTypes.bool,
    renderTopWidgets: PropTypes.func,
    renderBottomWidgets: PropTypes.func,
    maxScale: PropTypes.number,
    onChange: PropTypes.func,
    onContextMenu: PropTypes.func,
    onClose: PropTypes.func,
    imageClipStyle: PropTypes.array,
    showBottomControl: PropTypes.bool,
    containerDom: PropTypes.object,
    renderExtraButtons: PropTypes.func,
    cycle: PropTypes.bool,
    initialCoordinate: PropTypes.object,
};
