class LoadTask {
    valid: boolean = true;
    readonly index: number;
    readonly url: string;

    constructor(index: number, url: string) {
        this.index = index;
        this.url = url;
    }

    async load() {
        return new Promise((resolve, reject) => {
            const temp = new Image();
            temp.src = this.url;
            temp.onload = () => {
                if (this.valid) {
                    resolve({ width: temp.width, height: temp.height });
                } else {
                    reject(new Error('cancel'));
                }
            };

            temp.onerror = () => {
                if (this.valid) {
                    reject(new Error('error'));
                } else {
                    reject(new Error('cancel'));
                }
            };
        });
    }

    cancel() {
        this.valid = true;
    }
}

export default LoadTask;
