import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IRadioProps, IRadioState } from './interface';
import Group from './Group';
import { emptyFn } from '../../utils/helper';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Radio extends React.Component<IRadioProps, IRadioState> {
    static defaultProps: TDefaultProps<IRadioProps>;
    static propTypes: Partial<TPropTypes<IRadioProps>>;
    static Group: typeof Group;

    state: IRadioState = {
        checked: !!this.props.defaultChecked
    }

    handleChange: React.ChangeEventHandler<HTMLInputElement> = e => {
        const { props } = this;
        if (props.disabled) {
            return;
        }

        if (!('checked' in props)) {
            this.setState({
                checked: e.target.checked
            });
        }

        props.onChange!(e.target.checked, {
            target: {
                ...props,
                checked: e.target.checked
            },
            stopPropagation: e.stopPropagation.bind(e),
            preventDefault: e.preventDefault.bind(e),
            nativeEvent: e.nativeEvent
        });
    };

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, disabled, hollow, labelClassName, labelStyle, iconClassName, iconStyle, ...rest
        } = this.props;
        const checked = 'checked' in this.props ? this.props.checked : this.state.checked;

        const cls = classNames(prefixCls, className, `${prefixCls}-${hollow ? 'hollow' : 'normal'}`, {
            [`${prefixCls}-multi-line`]: React.Children.count(children) > 1,
            [`${prefixCls}-checked`]: checked,
            [`${prefixCls}-disabled`]: disabled
        });

        const iconCls = classNames(`${prefixCls}-icon`, iconClassName);
        const labelCls = classNames(`${prefixCls}-label`, labelClassName);

        return (
            <div className={cls} style={style}>
                <span className={iconCls} style={iconStyle} />
                <div className={labelCls} style={labelStyle}>
                    {children}
                </div>
                <input
                    {...rest}
                    type="radio"
                    className={classNames(`${prefixCls}-input`)}
                    onChange={this.handleChange}
                />
            </div>
        );
    }
}

Radio.defaultProps = {
    prefixCls: 'dui-radio',
    onChange: emptyFn,
    hollow: true
};

Radio.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    onChange: PropTypes.func,
    hollow: PropTypes.bool,
    iconClassName: PropTypes.string,
    iconStyle: PropTypes.object,
    labelClassName: PropTypes.string,
    labelStyle: PropTypes.object,
};
