import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IDividerProps, IDividerState } from './interface';
import { TPropTypes } from '../../common/interface';
import { SubMenuContext } from '../Menu/context';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Divider extends React.Component<IDividerProps, IDividerState> {
    static defaultProps: IDividerProps;
    static propTypes: TPropTypes<IDividerProps>;

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, dashed, children, orientation, textPosition, textStyle, textClassName
        } = this.props;

        const containsText = children && orientation === 'horizontal';

        return (
            <SubMenuContext.Consumer>
                {({ type }) => {
                    const isCompact = type === 'compact';
                    const cls = classNames(prefixCls, className, `${prefixCls}-${orientation}${dashed ? '-dashed' : ''}`, {
                        [`${prefixCls}-with-text`]: containsText,
                        [`${prefixCls}-compact`]: isCompact
                    });
                    const textCls = classNames(`${prefixCls}-text`, textClassName, `${prefixCls}-text-${textPosition}`, {
                        [`${prefixCls}-text-compact`]: isCompact
                    });
                    return (
                        <div className={cls} style={style}>
                            {containsText ? <span className={textCls} style={textStyle}>{children}</span> : null}
                        </div>
                    );
                }}
            </SubMenuContext.Consumer>
        );
    }
}

Divider.defaultProps = {
    prefixCls: 'dui-divider',
    dashed: false,
    orientation: 'horizontal',
    textPosition: 'center',
};

Divider.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    dashed: PropTypes.bool,
    orientation: PropTypes.string,
    textPosition: PropTypes.string,
    textStyle: PropTypes.object,
    textClassName: PropTypes.string,
};
