import * as React from 'react';
import * as PropTypes from 'prop-types';
import { ISliderProps, ISliderState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { emptyFn, castInto, roundByStep, assert } from '../../utils/helper';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Slider extends React.Component<ISliderProps, ISliderState> {
    static defaultProps: TDefaultProps<ISliderProps>;
    static propTypes: TPropTypes<ISliderProps>;

    containerRef = React.createRef<HTMLDivElement>();

    width: number;
    private _dragging: boolean;
    private _prevMousePosition: number;

    constructor(props: ISliderProps, ...rest: any[]) {
        super(props, ...rest);
        this.checkParams();
        this.state = {
            currentValue: typeof props.defaultValue === 'number' ? props.defaultValue : props.min!
        };
        this._dragging = false;
    }

    static getDerivedStateFromProps(props: ISliderProps, state: ISliderState): Partial<ISliderState> | null {
        const { value, min, max } = props;
        if (typeof value === 'number') {
            return { currentValue: castInto(value, [min!, max!]) };
        }
        return null;
    }

    get valueSpan(): number {
        return this.props.max! - this.props.min!;
    }

    // 滑块相对于最小值的位置，为px数
    get currentPosition(): number {
        return this.width * this.getValuePercentage(this.state.currentValue);
    }

    componentDidMount() {
        document.addEventListener('mousemove', this.handleMouseMove);
        document.addEventListener('mouseup', this.handleMouseUp);
        this.width = this.containerRef.current!.getBoundingClientRect().width;
    }

    componentWillUnmount() {
        document.removeEventListener('mousemove', this.handleMouseMove);
        document.removeEventListener('mouseup', this.handleMouseUp);
    }

    checkParams() {
        const { min, max, step, defaultValue } = this.props;
        assert(min! < max!, 'expected: min < max');
        assert(step! >= 0, 'expected: step >= 0');
        if (step! > 0) {
            assert((max! - min!) % step! === 0, 'expected: max - min can be divided by step');
        }

        if (typeof defaultValue !== 'number') {
            return;
        }
        assert(defaultValue >= min! && defaultValue <= max!, 'expected: min <= defaultValue <= max');
        const relativeValue = defaultValue - min!;
        if (step! > 0) {
            assert(relativeValue % step! === 0, 'expected: defaultValue matches step constraint');
        }
    }

    handleMouseDown = (event: React.MouseEvent<HTMLDivElement>) => {
        this._prevMousePosition = event.clientX;
        this._dragging = true;
        event.stopPropagation();
    };

    handleMouseMove = (event: React.MouseEvent<Document> | MouseEvent) => {
        if (this._dragging) {
            const mouseMoveDistance = event.clientX - this._prevMousePosition;
            const { isValid, roundedOffset } = this.setStateByPosition(this.currentPosition + mouseMoveDistance);
            if (isValid) {
                this._prevMousePosition = event.clientX + roundedOffset;    // 补偿自动舍入的距离，避免鼠标和滑块位置越来越远
            }
        }
    };

    handleMouseUp = () => {
        if (this._dragging) {
            this._dragging = false;
            this.props.onChangeEnd!(this.state.currentValue);
        }
    };

    handleBarContainerClick = (event: React.MouseEvent<HTMLDivElement>) => {
        let pos = event.clientX - this.containerRef.current!.getBoundingClientRect().left;
        this.setStateByPosition(pos, this.props.onChangeEnd);
    };

    /**
     * 根据位置值设置value
     * @param position 滑块相对于最小值的位置，为px数
     * @returns 是否生效及round应用的距离偏移量
     */
    setStateByPosition(position: number, callback: (value: number) => void = emptyFn): { isValid: boolean, roundedOffset: number } {
        const { step, onChange, min } = this.props;
        let correctedPosition = castInto(position, [0, this.width]);
        let roundedOffset = 0;
        let relativeValue = correctedPosition / this.width * this.valueSpan;
        if (step! > 0) {
            const roundedValue = roundByStep(relativeValue, step!);
            roundedOffset = (roundedValue - relativeValue) / this.valueSpan * this.width;
            relativeValue = roundedValue;
        }
        const nextValue = relativeValue + min!;
        if (nextValue !== this.state.currentValue) {
            this.setState({ currentValue: nextValue }, () => {
                onChange!(nextValue);
                callback(nextValue);
            });
            return { isValid: true, roundedOffset };
        }

        return { isValid: false, roundedOffset };
    }

    getValuePercentage(value: number): number {
        return (value - this.props.min!) / this.valueSpan;
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, dragStyle, fillStyle, type, dragClassName, fillClassName, disabled
        } = this.props;
        const { currentValue } = this.state;
        const cls = classNames(prefixCls, className, `${prefixCls}-${type}`, {
            [`${prefixCls}-disabled`]: disabled
        });
        const percentage = this.getValuePercentage(currentValue);

        const mergedfillStyle = { ...fillStyle, width: `${percentage * 100}%` };
        const mergedDragStyle = { ...dragStyle, left: `${percentage * 100}%` };

        return (
            <div className={cls} style={style} ref={this.containerRef}>
                <div className={classNames(`${prefixCls}-bar-container`)} onClick={this.handleBarContainerClick}>
                    <div className={classNames(`${prefixCls}-bar`)}>
                        <div className={classNames(`${prefixCls}-fill`, fillClassName)} style={mergedfillStyle} />
                    </div>
                </div>
                <div
                    className={classNames(`${prefixCls}-drag`, dragClassName)}
                    style={mergedDragStyle}
                    onMouseDown={this.handleMouseDown}
                />
            </div>
        );
    }

}

Slider.defaultProps = {
    prefixCls: 'dui-slider',
    min: 0,
    max: 100,
    step: 0,
    onChange: emptyFn,
    onChangeEnd: emptyFn,
    type: 'progress',
    disabled: false,
};

Slider.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    min: PropTypes.number,
    max: PropTypes.number,
    value: PropTypes.number,
    defaultValue: PropTypes.number,
    onChange: PropTypes.func,
    step: PropTypes.number,
    dragStyle: PropTypes.object,
    dragClassName: PropTypes.string,
    fillStyle: PropTypes.object,
    fillClassName: PropTypes.string,
    onChangeEnd: PropTypes.func,
    type: PropTypes.oneOf(['progress', 'balance']),
    disabled: PropTypes.bool,
};