import * as React from 'react';
import * as PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { IDrawerProps, IDrawerState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { emptyFn } from '../../utils/helper';
import { decideKeepDom } from '../../utils/keepDom';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';
import { PreventScrollPenetrateContainer } from '../../utils/preventScrollPenetrate';

const DRAWER_LEAVING_TRANSITION_DURATION = 320;

export default class Drawer extends React.Component<IDrawerProps, IDrawerState> {
    static defaultProps: TDefaultProps<IDrawerProps>;
    static propTypes: TPropTypes<IDrawerProps>;

    handleClose = () => {
        this.props.onClose!();
    }

    handleMaskClick = () => {
        if (this.props.mask && this.props.maskClosable) {
            this.handleClose();
        }
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, closable, mask, placement, title, visible, headerStyle,
            contentStyle, destroyOnClose, titleStyle, closeStyle, containerStyle, zIndex, closeClassName,
            containerClassName, contentClassName, headerClassName, titleClassName
        } = this.props;

        const containerCls = classNames(`${prefixCls}-container`, containerClassName, `${prefixCls}-container-${visible ? 'visible' : 'hidden'}`);
        const cls = classNames(prefixCls, className, `${prefixCls}-${placement}`, `${prefixCls}-${visible ? 'visible' : 'hidden'}`);
        const maskCls = classNames(`${prefixCls}-mask`, `${prefixCls}-mask-${visible ? 'visible' : 'hidden'}`, {
            [`${prefixCls}-mask-display`]: mask,
        });

        const shouldDestroy = destroyOnClose && !decideKeepDom.call(this, DRAWER_LEAVING_TRANSITION_DURATION, visible);
        const drawer = shouldDestroy ?
            null
            : (
                <div className={cls} style={style}>
                    {title || closable ?
                        <div className={classNames(`${prefixCls}-header`, headerClassName)} style={headerStyle}>
                            <span className={classNames(`${prefixCls}-header-title`, titleClassName)} style={titleStyle}>{title}</span>
                            {closable ?
                                <span className={classNames(`${prefixCls}-header-close`, closeClassName)} style={closeStyle} onClick={this.handleClose} />
                                : null}
                        </div>
                        : null}
                    <div className={classNames(`${prefixCls}-content`, contentClassName)} style={contentStyle}>{children}</div>
                </div>
            );

        return ReactDOM.createPortal(
            <PreventScrollPenetrateContainer className={containerCls} style={{ zIndex, ...containerStyle }}>
                <div className={maskCls} onClick={this.handleMaskClick} />
                {drawer}
            </PreventScrollPenetrateContainer>
            ,
            document.body
        );
    }
}

Drawer.defaultProps = {
    prefixCls: 'dui-drawer',
    closable: true,
    mask: false,
    maskClosable: true,
    onClose: emptyFn,
    placement: 'right',
    title: 'Drawer',
    destroyOnClose: false
};

Drawer.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    closable: PropTypes.bool,
    mask: PropTypes.bool,
    maskClosable: PropTypes.bool,
    onClose: PropTypes.func,
    placement: PropTypes.oneOf(['top', 'bottom', 'right', 'left']),
    title: PropTypes.string,
    visible: PropTypes.bool,
    contentStyle: PropTypes.object,
    contentClassName: PropTypes.string,
    headerStyle: PropTypes.object,
    headerClassName: PropTypes.string,
    destroyOnClose: PropTypes.bool,
    titleStyle: PropTypes.object,
    titleClassName: PropTypes.string,
    closeStyle: PropTypes.object,
    closeClassName: PropTypes.string,
    containerStyle: PropTypes.object,
    containerClassName: PropTypes.string,
    zIndex: PropTypes.number
};
