import { ILoadingProps } from './interface';
import Loading, { LOADING_TRANSITION_DURATION } from './Loading';
import { showWithTransition } from '../../utils/show';
import { Omit } from '../../common/interface';
import { wrapWithConfigDefaults } from '../../utils/helper';

export default wrapWithConfigDefaults(function (config: Omit<ILoadingProps, 'visible'> = {}) {
    return showWithTransition<ILoadingProps>({
        Component: Loading,
        transitionDuration: LOADING_TRANSITION_DURATION,
        autoClose: false,
        forcedProps: { embedded: false },
        props: config as any
    });
});
