import * as React from 'react';
import * as PropTypes from 'prop-types';
import { ICardProps, ICardState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import CardContent from './CardContent';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Card extends React.Component<ICardProps, ICardState> {
    static defaultProps: TDefaultProps<ICardProps>;
    static propTypes: Partial<TPropTypes<ICardProps>>;

    static CardContent: typeof CardContent;

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, title, clickable,
            coverImage, coverStyle, headerStyle, bodyStyle, coverClassName,
            headerClassName, bodyClassName, ...rest
        } = this.props;

        const cls = classNames(prefixCls, className, {
            [`${prefixCls}-clickable`]: clickable,
        });

        const header = title ? (
            <div className={classNames(`${prefixCls}-header`, headerClassName)} style={headerStyle}>
                {title}
            </div>
        ) : null;

        const cover = (
            <div className={classNames(`${prefixCls}-cover`, coverClassName)} style={coverStyle}>
                {coverImage ?
                    (typeof coverImage === 'string' ?
                        <div className={classNames(`${prefixCls}-cover-img`)} style={{ backgroundImage: `url(${coverImage})` }} />
                        : coverImage)
                    : null
                }
            </div>
        );

        const body = children ? (
            <div className={classNames(`${prefixCls}-body`, bodyClassName)} style={bodyStyle}>
                {children}
            </div>
        ) : null;

        return (
            <div className={cls} style={{ ...style }} {...rest}>
                {header}
                {cover}
                {body}
            </div>
        );
    }
}

Card.defaultProps = {
    prefixCls: 'dui-card',
    // size: 'default',
    clickable: true,
};

Card.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    // size: PropTypes.string,
    title: PropTypes.string,
    clickable: PropTypes.bool,
    coverImage: PropTypes.node,
    headerStyle: PropTypes.object,
    headerClassName: PropTypes.string,
    coverStyle: PropTypes.object,
    coverClassName: PropTypes.string,
    bodyStyle: PropTypes.object,
    bodyClassName: PropTypes.string
};