export function emptyFn() { }

export function falseFn() { return false }

export function trueFn() { return true }

/**
 * 将一个数转换到min <= value && value <= max范围内
 * @param targetRange [min,max]
 * @param value
 */
export function castInto(value: number, targetRange: [number, number]): number {
    if (value > targetRange[1]) {
        return targetRange[1];
    }
    if (value < targetRange[0]) {
        return targetRange[0];
    }
    return value;
}

/**
 * 返回离这个整数最近的step倍数
 * 例如：(11, 5) => 10,   (-3, 5) => -5
 * @param step 步长，为正整数
 * @param value
 */
export function roundByStep(value: number, step: number): number {
    if (value < 0) {
        return -roundByStep(-value, step);
    }
    const diff = value % step;
    if (diff < step / 2) {
        return value - diff;
    } else {
        return value - diff + step;
    }
}

/**
 * 将接受数字作为参数的函数转换为接受字符串作为参数
 * 此方法假设传入的字符串都可以被正确地转换成数字
 * @param numberParamFunc
 */
export function createStringParamFunc(numberParamFunc: (...value: number[]) => any) {
    return (...stringValues: string[]) => {
        const numberValues = stringValues.map(str => Number(str));
        return numberParamFunc(...numberValues);
    };
}

let isIEBrowser: boolean;

export function isIE(): boolean {

    if (typeof isIEBrowser === 'boolean') {
        return isIEBrowser;
    }

    const ua = navigator.userAgent;
    if (/compatible/.test(ua) && /MSIE/.test(ua)) {
        // IE < 11
        isIEBrowser = true;
    } else if (/Trident/.test(ua) && /rv:11\.0/.test(ua)) {
        // IE 11
        isIEBrowser = true;
    } else {
        isIEBrowser = false;
    }
    return isIEBrowser;

}

/**
 * 断言不通过时抛出错误
 * @param condition 求值为falsy的时候抛出错误
 * @param errorMessage 错误信息
 */
export function assert(condition: boolean, errorMessage: string) {
    if (!condition) {
        throw new Error(errorMessage);
    }
}

/**
 * 若原函数调用后不返回false，则自动调用额外函数
 * @param originalFn 原函数
 * @param additionalFn 额外函数
 */
export function inject(originalFn: Function, additionalFn: Function) {
    return (...args: any[]) => {
        const result = originalFn.apply(null, args.concat(additionalFn));
        if (result !== false) {
            additionalFn();
        }
        return result;
    };
}

export function wrapWithConfigDefaults<P, R>(originalFn: (params: P) => R) {

    let defaultConfig = {};

    function configDefaults(defaults: P) {
        defaultConfig = defaults;
    }

    // 这里如果写成function，生成的.d.ts不对，导致用户调用wrappedFn.configDefaults时参数没有类型提醒
    const wrappedFn = (passedConfig: P) => {
        const config = { ...defaultConfig, ...passedConfig };
        return originalFn(config);
    };

    wrappedFn.configDefaults = configDefaults;

    return wrappedFn;
}

export function isDom(node: any) {
    // instanceof在iframe下有坑，不用
    return node && typeof node === 'object' && node.nodeType === 1;
}
