import { IPreviewProps } from './interface';
import Preview, { PREVIEW_TRANSITION_DURATION } from './Preview';
import { showWithTransition } from '../../utils/show';
import { Omit } from '../../common/interface';
import { wrapWithConfigDefaults } from '../../utils/helper';

export default wrapWithConfigDefaults(function (config: Omit<IPreviewProps, 'visible'>) {
    return showWithTransition<IPreviewProps>({
        Component: Preview,
        transitionDuration: PREVIEW_TRANSITION_DURATION,
        autoClose: false,
        props: config as any,
        injectClosePropNames: ['onClose']
    });
});
