import * as React from 'react';
import * as PropTypes from 'prop-types';
import { ITagProps, ITagState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Tag extends React.Component<ITagProps, ITagState> {
    static defaultProps: TDefaultProps<ITagProps>;
    static propTypes: Partial<TPropTypes<ITagProps>>;

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, clickable, ...rest
        } = this.props;
        const cls = classNames(prefixCls, className, {
            [`${prefixCls}-clickable`]: clickable
        });
        return (
            <div className={cls} style={style} {...rest}>
                {children}
            </div>
        );
    }
}

Tag.defaultProps = {
    prefixCls: 'dui-tag',
    clickable: true
};

Tag.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    clickable: PropTypes.bool
};
