import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IItemProps, IItemState, ISubmenuClickEvent } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { emptyFn } from '../../utils/helper';
import { MenuItemContext } from './context';
import Keep from '../Dropdown/Keep';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Item extends React.Component<IItemProps, IItemState> {
    static defaultProps: TDefaultProps<IItemProps>;
    static propTypes: TPropTypes<IItemProps>;

    static contextType = MenuItemContext;
    context!: React.ContextType<typeof MenuItemContext>

    domRef = React.createRef<HTMLLIElement>();  // 用于计算位置

    handleClick = (e: React.MouseEvent<HTMLLIElement>) => {
        const { clickHandler } = this.context;
        const { disabled, onClick } = this.props;
        if (disabled) {
            return;
        }
        onClick!(e);

        const nativeEvent = e.nativeEvent as ISubmenuClickEvent;
        if (nativeEvent._dui_submenu_click) {
            // 点击submenu项时不应触发回调
            delete nativeEvent._dui_submenu_click;
            return;
        }

        clickHandler(this.props.id);
    }

    isSelected(): boolean {
        return this.context.selectedIds.indexOf(this.props.id) !== -1;
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, onMouseEnter, onMouseLeave, disabled: selfDisabled, description
        } = this.props;
        const { itemStyle, itemClassName } = this.context;
        const cls = classNames(prefixCls, className, itemClassName, {
            [`${prefixCls}-disabled`]: selfDisabled,
            [`${prefixCls}-selected`]: this.isSelected()
        });
        return (
            <Keep disabled={!selfDisabled}>
                <li
                    className={cls}
                    style={{ ...itemStyle, ...style }}
                    onClick={this.handleClick}
                    ref={this.domRef}
                    onMouseEnter={onMouseEnter}
                    onMouseLeave={onMouseLeave}
                >
                    {children}
                    {description ?
                        <span className={classNames(`${prefixCls}-description`)}>{description}</span>
                        : null
                    }
                </li>
            </Keep>
        );
    }
}

Item.defaultProps = {
    prefixCls: 'dui-menu-item',
    id: '',
    onMouseEnter: emptyFn,
    onMouseLeave: emptyFn,
    onClick: emptyFn,
    disabled: false
};

Item.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    id: PropTypes.string,
    onMouseEnter: PropTypes.func,
    onMouseLeave: PropTypes.func,
    onClick: PropTypes.func,
    disabled: PropTypes.bool,
    description: PropTypes.string
};
