import * as React from 'react';
import ReactDOM from 'react-dom';
import * as PropTypes from 'prop-types';
import { ActualPlacement, IDropdownProps, IDropdownState, IKeepClickEvent } from './interface';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import { emptyFn } from '../../utils/helper';
import Trigger from '../../utils/Trigger';
import Keep from './Keep';
import { DropdownContext } from './context';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';
import { triggerMicroTask } from '../../utils/microTask';
import { decideKeepDom } from '../../utils/keepDom';
import { DocumentDomContext } from '../../utils/documentDom';

const DEFAULT_METRICS: IDropdownState['dropContentMetrics'] = {
    left: '0',
    top: '0'
};

const DROPDOWN_TRANSITION_DURATION = 120; // 与css中隐藏动画时长一致
const CHECK_POSTION_INTERVAL = 100;

export default class Dropdown extends React.Component<IDropdownProps, IDropdownState> {
    static defaultProps: TDefaultProps<IDropdownProps>;
    static propTypes: TPropTypes<IDropdownProps>;

    static Keep: typeof Keep;
    static contextType = DropdownContext;

    context!: React.ContextType<typeof DropdownContext>;

    rootDomRef = React.createRef<HTMLDivElement>();
    dropContentRef = React.createRef<HTMLDivElement>();
    prevVisible?: boolean;

    forceRenderDom = false;
    checkPositionChangeTimer: NodeJS.Timeout | null = null;     // 用于在visible时检查自身位置变化，从而触发hide
    displayPostion: DOMRect | null = null;

    state: IDropdownState = {
        visible: false,
        dropContentMetrics: {
            ...DEFAULT_METRICS
        },
        actualPlacement: 'bottom',
    };

    static getDerivedStateFromProps(props: IDropdownProps, state: IDropdownState): Partial<IDropdownState> | null {
        if (props.disabled) {
            return { visible: false };  // 避免重新enable后自动弹出
        }
        return null;
    }

    componentDidMount() {
        this.forceUpdate();  // 拿到ref，更新位置
    }

    componentDidUpdate() {
        const { visible: propVisible } = this.props;
        const visible = this.isVisible();

        const prevVisible = this.prevVisible;
        this.prevVisible = visible;
        if (visible && !prevVisible && propVisible) {
            // 使受控模式也支持自适应高度
            // 使用微任务避免闪烁
            triggerMicroTask(() => this.show(true));
        }

        if (!visible) {
            clearInterval(this.checkPositionChangeTimer!);
            this.displayPostion = null;
        }
    }

    componentWillUnmount() {
        clearInterval(this.checkPositionChangeTimer!);
    }

    isVisible() {
        const { visible: forceVisible, disabled } = this.props;
        if (disabled) {
            return false;
        }
        const { visible: stateVisible } = this.state;
        return typeof forceVisible === 'boolean' ? forceVisible : stateVisible;
    }

    refreshDropContentMetric() {
        this.setState({
            // 先移除之前强制设定的高度
            dropContentMetrics: { ...this.state.dropContentMetrics, height: undefined }
        }, () => {
            this.updateDropContentMetric();
        });
    }

    updateDropContentMetric() {
        const rootDom = this.rootDomRef.current;
        const dropContentDom = this.dropContentRef.current;
        if (!rootDom || !dropContentDom) {
            return;
        }

        const { placement, alignment, matchWidth } = this.props;
        const { width: rootWidth, bottom, top, left, right } = rootDom.getBoundingClientRect();
        const { width: selfWidth, height } = dropContentDom.getBoundingClientRect();

        let resultWidth, resultHeight;  // 实际宽高，压缩时小于selfWidth和height
        let resultLeft = window.pageXOffset;
        let resultTop = window.pageYOffset;

        // 计算水平度量
        if (matchWidth) {
            resultWidth = rootWidth;
            resultLeft += left;
        } else {
            if (alignment === 'left') {
                resultLeft += left;
            } else if (alignment === 'right') {
                resultLeft += right - selfWidth;
            }
        }

        // 计算垂直度量，不可以超出viewport
        const viewportGap = 2;  // 与viewport间留一点缝隙
        let viewportTop = 0;
        let actualPlacement: ActualPlacement = 'bottom';
        const tryTop = () => {
            viewportTop = Math.max(viewportGap, top - height);
            resultHeight = top - viewportTop;
            actualPlacement = 'top';
            return resultHeight;
        };
        const tryBottom = () => {
            viewportTop = bottom;
            resultHeight = Math.min(height, window.innerHeight - viewportGap - viewportTop);
            actualPlacement = 'bottom';
            return resultHeight;
        };

        if (placement === 'top') {
            tryTop();
        } else if (placement === 'bottom') {
            tryBottom();
        } else if (placement === 'auto') {
            if (tryBottom() === height) {
                // 下方空间足够，无需压缩
            } else if (tryTop() === height) {
                // 上方空间足够
            } else {
                // 两边空间都不够，选空间更大的那边
                tryBottom() >= tryTop() ? tryBottom() : tryTop();
            }
        }

        resultTop += viewportTop;

        const result: IDropdownState['dropContentMetrics'] = {
            left: `${resultLeft}px`,
            top: `${resultTop}px`,
            width: resultWidth && `${resultWidth}px`,
            height: resultHeight && `${resultHeight}px`
        };

        this.setState({ dropContentMetrics: result, actualPlacement });
    }

    setStateVisible(visible: boolean) {
        this.setState({ visible });
        this.props.onVisibleChange!(visible);
    }

    checkPostionChange = () => {
        const rootDom = this.rootDomRef.current;
        if (!rootDom) {
            return;
        }
        const position = rootDom.getBoundingClientRect();
        if (
            this.displayPostion
            && (this.displayPostion.left !== position.left || this.displayPostion.top !== position.top)
        ) {
            this.hide();
            return;
        }
        this.displayPostion = position;
    }

    hide = (e?: React.MouseEvent | Event) => {
        if (e && (e as any).nativeEvent) {
            const nativeEvent = (e as React.MouseEvent).nativeEvent as IKeepClickEvent;
            if (nativeEvent._dui_dropdown_keep) {
                delete nativeEvent._dui_dropdown_keep;
                return;
            }
        }
        // trigger会多次调用此方法
        if (!this.isVisible()) {
            return;
        }
        this.setStateVisible(false);

        // 以上setStateVisible只是触发hide的逻辑，组件不一定真的被hide了（例如受控）
        // 真正被hide时的副作用转移到componentDidUpdate里
    }

    show(preventStateVisibleUpdate?: boolean) {
        this.forceRenderDom = true;
        this.setState({
            // 先移除之前强制设定的高度
            dropContentMetrics: { ...this.state.dropContentMetrics, height: undefined }
        }, () => {
            // 重新计算合适的高度和位置并显示，合并到一次render中做完
            this.forceRenderDom = false;
            this.updateDropContentMetric();
            (!preventStateVisibleUpdate) && this.setStateVisible(true);

            this.checkPostionChange();
            clearInterval(this.checkPositionChangeTimer!);  // 防御show被多次调用的情况，目前受控场景下使用toggle来显示，会触发两次show
            this.checkPositionChangeTimer = setInterval(this.checkPostionChange, CHECK_POSTION_INTERVAL);
        });
    }

    toggle = () => {
        if (this.props.disabled) {
            return;
        }

        const visible = !this.isVisible();
        if (visible) {
            this.show();
        } else {
            this.hide();
        }
    }

    handleMouseEnter = () => {
        if (this.props.disabled) {
            return;
        }
        !this.isVisible() && this.show();
    }

    handleMouseLeave = () => {
        if (this.props.disabled) {
            return;
        }
        this.hide();
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, dropContent, disabled, dropContentStyle,
            dropContentClassName, zIndex: forcedZIndex, animated, trigger, destroyOnClose
        } = this.props;
        const { dropContentMetrics, actualPlacement } = this.state;
        const visible = this.isVisible();

        const { zIndex: contextZIndex } = this.context;
        const zIndex = forcedZIndex || contextZIndex;

        const cls = classNames(prefixCls, className, {
            [`${prefixCls}-disabled`]: disabled
        });
        const contentCls = classNames(
            `${prefixCls}-content`,
            dropContentClassName,
            `${prefixCls}-content-${actualPlacement}`,
            `${prefixCls}-content-${visible ? 'visible' : 'hidden'}`,
            {
                [`${prefixCls}-content-no-animation`]: !animated
            }
        );

        const shouldDestroyDom = !this.forceRenderDom && destroyOnClose && !decideKeepDom.call(this, DROPDOWN_TRANSITION_DURATION, visible);
        // 先在js里改，待后续各品类都升级版本后再移到css中，避免新旧版本共存造成边距叠加样式不对
        const dropContentPadding: React.CSSProperties = { [actualPlacement === 'bottom' ? 'paddingTop' : 'paddingBottom']: '5px' };
        const portalContent = shouldDestroyDom ?
            null : (
                <div
                    className={contentCls}
                    style={{ zIndex, ...dropContentMetrics, ...dropContentPadding, ...dropContentStyle }}
                    onClick={this.hide}
                    ref={this.dropContentRef}
                >
                    {dropContent}
                </div>
            );

        return (
            <Trigger
                action={this.hide}
                className={cls}
                style={style}
                domRef={this.rootDomRef}
            >
                <div
                    onMouseEnter={trigger === 'hover' ? this.handleMouseEnter : undefined}
                    onMouseLeave={trigger === 'hover' ? this.handleMouseLeave : undefined}
                >
                    <div onClick={trigger === 'click' ? this.toggle : undefined}>
                        {children}
                    </div>
                    <DropdownContext.Provider value={{ visible, zIndex: zIndex + 1 }}>
                        <DocumentDomContext.Consumer>
                            {(doc) => ReactDOM.createPortal(portalContent, doc?.body || document.body)}
                        </DocumentDomContext.Consumer>
                    </DropdownContext.Provider>
                </div>
            </Trigger>
        );
    }

}

Dropdown.defaultProps = {
    prefixCls: 'dui-dropdown',
    alignment: 'left',
    matchWidth: false,
    disabled: false,
    onVisibleChange: emptyFn,
    placement: 'bottom',
    animated: true,
    trigger: 'click',
    destroyOnClose: true,
};

Dropdown.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    dropContent: PropTypes.element,
    alignment: PropTypes.string,
    matchWidth: PropTypes.bool,
    disabled: PropTypes.bool,
    onVisibleChange: PropTypes.func,
    visible: PropTypes.bool,
    dropContentStyle: PropTypes.object,
    dropContentClassName: PropTypes.string,
    placement: PropTypes.oneOf(['top', 'bottom', 'auto']),
    zIndex: PropTypes.number,
    animated: PropTypes.bool,
    trigger: PropTypes.oneOf(['hover', 'click']),
    destroyOnClose: PropTypes.bool,
};
