import { ISnackbarProps, TSnackbarShowConfig } from './interface';
import Snackbar from './Snackbar';
import { showWithTransition } from '../../utils/show';
import { wrapWithConfigDefaults } from '../../utils/helper';
import { SNACKBAR_LEAVING_TRANSITION_DURATION } from './constant';

export default wrapWithConfigDefaults(function (config: TSnackbarShowConfig) {
    config.autoClose = typeof config.autoClose === 'boolean' ? config.autoClose : true;
    const result = showWithTransition<ISnackbarProps>({
        Component: Snackbar,
        transitionDuration: SNACKBAR_LEAVING_TRANSITION_DURATION,
        autoClose: config.autoClose,
        props: config as any,
        injectClosePropNames: ['onClose']
    });
    if (!config.autoClose) {
        return result;
    }
});
