import * as React from 'react';
import ReactDOM from 'react-dom';
import * as PropTypes from 'prop-types';
import { ILoadingProps } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import Spin from './Spin';
import { decideKeepDom } from '../../utils/keepDom';
import show from './show';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';
import { PreventScrollPenetrateContainer } from '../../utils/preventScrollPenetrate';

export const LOADING_TRANSITION_DURATION = 200;  // 与less中一致

export default class Loading extends React.Component<ILoadingProps, {}> {
    static defaultProps: TDefaultProps<ILoadingProps>;
    static propTypes: TPropTypes<ILoadingProps>;

    static show: typeof show;

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, tip, visible, children, embedded, tipStyle, zIndex, tipClassName, maskClassName, maskStyle, spinType
        } = this.props;

        const type = children ?
            'wrap'
            : embedded ?
                'embedded'
                : 'fixed';

        const cls = classNames(prefixCls, className, `${prefixCls}-${type}`, {
            [`${prefixCls}-hidden`]: !visible && !embedded
        });
        const maskCls = classNames(`${prefixCls}-mask`, `${prefixCls}-mask-${type}`, maskClassName, {
            [`${prefixCls}-mask-hidden`]: !visible
        });
        const containerCls = classNames(`${prefixCls}-container`, `${prefixCls}-container-${type}`);

        const defaultSpinSize: React.CSSProperties = { width: '48px', height: '48px' };

        const tipElement = tip ?
            <div className={classNames(`${prefixCls}-tip`, tipClassName)} style={tipStyle}>{tip}</div>
            : null;

        // 蒙在元素上面
        if (type === 'wrap') {
            return (
                <div className={containerCls}>
                    {children}
                    <div className={maskCls} style={maskStyle}>
                        <div className={cls} style={style}>
                            <Spin style={defaultSpinSize} type={spinType || 'dark'} />
                            {tipElement}
                        </div>
                    </div>
                </div>
            );
        }

        // 内嵌到其他元素中，不显示tip和蒙层
        if (type === 'embedded') {
            return (
                <div className={cls} style={{ ...defaultSpinSize, ...style }}>
                    <Spin type={spinType || 'dark'} />
                </div>
            );
        }

        // 独立使用，自带蒙层及外层样式出现在页面中间
        if (!decideKeepDom.call(this, LOADING_TRANSITION_DURATION, visible)) {
            return null;
        }

        return ReactDOM.createPortal(
            <PreventScrollPenetrateContainer className={containerCls} style={{ zIndex }}>
                <div className={maskCls} style={maskStyle} />
                <div className={cls} style={style}>
                    <Spin style={defaultSpinSize} type={spinType || 'light'} />
                    {tipElement}
                </div>
            </PreventScrollPenetrateContainer>,
            document.body
        );
    }
}

Loading.defaultProps = {
    prefixCls: 'dui-loading',
    visible: false,
    embedded: true
};

Loading.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    tip: PropTypes.any,
    // indicator: PropTypes.node,
    visible: PropTypes.bool,
    tipStyle: PropTypes.object,
    tipClassName: PropTypes.string,
    embedded: PropTypes.bool,
    zIndex: PropTypes.number,
    maskClassName: PropTypes.string,
    maskStyle: PropTypes.object,
    spinType: PropTypes.oneOf(['light', 'dark', 'primary'])
};
