import * as React from 'react';

interface IAnimatedLeavingComponent extends React.Component {
    _ALC_HELPER_prevVisible?: boolean;    // 起奇怪的名字避免命名冲突
}

/**
 * 确定当前组件是否需要保留dom结构，只能用于有退出动画的且退出后会摧毁dom结构的组件
 * 有副作用，只能用于render函数中
 * @returns true应该保留dom；false应该摧毁dom（动画已播放完；或者visible一直为false，不需要动画）
 */
export function decideKeepDom(this: IAnimatedLeavingComponent, animationDuration: number, visible: boolean): boolean {
    const willLeave = (!visible) && this._ALC_HELPER_prevVisible;
    this._ALC_HELPER_prevVisible = visible;

    if (visible) {
        return true;
    }

    // 连续两次falsy（包括prevVisiblew为undefined），要么处于不可见状态，要么动画已播放完
    if (!willLeave) {
        return false;
    }

    // 退出动画开始，动画结束再摧毁dom
    setTimeout(() => {
        this.forceUpdate();
    }, animationDuration);
    return true;
}