import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IMenuProps, IMenuState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import SubMenu from './SubMenu';
import Item from './Item';
import { emptyFn } from '../../utils/helper';
import { DropdownContext } from '../Dropdown/context';
import { MenuItemContext, SubMenuContext, ContextMenuContext } from './context';
import ContextMenu from './ContextMenu';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Menu extends React.Component<IMenuProps, IMenuState> {
    static defaultProps: TDefaultProps<IMenuProps>;
    static propTypes: TPropTypes<IMenuProps>;

    static SubMenu: typeof SubMenu;
    static Item: typeof Item;
    static ContextMenu: typeof ContextMenu;
    static contextType = ContextMenuContext;
    context!: React.ContextType<typeof ContextMenuContext>

    clickedIds: string[] = [];      // 从左到右依次为menu中叶节点到根结点的id，如果以后需要整个路径的所有id可以直接使用

    handleClick = (e: React.MouseEvent) => {
        if (this.clickedIds.length === 0) {
            return;
        }
        const id = this.clickedIds.shift()!;
        this.clickedIds = [];
        this.props.onClick!(id, e);
        this.context.onClick();
    }

    handleItemClick = (id: string) => {
        this.clickedIds.push(id);
    }

    handleContextMenu = (e: React.MouseEvent) => {
        e.preventDefault();
    }

    componentDidMount() {
        this.forceUpdate();     // 更新context中的ref
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, itemStyle,
            selectedIds, visible: propsVisible, type, itemClassName
        } = this.props;

        const selfVisible = this.context.visible && propsVisible;
        const cls = classNames(prefixCls, `${prefixCls}-${type}`, className, {
            [`${prefixCls}-hidden`]: !selfVisible
        });

        return (
            <DropdownContext.Consumer>
                {({ visible: dropdownVisible }) => (
                    // dropdown隐藏时，menu自己还是需要visible，不然会影响动画
                    <MenuItemContext.Provider
                        value={{
                            itemStyle,
                            itemClassName,
                            clickHandler: this.handleItemClick,
                            selectedIds: selectedIds!,
                        }}
                    >
                        <SubMenuContext.Provider
                            value={{
                                zIndex: 10000,
                                visible: dropdownVisible && selfVisible!,
                                type,
                                horizontalPlacement: 'right'
                            }}
                        >
                            <ul className={cls} style={style} onClick={this.handleClick} onContextMenu={this.handleContextMenu}>
                                {children}
                            </ul>
                        </SubMenuContext.Provider>
                    </MenuItemContext.Provider>
                )}
            </DropdownContext.Consumer>
        );
    }
}

Menu.defaultProps = {
    prefixCls: 'dui-menu',
    onClick: emptyFn,
    selectedIds: [],
    visible: true,
    type: 'normal'
};

Menu.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    onClick: PropTypes.func,
    itemStyle: PropTypes.object,
    itemClassName: PropTypes.string,
    selectedIds: PropTypes.arrayOf(PropTypes.string),
    visible: PropTypes.bool,
    type: PropTypes.oneOf(['normal', 'compact'])
};
