import * as React from 'react';
import * as PropTypes from 'prop-types';
import { ITabPaneProps, ITabPaneState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { ActiveTabIdContext } from './context';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class TabPane extends React.Component<ITabPaneProps, ITabPaneState> {
    static defaultProps: TDefaultProps<ITabPaneProps>;
    static propTypes: TPropTypes<ITabPaneProps>;

    static contextType = ActiveTabIdContext;
    context!: React.ContextType<typeof ActiveTabIdContext>;

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, forceRender, id,
        } = this.props;

        const isActive = this.context === id;
        const shouldRender = isActive || forceRender;
        const cls = classNames(prefixCls, className, {
            [`${prefixCls}-active`]: isActive
        });

        return (
            <div className={cls} style={style}>
                {shouldRender ? children : null}
            </div>
        );
    }
}

TabPane.defaultProps = {
    prefixCls: 'dui-tabpane',
    forceRender: false,
};

TabPane.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    forceRender: PropTypes.bool,
    id: PropTypes.string,
    tab: PropTypes.node,
    icon: PropTypes.node,
    activeIcon: PropTypes.node,
};
