import * as React from 'react';
import ReactDOM from 'react-dom';
import * as PropTypes from 'prop-types';
import { ISubMenuProps, ISubMenuState, ISubmenuClickEvent, IPopoutMetrics } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import Item from './Item';
import { SubMenuContext } from './context';
import Keep from '../Dropdown/Keep';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';
import { emptyFn } from '../../utils/helper';

const DEFAULT_METRICS: IPopoutMetrics = {
    verticalPlacement: 'bottom',
    horizontalPlacement: 'right',
    location: { left: '0px', top: '0px' },
    forcedHeight: 0,
};

const SUBMENU_OFFSET = -2; // 子菜单弹出层在水平方向相对于父元素的偏移像素量，负值表示靠近，正值表示远离

export default class SubMenu extends React.Component<ISubMenuProps, ISubMenuState> {
    static defaultProps: TDefaultProps<ISubMenuProps>;
    static propTypes: TPropTypes<ISubMenuProps>;
    static contextType = SubMenuContext;

    context!: React.ContextType<typeof SubMenuContext>;

    itemRef = React.createRef<Item>();
    popupWrapperRef = React.createRef<HTMLDivElement>();
    popupContentRef = React.createRef<HTMLUListElement>();

    state: ISubMenuState = {
        visible: false,
        metrics: {
            ...DEFAULT_METRICS
        }
    }

    static getDerivedStateFromProps(props: ISubMenuProps, state: ISubMenuState): Partial<ISubMenuState> | null {
        if (props.disabled) {
            return { visible: false };  // 避免重新enable后自动弹出
        }
        return null;
    }

    updatePopoutMetrics() {
        const item = this.itemRef.current;
        if (!item) {
            return;
        }
        const itemDom = item.domRef.current;
        if (!itemDom) {
            return;
        }
        const popupDom = this.popupWrapperRef.current;
        if (!popupDom) {
            return;
        }

        // 计算可以弹出的方向，此时坐标系为viewport
        const { innerWidth: maxRight, innerHeight: maxBottom } = window;
        const { right: parentRight, left: parentLeft, top: parentTop, bottom: parentBottom } = itemDom.getBoundingClientRect();
        const { width, height } = popupDom.getBoundingClientRect();

        const { horizontalPlacement: parentMenuPlacement } = this.context;
        const availableDirections: IPopoutMetrics['horizontalPlacement'][] = [];
        if (parentRight + width < maxRight) {
            availableDirections.push('right');
        }
        if (parentLeft - width > 0) {
            availableDirections.push('left');
        }

        // 优先维持上一级子菜单弹出的水平方向，若屏幕空间两边都不够，就强行弹右边
        const horizontalPlacement = availableDirections.includes(parentMenuPlacement) ? parentMenuPlacement : (availableDirections[0] || 'right');

        const gap = 2;  // 与viewport间留一点缝隙
        const remainingBottom = maxBottom - parentTop - gap;
        const remainingTop = parentBottom - gap;

        let verticalPlacement: IPopoutMetrics['verticalPlacement'] = 'bottom';
        let forcedHeight = 0;

        if (height >= remainingBottom) {
            if (height >= remainingTop) {   // 必须包含边界，否则会出现消失时高度突变
                // 上下都放不下，选更宽的那边，同时压缩高度，出现滚动条
                if (remainingBottom >= remainingTop) {
                    verticalPlacement = 'bottom';
                    forcedHeight = remainingBottom;
                } else {
                    verticalPlacement = 'top';
                    forcedHeight = remainingTop;
                }
            } else {
                // 上边可以放下
                verticalPlacement = 'top';
            }
        } else {
            // 下边可以放下
            verticalPlacement = 'bottom';
        }
        // 计算弹出位置的坐标，使用手动计算坐标的方式
        // 1.若使用css相对于item确定位置，用户在菜单根元素设置overflow:auto时弹出会失效
        // 2.若相对于菜单根元素定位，用户在菜单项上使用position时会导致子菜单定位错误，同时引入1问题
        // 所以选择使用absolute定位，挂载到body上；若使用fixed定位，在元素显示时进行滚动位置会显示不正确
        const resultLeft = window.pageXOffset + (
            horizontalPlacement === 'right' ?
                (parentRight + SUBMENU_OFFSET)
                : (parentLeft - width - SUBMENU_OFFSET)
        );
        const resultTop = window.pageYOffset + (
            verticalPlacement === 'bottom' ?
                parentTop
                : (parentBottom - (forcedHeight || height))
        );

        const result: IPopoutMetrics = {
            verticalPlacement,
            horizontalPlacement,
            location: { left: `${resultLeft}px`, top: `${resultTop}px` },
            forcedHeight: forcedHeight,
        };

        this.setState({ metrics: result });
    }

    isVisible(): boolean {
        const { visible: selfVisible } = this.state;
        const { visible: parentVisible } = this.context;
        const { disabled } = this.props;
        return parentVisible && selfVisible && !disabled;
    }

    setStateVisible(visible: boolean) {
        this.setState({ visible }, () => {
            this.props.onVisibleChange!(visible);
        });
    }

    handleMouseEnter = () => {
        // word遇到了在submenu内部移动鼠标也触发了handleMouseEnter的bug，原因未知，这里兼容处理下
        if (this.props.disabled || this.isVisible()) {
            return;
        }
        this.setState({
            // 先移除之前强制设定的高度
            metrics: { ...this.state.metrics, forcedHeight: 0 }
        }, () => {
            // 重新计算合适的高度和位置并显示，合并到一次render中做完
            this.updatePopoutMetrics();
            this.setStateVisible(true);
        });
    }

    handleMouseLeave = (e: React.MouseEvent) => {
        // win10 chrome在快速点击时除了先触发click外，有一定几率再额外错误触发mouseleave/mouseout，导致子菜单被收回
        // https://bugs.chromium.org/p/chromium/issues/detail?id=798535
        // 所以这里额外检查relatedTarget
        if (this.props.disabled || !e.relatedTarget) {
            return;
        }
        this.setStateVisible(false);
    }

    handleSubmenuClick: React.MouseEventHandler = e => {
        (e.nativeEvent as ISubmenuClickEvent)._dui_submenu_click = true;
        e.stopPropagation();
        // 阻止触发documment.click
        e.nativeEvent.stopImmediatePropagation();
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, id, title, disabled, popupStyle, popupClassName, zIndex: forcedZIndex
        } = this.props;
        const { zIndex: contextZIndex, type } = this.context;
        const { location, verticalPlacement, forcedHeight, horizontalPlacement } = this.state.metrics;

        const zIndex = forcedZIndex || contextZIndex;
        const visible = this.isVisible();

        const cls = classNames(prefixCls, className, {
            [`${prefixCls}-disabled`]: disabled
        });
        const popupWrapperCls = classNames(
            `${prefixCls}-wrapper`,
            `${prefixCls}-wrapper-${visible ? 'visible' : `hidden-${verticalPlacement}`}`
        );
        const contentCls = classNames(`${prefixCls}-content`, popupClassName, `${prefixCls}-content-${type}`);

        return (
            <SubMenuContext.Provider value={{ zIndex: zIndex + 1, visible, type, horizontalPlacement }}>
                <div
                    onMouseEnter={this.handleMouseEnter}
                    onMouseLeave={this.handleMouseLeave}
                >
                    <Keep>
                        <Item
                            id={id}
                            className={cls}
                            style={style}
                            ref={this.itemRef}
                            disabled={disabled}
                            onClick={this.handleSubmenuClick}
                        >
                            {title || id}
                        </Item>
                    </Keep>
                    {ReactDOM.createPortal(
                        <div
                            className={popupWrapperCls}
                            ref={this.popupWrapperRef}
                            style={{
                                ...location,
                                zIndex
                            }}
                        >
                            <ul
                                className={contentCls}
                                style={{
                                    height: forcedHeight ? `${forcedHeight}px` : undefined,
                                    ...popupStyle
                                }}
                                ref={this.popupContentRef}
                            >
                                {children}
                            </ul>
                        </div>,
                        document.body
                    )}
                </div>
            </SubMenuContext.Provider>
        );
    }
}

SubMenu.defaultProps = {
    prefixCls: 'dui-menu-submenu',
    id: '',
    disabled: false,
    onVisibleChange: emptyFn
};

SubMenu.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    id: PropTypes.string,
    title: PropTypes.node,
    disabled: PropTypes.bool,
    popupStyle: PropTypes.object,
    popupClassName: PropTypes.string,
    zIndex: PropTypes.number,
    onVisibleChange: PropTypes.func
};
