import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IListProps, IListState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';
import { emptyFn } from '../../utils/helper';
import Item from './Item';

export default class List extends React.Component<IListProps, IListState> {
    static defaultProps: TDefaultProps<IListProps>;
    static propTypes: TPropTypes<IListProps>;

    static Item: typeof Item;

    keys: { [key: string]: string } = {};

    render() {
        return consumeTheme(this.themedRender);
    }

    renderListItem = (item: any, index: number) => {
        const { renderItem } = this.props;
        let key;
        if (item.key) {
            key = item.key;
        } else {
            key = `dui-list-item-${index}`;
        }
        this.keys[index] = key;
        return renderItem(item, index);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, /* size, */ dataSource,
        } = this.props;
        const classes = classNames(prefixCls, /* `${prefixCls}-${size}`, */ className);
        const ulClass = `${prefixCls}-items`;
        const items = dataSource.map(this.renderListItem);
        const cloneItems: Array<React.ReactNode> = [];
        React.Children.forEach(items, (child: any, index) => {
            cloneItems.push(
                React.cloneElement(child, {
                    key: this.keys[index],
                })
            );
        });
        return (
            <div className={classes} style={style}>
                <ul className={ulClass}>
                    {cloneItems}
                </ul>
            </div>
        );
    }
}

List.defaultProps = {
    prefixCls: 'dui-list',
    dataSource: [],
    renderItem: emptyFn,
    // size: 'default',
};

List.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    dataSource: PropTypes.array,
    renderItem: PropTypes.func,
    // size: PropTypes.oneOf(['default', 'big', 'small']),
};
