import * as React from 'react';
import ReactDOM from 'react-dom';
import * as PropTypes from 'prop-types';
import { IStatusProps, IStatusState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Status extends React.Component<IStatusProps, IStatusState> {
    static defaultProps: TDefaultProps<IStatusProps>;
    static propTypes: TPropTypes<IStatusProps>;

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, message, size, children, visible, zIndex
        } = this.props;

        const cls = classNames(prefixCls, className, `${prefixCls}-${size}`, `${prefixCls}-${children ? 'attached' : 'fixed'}`, {
            [`${prefixCls}-hidden`]: !visible
        });

        // 挂载在元素附近
        if (children) {
            return (
                <div className={classNames(`${prefixCls}-container`)}>
                    <div>{children}</div>
                    <div className={cls} style={style}>
                        {message}
                    </div>
                </div>
            );
        }

        // 显示在页面固定位置
        return ReactDOM.createPortal(
            <div className={cls} style={{ zIndex, ...style }}>
                {message}
            </div>,
            document.body
        );
    }
}

Status.defaultProps = {
    prefixCls: 'dui-status',
    size: 'small'
};

Status.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    message: PropTypes.string,
    size: PropTypes.oneOf(['small', 'big']),
    visible: PropTypes.bool,
    zIndex: PropTypes.number
};
