import * as React from 'react';
import ReactDOM from 'react-dom';
import * as PropTypes from 'prop-types';
import { emptyFn } from '../../utils/helper';
import { ITooltipProps, ITooltipState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import Trigger from '../../utils/Trigger';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';
import { decideKeepDom } from '../../utils/keepDom';

type TOverlayPosition = Pick<React.CSSProperties, 'left' | 'top'>;

const TOOLTIP_GAP = 11;  // tooltip与挂载元素间的间距，像素数

const TOOLTIP_TRANSITION_DURATION = 200;    // 与css中一致

export default class Tooltip extends React.Component<ITooltipProps, ITooltipState> {

    static defaultProps: TDefaultProps<ITooltipProps>;
    static propTypes: TPropTypes<ITooltipProps>;

    rootRef = React.createRef<HTMLDivElement>();
    overlayContainerRef = React.createRef<HTMLDivElement>();
    prevVisible?: boolean;

    state: ITooltipState = {
        currentVisible: !!this.props.defaultVisible,
    }

    static getDerivedStateFromProps(props: ITooltipProps, state: ITooltipState): Partial<ITooltipState> | null {
        if (props.disabled) {
            return { currentVisible: false }; // 避免重新enable后自动弹出
        }
        return null;
    }

    componentDidMount() {
        this.forceUpdate();     // 更新位置
    }

    componentDidUpdate() {
        const visible = this.isVisible();
        const prevVisible = this.prevVisible;
        this.prevVisible = visible;
        if (visible && !prevVisible && this.props.destroyOnClose) {
            // 刚mount的dom需要更新位置
            this.forceUpdate();
        }
    }

    isVisible(): boolean {
        const { visible: forceVisible, disabled } = this.props;
        if (disabled) {
            return false;
        }

        const { currentVisible: stateVisible } = this.state;
        return typeof forceVisible === 'boolean' ? forceVisible : stateVisible;
    }

    setStateVisible(currentVisible: boolean) {
        this.setState({ currentVisible });
        this.props.onVisibleChange!(currentVisible);
    }

    show = () => {
        if (this.props.disabled) {
            return;
        }
        this.setStateVisible(true);
    }

    hide = () => {
        // trigger会多次调用此方法
        if (!this.isVisible()) {
            return;
        }
        this.setStateVisible(false);
    }

    toggle = () => {
        if (this.props.disabled) {
            return;
        }
        if (this.isVisible()) {
            this.hide();
        } else {
            this.show();
        }
    }

    calcOverlayContainerPosition(): TOverlayPosition {
        const rootDom = this.rootRef.current;
        const containerDom = this.overlayContainerRef.current;
        if (!rootDom || !containerDom) {
            return { left: '0px', top: '0px' };
        }
        const { left: leftX, right: rightX, top: topY, bottom: bottomY } = rootDom.getBoundingClientRect();
        const { width, height } = containerDom.getBoundingClientRect();
        const { placement, containerOffsetX, containerOffsetY } = this.props;
        const centerX = (leftX + rightX) / 2;
        const centerY = (topY + bottomY) / 2;
        let resultLeft = window.pageXOffset + containerOffsetX!;
        let resultTop = window.pageYOffset + containerOffsetY!;

        if (placement === 'bottom') {
            resultLeft += centerX - width / 2;
            resultTop += bottomY + TOOLTIP_GAP;
        } else if (placement === 'top') {
            resultLeft += centerX - width / 2;
            resultTop += topY - TOOLTIP_GAP - height;
        } else if (placement === 'left') {
            resultLeft += leftX - TOOLTIP_GAP - width;
            resultTop += centerY - height / 2;
        } else if (placement === 'right') {
            resultLeft += rightX + TOOLTIP_GAP;
            resultTop += centerY - height / 2;
        }

        return { left: `${resultLeft}px`, top: `${resultTop}px` };
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            className, style, prefixCls, title, placement, children, arrowClassName,
            overlayClassName, overlayStyle, trigger, arrowStyle, offset, zIndex,
            containerClassName, containerStyle, destroyOnClose
        } = this.props;

        const wrapperCls = classNames(prefixCls, className, `${prefixCls}-wrapper`);
        const overlayCls = classNames(`${prefixCls}-content`, overlayClassName);
        const overlayStyleWithOffset: React.CSSProperties = {
            ...overlayStyle,
            transform: `translate${placement === 'top' || placement === 'bottom' ? 'X' : 'Y'}(${offset}px)`
        };
        const arrowCls = classNames(`${prefixCls}-arrow`, arrowClassName, `${prefixCls}-arrow-${placement}`);
        const visible = this.isVisible();
        const overlayContainerCls = classNames(`${prefixCls}-container`, containerClassName, {
            [`${prefixCls}-container-visible`]: visible
        });

        const shouldDestroyDom = destroyOnClose && !decideKeepDom.call(this, TOOLTIP_TRANSITION_DURATION, visible);
        const portalContent = shouldDestroyDom ?
            null : (
                <div
                    className={overlayContainerCls}
                    ref={this.overlayContainerRef}
                    style={{ zIndex, ...containerStyle, ...this.calcOverlayContainerPosition() }}
                >
                    <div
                        className={overlayCls}
                        style={overlayStyleWithOffset}
                    >
                        {title}
                    </div>
                    <div className={arrowCls} style={arrowStyle} />
                </div>
            );

        return (
            <Trigger
                className={wrapperCls}
                style={style}
                action={this.hide}
                disabled={trigger !== 'click'}
            >
                <div
                    onMouseEnter={trigger === 'hover' ? this.show : undefined}
                    onMouseLeave={trigger === 'hover' ? this.hide : undefined}
                    ref={this.rootRef}
                >
                    <div onClick={trigger === 'click' ? this.toggle : undefined}>
                        {children}
                    </div>
                    {ReactDOM.createPortal(portalContent, document.body)}
                </div>
            </Trigger>
        );
    }

}

Tooltip.defaultProps = {
    prefixCls: 'dui-tooltip',
    title: '',
    placement: 'bottom',
    trigger: 'hover',
    onVisibleChange: emptyFn,
    disabled: false,
    offset: 0,
    containerOffsetX: 0,
    containerOffsetY: 0,
    destroyOnClose: true,
};

Tooltip.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    placement: PropTypes.string,
    trigger: PropTypes.string,
    title: PropTypes.node,
    defaultVisible: PropTypes.bool,
    visible: PropTypes.bool,
    onVisibleChange: PropTypes.func,
    overlayStyle: PropTypes.object,
    overlayClassName: PropTypes.string,
    disabled: PropTypes.bool,
    arrowStyle: PropTypes.object,
    arrowClassName: PropTypes.string,
    offset: PropTypes.number,
    containerOffsetX: PropTypes.number,
    containerOffsetY: PropTypes.number,
    zIndex: PropTypes.number,
    containerClassName: PropTypes.string,
    containerStyle: PropTypes.object,
    destroyOnClose: PropTypes.bool,
};