import * as React from 'react';
import * as PropTypes from 'prop-types';
import { ICheckboxGroupProps, ICheckboxGroupState } from './interface';
import { renderChildren } from './util';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import { emptyFn } from '../../utils/helper';
import Checkbox from './Checkbox';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Group extends React.Component<ICheckboxGroupProps, ICheckboxGroupState> {
    static defaultProps: TDefaultProps<ICheckboxGroupProps>;
    static propTypes: TPropTypes<ICheckboxGroupProps>;

    state: ICheckboxGroupState = {
        values: this.props.defaultValues || []
    };

    static getDerivedStateFromProps(props: ICheckboxGroupProps, state: ICheckboxGroupState): Partial<ICheckboxGroupState> | null {
        if ('values' in props) {
            return { values: props.values };
        }
        return null;
    }

    handleSingleChange = (checked: boolean, e: any) => {
        if (this.props.disabled) {
            return;
        }
        const checkedValues = this.state.values;
        let newValues = [];
        if (checkedValues.indexOf(e.target.value) >= 0 && !checked) {
            newValues = checkedValues.filter((v: any) => v !== e.target.value);
            this.setState({ values: newValues });
            this.props.onChange!(newValues, e);
        } else if (checkedValues.indexOf(e.target.value) === -1 && checked) {
            newValues = checkedValues.concat(e.target.value);
            this.setState({ values: newValues });
            this.props.onChange!(newValues, e);
        }
    };

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const { prefixCls, className, style, itemStyle } = this.props;
        const cls = classNames(prefixCls, className);
        return (
            <div className={cls} style={style}>
                {renderChildren(this.props, this.state.values, this.handleSingleChange, Checkbox, itemStyle)}
            </div>
        );
    }
}

Group.defaultProps = {
    prefixCls: 'dui-checkbox-group',
    disabled: false,
    onChange: emptyFn
};

Group.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    options: PropTypes.array,
    disabled: PropTypes.bool,
    values: PropTypes.array,
    children: PropTypes.node,
    defaultValues: PropTypes.array,
    onChange: PropTypes.func,
    itemStyle: PropTypes.object
};
