import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IProgressProps, IProgressState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Progress extends React.Component<IProgressProps, IProgressState> {
    static defaultProps: TDefaultProps<IProgressProps>;
    static propTypes: TPropTypes<IProgressProps>;

    state: IProgressState = {
        normalizedPercentage: 0,
        animationDuration: 0
    };

    /**
     * 对百分比数字进行边界检查
     * @param percentage 百分比
     */
    static normalizePercentage(percentage?: number) {
        return Math.max(0, Math.min(100, percentage || 0));
    }

    /**
     * 格式化不精确的百分比字符串，用于文字显示
     * @param percentage 百分比
     */
    static formattedPercentage(percentage?: number) {
        return Math.round(Progress.normalizePercentage(percentage)) + '%';
    }

    /**
     * 计算从原百分比移动到新百分比的时间
     * @param oldNormalizedPercentage 原百分比
     * @param newNormalizedPercentage 新百分比
     * @param duration 每移动 1% 的毫秒数
     */
    static getAnimationDuration(oldNormalizedPercentage: number, newNormalizedPercentage: number, duration: number) {
        return Math.abs(newNormalizedPercentage - oldNormalizedPercentage) * duration;
    }

    static getDerivedStateFromProps(props: IProgressProps, state: IProgressState): Partial<IProgressState> | null {
        const oldPercentage = state.normalizedPercentage;
        const newPercentage = Progress.normalizePercentage(props.percentage);
        const duration = props.duration || 30;
        return {
            normalizedPercentage: newPercentage,
            animationDuration: Progress.getAnimationDuration(oldPercentage, newPercentage, duration)
        };
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style,
            showInfo, isLoading,
            barClassName, barStyle,
            completedClassName, completedStyle,
            remainingClassName, remainingStyle,
            infoClassName, infoStyle,
        } = this.props;

        const {
            normalizedPercentage,
            animationDuration,
        } = this.state;

        const containerCls = classNames(prefixCls, className, {
            [`${prefixCls}-loading`]: isLoading
        });

        const barCls = classNames(`${prefixCls}-bar`, barClassName);
        const completedCls = classNames(`${prefixCls}-bar-completed`, completedClassName);
        const remainingCls = classNames(`${prefixCls}-bar-remaining`, remainingClassName);
        const infoCls = classNames(`${prefixCls}-info`, infoClassName);

        return (
            <div className={containerCls} style={style}>
                <div className={barCls} style={barStyle}>
                    <div
                        className={completedCls}
                        style={{
                            width: normalizedPercentage + '%',
                            transitionDuration: animationDuration + 'ms',
                            ...completedStyle
                        }}
                    />
                    <div
                        className={remainingCls}
                        style={{
                            transitionDuration: animationDuration + 'ms',
                            ...remainingStyle
                        }}
                    />
                </div>
                {showInfo && <div className={infoCls} style={infoStyle}>{normalizedPercentage + '%'}</div>}
            </div>
        );
    }
}

Progress.defaultProps = {
    prefixCls: 'dui-progress',
    percentage: 0,
    showInfo: false,
    isLoading: false,
    duration: 10,
};

Progress.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    percentage: PropTypes.number,
    showInfo: PropTypes.bool,
    isLoading: PropTypes.bool,
    duration: PropTypes.number,
    barClassName: PropTypes.string,
    completedClassName: PropTypes.string,
    remainingClassName: PropTypes.string,
    infoClassName: PropTypes.string,
    barStyle: PropTypes.object,
    completedStyle: PropTypes.object,
    remainingStyle: PropTypes.object,
    infoStyle: PropTypes.object,
};
