import { IModalProps } from './interface';
import Modal, { MODAL_LEAVING_TRANSITION_DURATION } from './Modal';
import { showWithTransition } from '../../utils/show';
import { Omit } from '../../common/interface';
import { wrapWithConfigDefaults } from '../../utils/helper';

export default wrapWithConfigDefaults(function (config: Omit<IModalProps, 'visible'>) {
    // 避免未传入onClose时injectClose onClose使用emptyFn而非onCancel
    config.onClose = config.onClose || config.onCancel;

    return showWithTransition<IModalProps>({
        Component: Modal,
        transitionDuration: MODAL_LEAVING_TRANSITION_DURATION,
        autoClose: false,
        props: config as any,
        forcedProps: { destroyOnClose: true },
        injectClosePropNames: ['onOk', 'onCancel', 'onClickAction', 'onClose']     // onClickMask交给onClose
    });
});
