import * as React from 'react';
import * as PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { ISnackbarProps, ISnackbarState, ISnackbarAction } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { emptyFn, isIE, isDom } from '../../utils/helper';
import show from './show';
import { decideKeepDom } from '../../utils/keepDom';
import Spin from '../Loading/Spin';
import Container from './Container';
import { ContainerContext } from './context';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';
import { updateVisibleSnackbars, getMarginTop } from './queue';
import { SNACKBAR_LEAVING_TRANSITION_DURATION } from './constant';

export default class Snackbar extends React.Component<ISnackbarProps, ISnackbarState> {
    static defaultProps: TDefaultProps<ISnackbarProps>;
    static propTypes: TPropTypes<ISnackbarProps>;

    static show: typeof show;
    static Container: typeof Container;

    static contextType = ContainerContext;
    context!: React.ContextType<typeof ContainerContext>

    selfRef = React.createRef<HTMLDivElement>();

    getContainerDom(): HTMLElement {
        // 收集表外网会出现context是一个非dom对象的情况，代理到本地不复现，这里兼容处理下
        return isDom(this.context) ? this.context! : this.props.containerDom!;
    }

    handleClickAction = () => {
        this.props.onClickAction!();
        this.handleClose();
    }

    handleClose = () => {
        // close交给父组件决定
        this.props.onClose!();
    }

    componentDidMount() {
        this.handleEffect();
    }

    componentDidUpdate() {
        this.handleEffect();
    }

    handleEffect() {
        this.fixShrinkedWidthForIE();
        updateVisibleSnackbars(this);
    }

    fixShrinkedWidthForIE() {
        // IE11上绝对定位的子元素若设置了大于0的偏移量，其大小会受到其定位参考元素的大小的限制而收缩
        // 此问题在Snackbar在Modal内出现时尤其明显
        // 所以需要手动修正元素宽度
        const { visible, style } = this.props;
        if (!(visible && isIE())) {
            return;
        }
        const selfDom = this.selfRef.current;
        const isWidthAssigned = style && ('width' in style);
        if (selfDom && !isWidthAssigned) {
            const left = selfDom.style.left;
            selfDom.style.left = '-9999px';
            const width = selfDom.getBoundingClientRect().width;    // 亲测不用setTimeout也行
            selfDom.style.width = `${width}px`;
            selfDom.style.left = left;
        }
    }

    renderAction(classNames: TThemedClassNames) {
        const { prefixCls, action } = this.props;
        if (!action) {
            return null;
        }
        // 区分ReactNodeArray和我们自己的配置array
        if (Array.isArray(action) && action.length > 0 && (action[0] as any).text) {
            return (
                <span className={classNames(`${prefixCls}-actions`)}>
                    {(action as ISnackbarAction[]).map((a, index) =>
                        <span
                            key={index}
                            className={classNames(`${prefixCls}-action`)}
                            onClick={() => {
                                a.onClick && a.onClick();
                                this.handleClose();
                            }}
                        >
                            {a.text}
                        </span>
                    )}
                </span>
            );
        }

        return (
            <span
                className={classNames(`${prefixCls}-action`)}
                onClick={this.handleClickAction}
            >
                {action}
            </span>
        );

    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, action, children, message, visible, closable, type, zIndex, showIcon, spinType
        } = this.props;

        const placement = 'top';    // 暂不放开其他位置
        const messageCls = classNames(`${prefixCls}-message`);

        if (!decideKeepDom.call(this, SNACKBAR_LEAVING_TRANSITION_DURATION, visible)) {
            return null;
        }
        const containerDom = this.getContainerDom();
        const cls = classNames(prefixCls, className, `${prefixCls}-${placement}`, `${prefixCls}-${type}`, {
            [`${prefixCls}-with-icon`]: showIcon,
            [`${prefixCls}-hidden`]: !visible,
            [`${prefixCls}-in-container`]: containerDom !== document.body
        });

        return ReactDOM.createPortal(
            <div className={cls} style={{ zIndex, marginTop: `${getMarginTop(this)}px`, ...style }} ref={this.selfRef}>
                {type === 'loading' && showIcon ?
                    <Spin
                        type={spinType}
                        className={classNames(`${prefixCls}-loading-indicator`)}
                        circleClassName={classNames(`${prefixCls}-loading-indicator-circle`)}
                    />
                    : null
                }
                <span className={messageCls}>{children || message}</span>
                {(action || closable) ?
                    <span className={classNames(`${prefixCls}-controls`)}>
                        {this.renderAction(classNames)}
                        {closable ?
                            <span className={classNames(`${prefixCls}-close`)} onClick={this.handleClose} />
                            : null}
                    </span>
                    : null
                }
            </div>,
            containerDom
        );
    }
}

Snackbar.defaultProps = {
    prefixCls: 'dui-snackbar',
    // placement: 'top',
    onClickAction: emptyFn,
    closable: false,
    type: 'info',
    onClose: emptyFn,
    showIcon: true,
    containerDom: typeof document !== 'undefined' ? document.body : undefined,
    spinType: 'primary'
};

Snackbar.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    action: PropTypes.any,
    message: PropTypes.node,
    // placement: PropTypes.oneOf(['top', 'bottom']),
    visible: PropTypes.bool,
    onClickAction: PropTypes.func,
    closable: PropTypes.bool,
    type: PropTypes.oneOf(['info', 'success', 'error', 'loading']),
    onClose: PropTypes.func,
    zIndex: PropTypes.number,
    showIcon: PropTypes.bool,
    containerDom: PropTypes.object,
    spinType: PropTypes.oneOf(['light', 'dark', 'primary']),
};
