import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IMetaProps, IMetaState, } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Meta extends React.Component<IMetaProps, IMetaState> {
    static defaultProps: TDefaultProps<IMetaProps>;
    static propTypes: TPropTypes<IMetaProps>;

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, avatar, description, title,
        } = this.props;
        const cls = classNames(prefixCls, className);
        const avatarCls = classNames(`${prefixCls}-avatar`);
        const contentCls = classNames(`${prefixCls}-content`);
        const titleCls = classNames(`${prefixCls}-title`);
        const descriptionCls = classNames(`${prefixCls}-description`);
        return (
            <React.Fragment>
                <div
                    className={cls}
                    style={style}
                >
                    {avatar &&
                        <div
                            className={avatarCls}
                        >
                            {avatar}
                        </div>
                    }
                    {(title || description) &&
                        <div
                            className={contentCls}
                        >
                            {title &&
                                <h4
                                    className={titleCls}
                                >
                                    {title}
                                </h4>
                            }
                            {description &&
                                <div
                                    className={descriptionCls}
                                >
                                    {description}
                                </div>
                            }
                        </div>
                    }
                </div>
                {children}
            </React.Fragment>
        );
    }
}

Meta.defaultProps = {
    prefixCls: 'dui-list-item-meta',
};

Meta.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    avatar: PropTypes.node,
    title: PropTypes.node,
    description: PropTypes.node,
};
