import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IAvatarProps } from './interface';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Avatar extends React.Component<IAvatarProps, {}> {

    static defaultProps: TDefaultProps<IAvatarProps>;
    static propTypes: Partial<TPropTypes<IAvatarProps>>;

    render() {
        return consumeTheme(this.themedRender);
    }

    private themedRender = (classNames: TThemedClassNames) => {
        const { prefixCls, size, src, style, children, className, mask, useDefaultImage, radius, footer, clickable, ...rest } = this.props;

        const sizeStyle: React.CSSProperties = typeof size === 'number' ?
            { width: `${size}px`, height: `${size}px` }
            : {};

        const avatarCls = classNames(prefixCls, className, {
            [`${prefixCls}-${size}`]: typeof size === 'string',
            [`${prefixCls}-transparent`]: !!src,
            [`${prefixCls}-clickable`]: clickable
        });
        return (
            <span {...rest} className={avatarCls} style={{ borderRadius: radius, ...sizeStyle, ...style }}>
                {useDefaultImage || src ?
                    <span className={classNames(`${prefixCls}-img`)} style={{ backgroundImage: src ? `url(${src})` : undefined }} />
                    : null
                }
                {children}
                {footer && (
                    React.isValidElement(footer) ?
                        <span className={classNames(`${prefixCls}-footer`)}>{footer}</span>
                        : <span className={classNames(`${prefixCls}-footer`, `${prefixCls}-footer-${footer}`)} />
                )}
                {mask ?
                    <span className={classNames(`${prefixCls}-mask`)}>{mask}</span>
                    : null}
            </span>
        );
    }

}

Avatar.defaultProps = {
    prefixCls: 'dui-avatar',
    src: '',
    size: 'default',
    mask: '',
    useDefaultImage: true,
    radius: '50%',
    clickable: false,
};

Avatar.propTypes = {
    prefixCls: PropTypes.string,
    className: PropTypes.string,
    style: PropTypes.object,
    size: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    src: PropTypes.string,
    mask: PropTypes.string,
    useDefaultImage: PropTypes.bool,
    radius: PropTypes.string,
    footer: PropTypes.node,
    clickable: PropTypes.bool
};
