import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IKeepState, IKeepProps, IKeepClickEvent } from './interface';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

/**
 * 发生在此组件之内的click不会导致dropdown隐藏
 */
export default class Keep extends React.Component<IKeepProps, IKeepState> {
    static defaultProps: TDefaultProps<IKeepProps>;
    static propTypes: TPropTypes<IKeepProps>;

    handleClick: React.MouseEventHandler = (e) => {
        if (this.props.disabled) {
            return;
        }
        (e.nativeEvent as IKeepClickEvent)._dui_dropdown_keep = true;
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const { prefixCls, className, style, children } = this.props;
        const cls = classNames(prefixCls, className);

        return (
            <div className={cls} style={style} onClick={this.handleClick}>
                {children}
            </div>
        );
    }

}

Keep.defaultProps = {
    prefixCls: 'dui-dropdown-keep',
    disabled: false
};

Keep.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    disabled: PropTypes.bool
};