import {
    IStyleProps
} from '../../common/interface';

export interface IPreviewProps extends IStyleProps {

    /**
     * 作为受控组件时，控制是否显示
     */
    visible?: boolean;

    /**
     * 需要查看的图片 URL 列表
     * URL 为空/失效/加载中时，会显示 loading
     */
    urlList: string[];

    /**
     * 针对查看的 urlList，给了一份可以先展示预览的
     * 如果不传，那样加载大图过程中就先透明
     * 如果传了，那优先展示 previewList，等 urlList 请求完成，再展示 urlList
     */
    previewList?: string[];

    /**
     * 打开图片查看器时的初始图片序号
     * @default 0
     */
    initialIndex?: number;

    /**
     * mask 样式
     * @default {}
     */
    maskStyle?: React.CSSProperties;

    /**
     * 是否显示页码
     * @default true
     */
    showPageNumber?: boolean;

    /**
     * 可以根据传入的当前图片序号，传入需要固定展示在窗口顶部或底部的挂件
     * 提示：
     * - 关闭按钮会盖在顶部挂件上方
     * - 挂件会自动隐藏，鼠标 hover 会重新显示
     * - 挂件上可以用 backdrop-filter(chrome) / -webkit-backdrop-filter(safari) 实现毛玻璃效果
     */
    renderTopWidgets?: (imageIndex: number) => React.ReactChildren | null;

    renderBottomWidgets?: (imageIndex: number) => React.ReactChildren | null;

    /**
     * 图片缩放的最大倍数，以初始尺寸为单位
     * @default 5
     */
    maxScale?: number;

    /**
     * 切换图片的事件
     * @param imageIndex 切换到的图片序号
     */
    onChange?: (imageIndex: number) => any;

    /**
     * 右击图片的事件
     * @param event 鼠标事件
     * @param imageIndex 当前所在的图片序号
     */
    onContextMenu?: (event: React.MouseEvent<HTMLImageElement>, imageIndex: number) => any;

    /**
     * 关闭查看器的事件
     */
    onClose?: Function;

    /**
     * 是否展示底部 放大/缩小/1:1显示/适应显示 控件
     */
    showBottomControl?: boolean;

    /**
     * 图片裁剪样式, 进入图片查看器时，图片数目不一定为1，因此需要传入的图片蒙板参数为一个数组。
     */
    imageClipStyle?: Array<{
        marginTop: number;
        marginLeft: number;
        height: number;
        width: number;
    }>;

    /**
     * 图片查看器挂载节点
     */
    containerDom?: Element;

    /**
     * 自定义底部按钮控件右侧扩增
    */
    renderExtraButtons?: () => React.ReactChildren | null;

    /**
     * 是否循环查看，即最后一张与第一张之间可以互相切换
     */
    cycle?: boolean;

    /**
     * 初始坐标，图片出现的 scale 动画，是否也要增加一个 translate 的偏移
     */
    initialCoordinate?: {
        left: number;
        top: number;
    };
}

export enum ImageLoadCycle {
    LOADING,
    LOADED,
    FAILED,
}

export interface IPreviewState {
    currentIndex: number;
    imageState: 'loading' | 'loaded' | 'error';
    isWidgetsVisible: boolean;
    isForceHover: boolean;
    animationDirection?: 'left' | 'right';
    imageWrapperStyle: object;
    imageMarginStyle: object;
    scaleUpDisable: boolean;
    scaleDownDisable: boolean;
    showFitScreenButton: boolean;
    showActualSizeButton: boolean;
}
