import * as React from 'react';
import classNames from 'classnames';
import ua from '@tencent/docs-user-agent';
import { injectStyle } from './style';
import './polyfill';

const enableAntiAlias = ua.isMac && !ua.isIPadEmulatedMac && !ua.isFirefox;

const globalStyle = `
[class*=dui]{
    font-family: "-apple-system", "Helvetica Neue", Helvetica, "PingFang SC", "Microsoft YaHei", "Source Han Sans SC", "Noto Sans CJK SC", "WenQuanYi Micro Hei", sans-serif;
    ${enableAntiAlias ? `-webkit-font-smoothing: antialiased;` : ''}
}
${enableAntiAlias ? `.dui-button { font-weight: 500; }` : ''}
`.replace(/\n/g, '');

injectStyle('common/global.css', globalStyle);

type TThemeContext = null | {
    [K: string]: string;
};

const ThemeContext = React.createContext<TThemeContext>(null);

export type TThemedClassNames = typeof classNames;

type TRenderProp = (themedClassNames: TThemedClassNames) => React.ReactNode;

export function consumeTheme(renderProp: TRenderProp): React.ReactNode {
    return (
        <ThemeContext.Consumer>
            {(themeConfig) => {
                const classNames = createThemedClassNames(themeConfig);
                return renderProp(classNames);
            }}
        </ThemeContext.Consumer>
    );
}

function appendThemeClassNames(themeConfig: TThemeContext, className: string): string {
    if (!className || !themeConfig) {
        return className;
    }

    const currentClassList = className.split(/\s+/);
    const mergedClassList = currentClassList.concat(
        ...currentClassList.map(name => themeConfig[name]).filter(name => name)
    );

    return mergedClassList.join(' ');
}

export function createThemedClassNames(themeConfig: TThemeContext) {
    return ((...classes) => {
        return appendThemeClassNames(themeConfig, classNames(...classes));
    }) as typeof classNames;
}

export default ThemeContext;
