import * as React from 'react';
import ReactDOM from 'react-dom';
import * as PropTypes from 'prop-types';
import { IModalProps } from './interface';
import { emptyFn, isIE } from '../../utils/helper';
import Button from '../Button/index';
import Dragger from './Dragger';
import confirm from './confirm';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import { decideKeepDom } from '../../utils/keepDom';
import { ContainerContext } from '../Snackbar/context';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';
import { PreventScrollPenetrateContainer } from '../../utils/preventScrollPenetrate';

export const MODAL_LEAVING_TRANSITION_DURATION = 240;    // 与css一致

export default class Modal extends React.Component<IModalProps, {}> {

    static defaultProps: TDefaultProps<IModalProps>;
    static propTypes: TPropTypes<IModalProps>;

    static confirm: typeof confirm;

    draggerRef = React.createRef<Dragger>();

    handleClose = (e: React.MouseEvent<HTMLElement>) => {
        const { onClose, onCancel } = this.props;
        const handler = onClose || onCancel;
        handler!(e);
    };

    handleMaskClick = (e: React.MouseEvent<HTMLElement>) => {
        const { maskClosable, onClickMask } = this.props;
        if (maskClosable === true) {
            this.handleClose(e);
        }
        onClickMask!(e);
    };

    handleDraggerClick = (e: React.MouseEvent<HTMLElement>) => {
        e.stopPropagation();
    }

    handleKeyDown = (e: KeyboardEvent) => {
        const { visible, onCancel, onOk, footer } = this.props;
        if (!visible || footer || footer === null || this.draggerRef.current?.isFromInnerInput(e)) {
            // 只在显示默认footer时且不是来自modal中的输入元素时才响应键盘
            return;
        }
        if (e.key === 'Enter') {
            document.activeElement && (document.activeElement as HTMLElement).blur();   // 避免有焦点的button被enter键触发click
            onOk!();
        } else if (e.key === 'Escape') {
            onCancel!();
        }
    }

    componentDidMount() {
        this.forceUpdate();     // 更新ref current
        // 延时监听，避免通过回车触发的场景被自动关闭
        setTimeout(() => {
            document.addEventListener('keydown', this.handleKeyDown);
        }, 1000);
    }

    componentWillUnmount() {
        document.removeEventListener('keydown', this.handleKeyDown);
    }

    renderFooter = (classNames: TThemedClassNames) => {
        const {
            prefixCls, footer, okText, cancelText, action, onClickAction, onOk, onCancel, okDisabled
        } = this.props;

        if (footer === null) {
            return null;
        } else if (footer) {
            return footer;
        }

        let actionText;
        let icon;
        if (action !== null && typeof action === 'object') {
            if (React.isValidElement(action)) {
                actionText = action;
            } else {
                if ('text' in action) {
                    actionText = action.text;
                }
                if ('icon' in action) {
                    icon = action.icon;
                }
            }
        } else {
            actionText = action;
        }

        return (
            <div className={classNames(`${prefixCls}-footer`)}>
                {action ?
                    <div
                        className={classNames(`${prefixCls}-footer-action`)}
                        onClick={onClickAction}
                    >
                        {icon ?
                            typeof icon === 'string' ?
                                <div
                                    className={classNames(`${prefixCls}-footer-action-icon`)}
                                    style={{ backgroundImage: `url(${icon})` }}
                                />
                                : icon
                            : null}
                        {actionText ?
                            <div>{actionText}</div>
                            : null}
                    </div>
                    : null
                }
                {cancelText ?
                    <Button onClick={onCancel}>
                        {cancelText}
                    </Button>
                    : null
                }
                <Button className={classNames(`${prefixCls}-footer-ok`)} onClick={onOk} type="primary" disabled={okDisabled}>
                    {okText}
                </Button>
            </div>
        );
    };

    renderContent(classNames: TThemedClassNames) {
        const { prefixCls, title, content, children, } = this.props;

        const contentCls = classNames(`${prefixCls}-content`, {
            [`${prefixCls}-content-empty-title`]: title === ''
        });

        const passedContent = content || children;
        let renderedContent = passedContent;

        // 解决IE11上文字不换行bug
        if (isIE() && typeof passedContent === 'string') {
            renderedContent = <span style={{ maxWidth: '100%' }}>{passedContent}</span>;
        }

        // 解决IE上内容不垂直居中bug
        return isIE() ? (
            <div style={{ display: 'flex', flexDirection: 'column' }}>
                <div className={contentCls}>{renderedContent}</div>
            </div>
        ) : <div className={contentCls}>{renderedContent}</div>;
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, title, visible, maskStyle, maskClassName,
            mask, closable, draggable, onDragEnd, destroyOnClose, zIndex, containerDom
        } = this.props;

        const cls = classNames(prefixCls, className, `${prefixCls}-${visible ? 'visible' : 'hidden'}`);

        const maskCls = classNames(`${prefixCls}-mask`, maskClassName, `${prefixCls}-mask-${visible ? 'visible' : 'hidden'}`, {
            [`${prefixCls}-mask-display`]: mask,
            [`${prefixCls}-mask-in-container`]: containerDom !== document.body,
        });

        if (!containerDom || destroyOnClose && !decideKeepDom.call(this, MODAL_LEAVING_TRANSITION_DURATION, visible)) {
            return null;
        }

        const draggerDom = this.draggerRef.current && this.draggerRef.current.ref;

        return ReactDOM.createPortal(
            <ContainerContext.Provider value={draggerDom}>
                <PreventScrollPenetrateContainer className={maskCls} style={{ zIndex, ...maskStyle }} onClick={this.handleMaskClick} >
                    <Dragger
                        className={cls}
                        style={style}
                        draggable={draggable}
                        onDragEnd={onDragEnd}
                        onClick={this.handleDraggerClick}
                        ref={this.draggerRef}
                    >
                        {closable ?
                            <div className={classNames(`${prefixCls}-close`)} onClick={this.handleClose} />
                            : null}
                        {title !== null ?
                            <div className={classNames(`${prefixCls}-title`)}>{title}</div>
                            : null}
                        {this.renderContent(classNames)}
                        {this.renderFooter(classNames)}
                    </Dragger>
                </PreventScrollPenetrateContainer>
            </ContainerContext.Provider>,
            containerDom!
        );
    }
}

Modal.defaultProps = {
    prefixCls: 'dui-modal',
    title: '',
    content: '',
    footer: '',
    onCancel: emptyFn,
    onOk: emptyFn,
    cancelText: '取消',
    okText: '确认',
    visible: false,
    mask: true,
    maskClosable: false,
    closable: true,
    onDragEnd: emptyFn,
    destroyOnClose: false,
    onClickMask: emptyFn,
    action: '',
    onClickAction: emptyFn,
    containerDom: typeof document !== 'undefined' ? document.body : undefined,
    okDisabled: false,
};

Modal.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    title: PropTypes.node,
    content: PropTypes.node,
    footer: PropTypes.node,
    onCancel: PropTypes.func,
    onOk: PropTypes.func,
    cancelText: PropTypes.node,
    okText: PropTypes.node,
    visible: PropTypes.bool,
    mask: PropTypes.bool,
    maskClosable: PropTypes.bool,
    closable: PropTypes.bool,
    draggable: PropTypes.bool,
    onDragEnd: PropTypes.func,
    destroyOnClose: PropTypes.bool,
    maskStyle: PropTypes.object,
    maskClassName: PropTypes.string,
    onClickMask: PropTypes.func,
    action: PropTypes.any,
    onClickAction: PropTypes.func,
    onClose: PropTypes.func,
    zIndex: PropTypes.number,
    containerDom: PropTypes.object,
    okDisabled: PropTypes.bool,
};
