import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IOptionProps, IOptionState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import Menu from '../Menu';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Option extends React.Component<IOptionProps, IOptionState> {
    static defaultProps: TDefaultProps<IOptionProps>;
    static propTypes: TPropTypes<IOptionProps>;

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, value, disabled
        } = this.props;

        const cls = classNames(prefixCls, className);
        return (
            <Menu.Item className={cls} style={style} id={value} disabled={disabled}>
                {children}
            </Menu.Item>
        );
    }
}

Option.defaultProps = {
    prefixCls: 'dui-option',
    disabled: false,
};

Option.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    value: PropTypes.string,
    disabled: PropTypes.bool,
    label: PropTypes.string,
};
