import * as React from 'react';
import * as PropTypes from 'prop-types';
import { ICardContentProps, ICardContentState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class CardContent extends React.Component<ICardContentProps, ICardContentState> {
    static defaultProps: TDefaultProps<ICardContentProps>;
    static propTypes: TPropTypes<ICardContentProps>;

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, title, titleStyle, descriptionClassName,
            icon, iconStyle, description, descriptionStyle, iconClassName, titleClassName
        } = this.props;

        const cls = classNames(prefixCls, className);

        return (
            <div className={cls} style={style}>
                {icon ?
                    typeof icon === 'string' ?
                        <span className={classNames(`${prefixCls}-icon`, iconClassName)} style={{ ...iconStyle, backgroundImage: `url(${icon})` }} />
                        : icon
                    : null}
                <div className={classNames(`${prefixCls}-detail`)}>
                    <div className={classNames(`${prefixCls}-title`, titleClassName)} style={titleStyle}>{title}</div>
                    {description ?
                        <div className={classNames(`${prefixCls}-desc`, descriptionClassName)} style={descriptionStyle}>{description}</div>
                        : null}
                </div>
            </div>
        );
    }
}

CardContent.defaultProps = {
    prefixCls: 'dui-cardcontent'
};

CardContent.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    title: PropTypes.string,
    titleStyle: PropTypes.object,
    icon: PropTypes.node,
    iconStyle: PropTypes.object,
    description: PropTypes.string,
    descriptionStyle: PropTypes.object,
    className: PropTypes.string,
    descriptionClassName: PropTypes.string,
    iconClassName: PropTypes.string,
    titleClassName: PropTypes.string
};
