import * as React from 'react';
import * as PropTypes from 'prop-types';
import { INotetipProps, INotetipState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { emptyFn } from '../../utils/helper';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Notetip extends React.Component<INotetipProps, INotetipState> {
    static defaultProps: TDefaultProps<INotetipProps>;
    static propTypes: TPropTypes<INotetipProps>;

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, action, onClickAction
        } = this.props;

        const type = 'info';    // 暂不放开其他类型

        const cls = classNames(prefixCls, className, `${prefixCls}-${type}`);
        const iconCls = classNames(`${prefixCls}-icon`, `${prefixCls}-icon-${type}`);
        const messageCls = classNames(`${prefixCls}-message`);
        const actionCls = classNames(`${prefixCls}-action`);

        return (
            <div className={cls} style={style}>
                <div className={iconCls} />
                <div className={messageCls}>{children}</div>
                {action ?
                    <div className={actionCls} onClick={onClickAction}>{action}</div>
                    : null
                }
            </div>
        );
    }
}

Notetip.defaultProps = {
    prefixCls: 'dui-notetip',
    onClickAction: emptyFn,
    // type: 'info'
};

Notetip.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    action: PropTypes.node,
    onClickAction: PropTypes.func,
    // type: PropTypes.oneOf(['info', 'success', 'error'])
};
