import * as React from 'react';
import * as PropTypes from 'prop-types';
import { ISelectProps, ISelectState, ITag } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import Option from './Option';
import Dropdown from '../Dropdown';
import Menu from '../Menu';
import { emptyFn } from '../../utils/helper';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

// https://github.com/gaearon/react-hot-loader/issues/304
const OptionElementType = (<Option />).type;

export default class Select extends React.Component<ISelectProps, ISelectState> {
    static defaultProps: TDefaultProps<ISelectProps>;
    static propTypes: TPropTypes<ISelectProps>;
    static Option: typeof Option;

    searchInputRef = React.createRef<HTMLInputElement>();
    dropdownRef = React.createRef<Dropdown>();

    // 判断是否点击在 select 上，详情见 handleSelectClick
    isClickSelect = false;

    state: ISelectState = {
        currentValue: this.getInitValue(),
        visible: false,
        searchValue: '',
        isSearchFocus: false,
    }

    static getDerivedStateFromProps(props: ISelectProps, state: ISelectState): Partial<ISelectState> | null {
        if (props.disabled) {
            return { visible: false };  // 避免重新enable后自动弹出
        }
        return null;
    }

    getInitValue() {
        const { isMultiple, defaultValue, value } = this.props;
        // 受控情况下 初始化时同步 外部 value
        if (value) {
            return value;
        }

        // 非受控情况下 初始化时使用外部 defaultValue
        if (defaultValue) {
            return defaultValue;
        }

        // 非受控且没有 defaultValue
        if (isMultiple) {
            return [];
        }

        return '';
    }

    setSearchValue = (value: string) => {
        const { onSearch } = this.props;
        this.setState({ searchValue: value }, () => {
            if (onSearch) {
                onSearch(value);
            }
            this.updateDropContentMetric();
        });
    }

    setCurrentValue = (value: string | string[], cb?: () => void) => {
        const { onChange } = this.props;

        this.setState({
            currentValue: value,
        }, () => {
            onChange!(value);
            cb && cb();
        });
    }

    /**
     * 手动强制 dropContent 更新渲染
     * 1. 搜索时过滤后，更新大小
     * 2. 多选时 select 高度变化，更新位置
     * 3. 多选时 删除所选项，保留 show 的状态
     */
    updateDropContentMetric = () => {
        // setTimeout 是为了等 select 先更新完位置再更新 dropdown 位置
        setTimeout(() => {
            this.dropdownRef && this.dropdownRef.current && this.dropdownRef.current.refreshDropContentMetric();
        }, 0);
    }

    isVisible() {
        const { visible: forceVisible, disabled } = this.props;
        if (disabled) {
            return false;
        }
        const { visible: stateVisible } = this.state;
        return typeof forceVisible === 'boolean' ? forceVisible : stateVisible;
    }

    /**
     * 点击整个 Select
     * 在 showSearch 的情况下 需要手动 focus
     * 在 isMultiple 或 showSearch 的情况下 需要添加标记 让 dropdown 维持展现
     */
    handleSelectClick = (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
        const { showSearch, isMultiple } = this.props;

        if (!showSearch && !isMultiple) {
            return;
        }

        // 添加标记 告知 handleVisibleChange 此时点击在 select 上
        // 即 点击在搜索栏 或 多选的标签上
        this.isClickSelect = true;

        this.focusSearchInput();
    }

    // menu-item 的 click
    handleClick = (value: string) => {
        const { disabled, isMultiple } = this.props;
        if (disabled) {
            return;
        }

        if (!isMultiple) {
            this.setCurrentValue(value, () => {
                const inputDom = this.getSearchInput();
                inputDom && inputDom.blur();
            });

            return;
        }

        this.handleMultipleClick(value);
    }

    handleMultipleClick = (value: string) => {
        // 多选的情况
        const currentValue = this.state.currentValue as string[];
        const idx = currentValue.indexOf(value);
        if (-1 !== idx) {
            // 已选该项, 则删除
            currentValue.splice(idx, 1);
        } else {
            currentValue.push(value);
        }

        this.setCurrentValue(currentValue, () => {
            this.setSearchValue('');
        });
    }

    handleVisibleChange = (visible: boolean) => {
        let newVisible = visible;

        if (this.isClickSelect) {
            this.isClickSelect = false;
            newVisible = true;
        }

        this.setState({ visible: newVisible });
        this.props.onVisibleChange!(newVisible);
    }

    /**
     * 1. 开启输入框，输入框没有值的情况
     * 2. 多选且有值的情况
     * 按删除键可以删除已选内容
     * @param event keydown 事件
     */
    handleSearchKeydown = (event: React.KeyboardEvent<HTMLInputElement>) => {
        const { isMultiple, showSearch } = this.props;
        const { currentValue, searchValue } = this.state;

        if (!showSearch || searchValue) {
            return;
        }

        if (!isMultiple || !(currentValue as string[]).length) {
            return;
        }

        if (event.key !== 'Backspace') {
            return;
        }

        const values = [...(currentValue as string[])];
        values.pop();
        this.setCurrentValue(values, () => {
            this.updateDropContentMetric();
        });
    }

    handleSearchChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const value = event.target.value;
        this.setSearchValue(value);
    }

    handleMousedown = (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
        // 为了不触发 input 的 blur 事件
        // blur 中现在进行了 setState 会造成 menu 触发不了 click 的问题
        // https://stackoverflow.com/questions/35784671/why-does-a-react-components-onclick-event-handler-not-fire-when-the-display-a
        event.preventDefault();
    }

    handleSearchFocus = () => {
        this.setState({
            isSearchFocus: true,
        });
    }

    handleSearchBlur = () => {
        this.setState({
            isSearchFocus: false,
        });
        this.setSearchValue('');
    }

    /**
     * 主动 focus searchInput
     * 2. 点击 .dui-select 时
     */
    focusSearchInput = () => {
        const { isSearchFocus } = this.state;

        const inputDom = this.getSearchInput();
        if (isSearchFocus || !inputDom) {
            return;
        }
        inputDom.focus();
    }

    getSearchInput = () => {
        const { showSearch } = this.props;
        if (!showSearch || !this.searchInputRef) {
            return null;
        }
        return this.searchInputRef.current;
    }

    /**
     * 通过 value 来映射出相应的 text, value 对象
     * 借鉴 antd 命名为 tag
     * @param value string[]
     */
    getTags = (values: string[]) => {
        const { children } = this.props;

        if (!values.length) {
            return [];
        }

        const childArray = React.Children.toArray(children).filter(child => {
            return React.isValidElement(child) && child.type === OptionElementType;
        });

        // 外层循环 values 是因为需要保证 values 的顺序不变
        return values.reduce((tags: ITag[], value: string) => {
            for (const child of childArray) {
                if (!child) {
                    continue;
                }

                const childProps = (child as React.ReactElement).props as ITag;
                if (value === childProps.value) {
                    tags.push(childProps);
                    return tags;
                }
            }
            return tags;
        }, []);
    }

    // 默认的搜索栏过滤器
    searchFilter = (children: React.ReactNode) => {
        const { searchValue } = this.state;
        const { filterOption } = this.props;

        if (!searchValue) {
            return React.Children.toArray(children);
        }

        const filteredOptions =  React.Children.toArray(children).filter(child => {
            if (!React.isValidElement(child) || !child.type === OptionElementType) {
                return false;
            }

            // <Option> 的 props
            const childProps = child.props;

            if (filterOption) {
                return filterOption(searchValue, childProps);
            }

            return childProps.value.indexOf(searchValue) !== -1;
        });

        return filteredOptions;
    }

    /**
     * 删除已选项
     * @param value 要删除的项的 value
     */
    handleDelete = (value: string) => {
        const { currentValue } = this.state;
        const { isMultiple } = this.props;

        if (!isMultiple) {
            this.setCurrentValue('');
            return;
        }

        const values = [...(currentValue as string[])];
        const index = values.findIndex(val => val === value);
        if (index === -1) {
            return;
        }
        values.splice(index, 1);

        this.setCurrentValue(values, () => {
            this.updateDropContentMetric();
        });
    }

    searchInputRender = (classNames: TThemedClassNames) => {
        const { prefixCls, isMultiple, inputClassName, inputStyle } = this.props;
        const { searchValue, isSearchFocus } = this.state;
        const inputContainerCls = classNames(`${prefixCls}-input-container`, {
            [`${prefixCls}-input-container-multiple`]: isMultiple,
        });
        const inputCls = classNames(`${prefixCls}-input`, inputClassName, {
            [`${prefixCls}-input-focus`]: isSearchFocus,
            [`${prefixCls}-input-multiple`]: isMultiple,
        });

        return (
            <div className={inputContainerCls}>
                <input
                    style={inputStyle}
                    ref={this.searchInputRef}
                    className={inputCls}
                    value={searchValue}
                    onFocus={this.handleSearchFocus}
                    onChange={this.handleSearchChange}
                    onBlur={this.handleSearchBlur}
                    onKeyDown={this.handleSearchKeydown}
                />
                {/* { input-content 是为了让 input 根据输入内容自适应宽度，多选时需要 } */}
                {isMultiple ? <div className={`${prefixCls}-input-content-multiple`}>{searchValue}</div> : null}
            </div>
        );
    }

    defaultTagRender = (tag: ITag, handleDelete: (val: string) => void, classNames: TThemedClassNames) => {
        const { isMultiple, prefixCls, textClassName, textStyle, optionLabelProp } = this.props;
        const textCls = classNames(`${prefixCls}-text`, textClassName, {
            [`${prefixCls}-text-multiple`]: isMultiple
        });
        const text = tag[optionLabelProp!];
        if (typeof text === 'undefined') {
            console.error(`Option's ${optionLabelProp} is undefined`);
        }

        if (!isMultiple) {
            return <span className={textCls} style={textStyle} key={tag.value}>{text}</span>;
        }

        return <span className={textCls} style={textStyle} key={tag.value}>
            <span>{text}</span>
            <div className={`${prefixCls}-delete`} onClick={handleDelete.bind(this, tag.value)} />
        </span>;
    }

    MenuContentRender = (options: React.ReactNode[], classNames: TThemedClassNames) => {
        const { renderEmptyContent, prefixCls } = this.props;
        const { searchValue } = this.state;
        const emptyCls = classNames(`${prefixCls}-empty-content`);

        if (options.length === 0) {
            return renderEmptyContent ? renderEmptyContent(searchValue) : (<div className={emptyCls}>暂无选项</div>);
        }
        return options;
    }

    MenuRender = (values: string[], classNames: TThemedClassNames) => {
        const {
            isMultiple, children, prefixCls,
            listClassName, listStyle, renderDropdown: customDropdownRender,
        } = this.props;
        const { searchValue } = this.state;
        const options = this.searchFilter(children);
        const content = customDropdownRender ? customDropdownRender(options, searchValue) : this.MenuContentRender(options, classNames);
        const menuCls = classNames(`${prefixCls}-list`, listClassName);

        return (
            <div onMouseDown={this.handleMousedown}>
                <Dropdown.Keep disabled={!isMultiple}>
                    <Menu
                        className={menuCls}
                        style={listStyle}
                        onClick={this.handleClick}
                        selectedIds={values}
                    >
                        {content}
                    </Menu>
                </Dropdown.Keep>
            </div>
        );
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, value: forceValue,
            disabled, dropdownAlignment, dropdownMatchSelectWidth,
            showArrow, arrowStyle, textStyle, animated,
            arrowClassName, textClassName,
            zIndex, destroyOnClose, isMultiple, placeholder, renderTag: customizedTagRender, showSearch,
            textContainerStyle, textContainerClassName, placement
        } = this.props;
        const { searchValue, isSearchFocus } = this.state;

        const cls = classNames(prefixCls, className, {
            [`${prefixCls}-disabled`]: disabled,
            [`${prefixCls}-multiple`]: isMultiple,
            [`${prefixCls}-focus`]: isSearchFocus,
            [`${prefixCls}-search`]: showSearch,
        });

        const value = forceValue !== undefined ? forceValue : this.state.currentValue;
        const values = Array.isArray(value) ? value : [value];
        const tags = this.getTags(values);
        const tagRender = customizedTagRender ? customizedTagRender : this.defaultTagRender;

        const visible = this.isVisible();
        const arrowCls = classNames(`${prefixCls}-arrow`, arrowClassName, {
            [`${prefixCls}-arrow-visible`]: visible,
            [`${prefixCls}-arrow-no-animation`]: !animated,
        });

        const placeholderCls = classNames(`${prefixCls}-text`, `${prefixCls}-placeholder`, textClassName);
        const textContainerCls = classNames(`${prefixCls}-text-container`, textContainerClassName, {
            [`${prefixCls}-text-container-multiple`]: isMultiple
        });

        return (
            <Dropdown
                dropContent={this.MenuRender(values, classNames)}
                alignment={dropdownAlignment}
                placement={placement}
                matchWidth={dropdownMatchSelectWidth}
                disabled={disabled}
                visible={visible}
                onVisibleChange={this.handleVisibleChange}
                zIndex={zIndex}
                destroyOnClose={destroyOnClose}
                ref={this.dropdownRef}
                animated={animated}
            >
                <div className={cls} style={style} onClick={this.handleSelectClick}>
                    <div className={textContainerCls} style={textContainerStyle}>
                        {tags.map((tag) => tagRender(tag, this.handleDelete, classNames))}
                        {showSearch ? this.searchInputRender(classNames) : null}
                        {!searchValue && tags.length === 0 ? <span className={placeholderCls} style={textStyle}>{placeholder}</span> : null}
                    </div>
                    {showArrow ? <span className={arrowCls} style={arrowStyle} /> : null}
                </div>
            </Dropdown>
        );
    }
}

Select.defaultProps = {
    prefixCls: 'dui-select',
    onChange: emptyFn,
    showArrow: true,
    dropdownMatchSelectWidth: true,
    disabled: false,
    dropdownAlignment: 'left',
    placeholder: '请选择',
    onVisibleChange: emptyFn,
    destroyOnClose: true,
    isMultiple: false,
    showSearch: false,
    optionLabelProp: 'children',
    placement: 'bottom',
    animated: true,
};

Select.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    onChange: PropTypes.func,
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)]),
    defaultValue: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)]),
    disabled: PropTypes.bool,
    placement: PropTypes.string,
    dropdownAlignment: PropTypes.string,
    dropdownMatchSelectWidth: PropTypes.bool,
    placeholder: PropTypes.string,
    showArrow: PropTypes.bool,
    listStyle: PropTypes.object,
    listClassName: PropTypes.string,
    arrowStyle: PropTypes.object,
    arrowClassName: PropTypes.string,
    textStyle: PropTypes.object,
    textClassName: PropTypes.string,
    textContainerStyle: PropTypes.object,
    textContainerClassName: PropTypes.string,
    onVisibleChange: PropTypes.func,
    visible: PropTypes.bool,
    zIndex: PropTypes.number,
    destroyOnClose: PropTypes.bool,
    isMultiple: PropTypes.bool,
    renderDropdown: PropTypes.func,
    renderTag: PropTypes.func,
    showSearch: PropTypes.bool,
    onSearch: PropTypes.func,
    filterOption: PropTypes.func,
    renderEmptyContent: PropTypes.func,
    inputStyle: PropTypes.object,
    inputClassName: PropTypes.string,
    optionLabelProp: PropTypes.string,
    animated: PropTypes.bool,
};
