import * as React from 'react';

type ReachBorder = 'lower' | 'upper' | null;

/**
 * 修复自身内部可滚动的fixed容器的滚动穿透问题
 * @param container 滚动发生的容器
 * @param direction 滚动方向
 */
export function preventSelfScrollPenetrate(
    container: HTMLElement & { __preventSelfScrollPenetrate?: boolean },
    direction: 'x' | 'y' = 'y',
    stopPropagationBorder: ReachBorder = null
) {
    if (!container || container.__preventSelfScrollPenetrate) {
        return;
    }

    let prevX: number;
    let prevY: number;
    let reachBorderInScroll: ReachBorder = null;

    const handleTouchStart = (event: TouchEvent) => {
        reachBorderInScroll = null;
        const touch = event.touches[0];
        prevX = touch.screenX;
        prevY = touch.screenY;
    };

    const handleTouchMove = (event: TouchEvent) => {
        const touch = event.touches[0];
        const deltaX = touch.screenX - prevX;
        const deltaY = touch.screenY - prevY;
        const isAtLowerX = direction === 'x' && container.scrollLeft === 0;
        const isAtUpperX = direction === 'x' && container.scrollLeft === container.scrollWidth - container.offsetWidth;
        const isAtLowerY = direction === 'y' && container.scrollTop === 0;
        const isAtUpperY = direction === 'y' && container.scrollTop === container.scrollHeight - container.offsetHeight;

        // 到达边界，继续向边界外滚动
        const isScrollingBeyondBorder = direction === 'x' ?
            (deltaX > 0 && isAtLowerX || deltaX < 0 && isAtUpperX)
            : (deltaY > 0 && isAtLowerY || deltaY < 0 && isAtUpperY);

        const currentBorder: ReachBorder = (isAtLowerX || isAtLowerY) ? 'lower' : (isAtUpperX || isAtUpperY) ? 'upper' : null;

        if (isScrollingBeyondBorder) {
            event.cancelable && event.preventDefault();
            // 在滚动到边界的瞬间，需要重新触发一个touchStart事件
            // 避免因为alloytouch只记录了一开始的touchStart错过了中间的touchMove，而导致计算出来的移动距离出现跳变
            if (reachBorderInScroll !== currentBorder) {
                // @ts-ignore
                container.dispatchEvent(new TouchEvent('touchstart', event));
                reachBorderInScroll = currentBorder;
            }
            stopPropagationBorder === currentBorder && event.stopPropagation();
        } else {
            event.stopPropagation();    // 未滚动到边界时，不冒泡，pull-panel有用到，以此实现一次滚动内先滚动内层容器，再滚动外层容器
        }
    };

    container.addEventListener('touchstart', handleTouchStart);
    container.addEventListener('touchmove', handleTouchMove, { passive: false });
    container.__preventSelfScrollPenetrate = true;
}

/**
 * 阻止滚动穿透的容器，但不会影响内部可滚动元素的滚动
 * 容器本身应该不可滚动（常见于蒙层或其父元素）
 */
export class PreventScrollPenetrateContainer extends React.Component<React.HTMLProps<HTMLDivElement>> {
    domRef = React.createRef<HTMLDivElement>();
    isInnerScroll = false;

    handleTouchStart = (event: TouchEvent) => {
        const container = this.domRef.current!;
        const path = event.composedPath() as HTMLElement[];     // 此方法IE不支持，但是IE上不会触发touch事件

        for (const elem of path) {
            if (!container.contains(elem)) {
                this.isInnerScroll = false;
                return;
            }
            // 暂时只考虑纵向滚动
            if (elem.scrollHeight > elem.clientHeight) {
                this.isInnerScroll = true;
                preventSelfScrollPenetrate(elem);
                return;
            }
        }
        this.isInnerScroll = false;
    }

    handleTouchMove = (event: TouchEvent) => {
        if (this.isInnerScroll) {
            return;
        }
        event.cancelable && event.preventDefault();
    }

    componentDidMount() {
        const container = this.domRef.current!;
        container.addEventListener('touchstart', this.handleTouchStart);
        container.addEventListener('touchmove', this.handleTouchMove, { passive: false });
    }

    render() {
        return <div {...this.props} ref={this.domRef}>{this.props.children}</div>;
    }
}