import memoizeOne from 'memoize-one';
import { ICell } from './interface';

// [['00'~'23'],['00'~'59'],['00'~'59']]
const defaultTimeColumns: ICell[][] = [24, 60, 60].map(length => Array(length).fill(0).map((v, index) => ({ value: String(index).padStart(2, '0') })));

export const MAX_HOUR = 23;
export const MAX_MINUTE = 59;
export const MAX_SECOND = 59;

function createTimeColumnsImpl(hourStep: number, minuteStep: number, secondStep: number, containSecond: boolean): ICell[][] {
    const steps = [hourStep, minuteStep, secondStep];
    const result = defaultTimeColumns.map((col, i) => col.filter(cell => Number(cell.value) % steps[i] === 0));
    if (!containSecond) {
        result.pop();
    }
    return result;
}

export const createTimeColumns = memoizeOne(createTimeColumnsImpl);