import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IBadgeProps } from './interface';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Badge extends React.Component<IBadgeProps, {}> {

    static defaultProps: TDefaultProps<IBadgeProps>;
    static propTypes: TPropTypes<IBadgeProps>;

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            count, prefixCls, style, status, showZero, text,
            dot, children, className, overflowCount, offsetX, offsetY
        } = this.props;
        let content = count;

        if (Number(count) > overflowCount!) {
            content = `${overflowCount}+`;
        } else if (!count && showZero) {
            content = '0';
        }

        const cls = classNames(prefixCls, className);
        const statusCls = classNames(`${prefixCls}-status`, {
            [`${prefixCls}-status-${status || 'default'}`]: status
        });
        const offset: React.CSSProperties = { marginRight: `${-offsetX!}px`, marginTop: `${offsetY}px` };

        // 独立，作为状态使用
        if (status) {
            const textCls = classNames(`${prefixCls}-status-text`);
            return (
                <span className={cls} style={style}>
                    <span className={statusCls} />
                    <span className={textCls}>{text}</span>
                </span>
            );
        }

        // 独立，计数
        if (!children) {
            const countCls = classNames(`${prefixCls}-count ${prefixCls}-count-standalone`);
            return (
                <span className={cls}>
                    <sup className={countCls} style={style}>
                        {content}
                    </sup>
                </span>
            );
        }

        // 挂载到右上角，显示为一个点
        if (dot) {
            const dotCls = classNames(`${prefixCls}-dot`);
            return (
                <span className={cls}>
                    {children}
                    <sup className={dotCls} style={{ ...style, ...offset }} />
                </span>
            );
        }

        // 挂载到右上角，显示为计数
        const countCls = classNames(`${prefixCls}-count`);
        return (
            <span className={cls}>
                {children}
                {content ?
                    <sup className={countCls} style={{ ...style, ...offset }}>
                        {content}
                    </sup>
                    : null}
            </span>
        );
    }
}

Badge.defaultProps = {
    prefixCls: 'dui-badge',
    showZero: false,
    text: '',
    dot: false,
    overflowCount: 99,
    offsetX: 0,
    offsetY: 0
};

Badge.propTypes = {
    prefixCls: PropTypes.string,
    className: PropTypes.string,
    style: PropTypes.object,
    count: PropTypes.node,
    showZero: PropTypes.bool,
    status: PropTypes.string,
    text: PropTypes.string,
    dot: PropTypes.bool,
    overflowCount: PropTypes.number,
    offsetX: PropTypes.number,
    offsetY: PropTypes.number
};