import * as React from 'react';
import ReactDOM from 'react-dom';
import * as PropTypes from 'prop-types';
import { IContextMenuProps, IContextMenuState, TContextMenuMetrics } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import Trigger from '../../utils/Trigger';
import { emptyFn } from '../../utils/helper';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';
import { ContextMenuContext } from './context';
import throttle from 'lodash.throttle';

const DEFAULT_METRICS: TContextMenuMetrics = {
    horizontal: 'right',
    vertical: 'bottom',
    height: 0
};

export default class ContextMenu extends React.Component<IContextMenuProps, IContextMenuState> {
    static defaultProps: TDefaultProps<IContextMenuProps>;
    static propTypes: TPropTypes<IContextMenuProps>;

    popupRef = React.createRef<HTMLDivElement>();
    anchorRef = React.createRef<HTMLDivElement>();

    state: IContextMenuState = {
        visible: false,
        menu: null,
        location: {
            left: 0,
            top: 0
        },
        metrics: {
            ...DEFAULT_METRICS
        }
    }

    handleScroll = throttle((e: Event) => {
        if (this.popupRef.current?.contains(e.target as HTMLElement)) {
            // 来自菜单内部的滚动
            return;
        }
        this.hide();
    }, 30)

    static getDerivedStateFromProps(props: IContextMenuProps, state: IContextMenuState): Partial<IContextMenuState> | null {
        if (props.disabled) {
            return { visible: false };  // 避免重新enable后自动弹出
        }
        return null;
    }

    componentDidMount() {
        document.addEventListener('scroll', this.handleScroll, { passive: true, capture: true });
        document.addEventListener('wheel', this.handleScroll);   // 兜底业务方不触发scroll事件的场景
    }

    componentWillUnmount() {
        document.removeEventListener('scroll', this.handleScroll);
        document.removeEventListener('wheel', this.handleScroll);
    }

    isVisible() {
        const { visible: forceVisible, disabled } = this.props;
        if (disabled) {
            return false;
        }
        const { visible: stateVisible } = this.state;
        return typeof forceVisible === 'boolean' ? forceVisible : stateVisible;
    }

    updatePopoutMetrics() {
        const { popupBoundary, popupMargin } = this.props;

        const anchorDom = this.anchorRef.current;
        const popupDom = this.popupRef.current;
        if (!popupDom || !anchorDom) {
            return;
        }

        const { left, top } = this.state.location;
        // 以下切换为以viewport为坐标系
        const targetLeft = left - window.pageXOffset;
        const targetTop = top - window.pageYOffset;

        let maxLeft = 0,
            maxRight = window.innerWidth,
            maxTop = 0,
            maxBottom = window.innerHeight;

        const {
            left: containerLeft,
            right: containerRight,
            top: containerTop,
            bottom: containerBottom
        } = anchorDom.getBoundingClientRect();

        // 即使是以container作为边界也不可以超出viewport
        if (popupBoundary === 'container') {
            maxLeft = Math.max(maxLeft, containerLeft);
            maxRight = Math.min(maxRight, containerRight);
            maxTop = Math.max(maxTop, containerTop);
            maxBottom = Math.min(maxBottom, containerBottom);
        }

        // 叠加margin
        maxLeft += popupMargin!;
        maxRight -= popupMargin!;
        maxTop += popupMargin!;
        maxBottom -= popupMargin!;

        const { width, height } = popupDom.getBoundingClientRect();

        // 计算可以弹出的方向
        const horizontal: TContextMenuMetrics['horizontal'] =
            (targetLeft + width <= maxRight) ?
                'right'
                : (targetLeft <= (maxRight + maxLeft) / 2) ?
                    'right'     // 左边同样放不下，且更窄
                    : 'left';
        const remainingBottom = maxBottom - targetTop,
            remainingTop = targetTop - maxTop;
        let vertical: TContextMenuMetrics['vertical'] = 'bottom';
        let forcedHeight = 0;

        if (height > remainingBottom) {
            if (height > remainingTop) {
                // 上下都放不下，选更宽的那边，同时压缩高度，出现滚动条
                if (remainingBottom >= remainingTop) {
                    vertical = 'bottom';
                    forcedHeight = remainingBottom;
                } else {
                    vertical = 'top';
                    forcedHeight = remainingTop;
                }
            } else {
                // 上边可以放下
                vertical = 'top';
            }
        } else {
            // 下边可以放下
            vertical = 'bottom';
        }

        const result: TContextMenuMetrics = {
            horizontal,
            vertical,
            height: forcedHeight
        };

        this.setState({ metrics: result });
    }

    handleContextMenu = (e: React.MouseEvent<HTMLDivElement>) => {
        e.preventDefault();
        const { menu, disabled, onVisibleChange } = this.props;
        if (disabled) {
            return;
        }
        // 先更新内容和位置
        this.setState({
            menu: typeof menu === 'function' ? menu(e) : menu,
            visible: false,
            location: { left: e.pageX, top: e.pageY },
            metrics: { ...this.state.metrics, height: 0 }  // 需要移除之前强制设定的高度
        }, () => {
            // 再更新方向和高度
            this.updatePopoutMetrics();
            this.setState({ visible: true }, () => {
                onVisibleChange!(true);
            });
        });
    }

    hide = () => {
        // trigger会多次调用此方法
        if (!this.isVisible()) {
            return;
        }
        this.setState({ visible: false }, () => {
            this.props.onVisibleChange!(false);
        });
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, disabled, popupStyle, zIndex, popupClassName
        } = this.props;
        const visible = this.isVisible();

        const cls = classNames(prefixCls, className, {
            [`${prefixCls}-disabled`]: disabled
        });
        const {
            location: { left, top },
            menu,
            metrics: { height, vertical, horizontal }
        } = this.state;
        const popupCls = classNames(
            `${prefixCls}-popup`,
            popupClassName,
            `${prefixCls}-popup-${horizontal}-${vertical}`,
            `${prefixCls}-popup-${visible ? 'visible' : 'hidden'}`
        );

        return (
            <Trigger action={this.hide}>
                <div
                    className={cls}
                    style={style}
                    onContextMenu={this.handleContextMenu}
                    ref={this.anchorRef}
                >
                    {children}
                </div>
                {ReactDOM.createPortal(
                    <Trigger
                        className={popupCls}
                        style={{
                            left: `${left}px`,
                            top: `${top}px`,
                            height: height ? `${height}px` : undefined,
                            zIndex,
                            ...popupStyle
                        }}
                        action={this.hide}
                        domRef={this.popupRef}
                        disableContextMenu={true}
                    >
                        <ContextMenuContext.Provider value={{ visible, onClick: this.hide }}>
                            {menu}
                        </ContextMenuContext.Provider>
                    </Trigger>,
                    document.body
                )}
            </Trigger>
        );
    }
}

ContextMenu.defaultProps = {
    prefixCls: 'dui-menu-contextmenu',
    disabled: false,
    popupBoundary: 'viewport',
    popupMargin: 0,
    onVisibleChange: emptyFn
};

ContextMenu.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    menu: PropTypes.any,
    disabled: PropTypes.bool,
    popupStyle: PropTypes.object,
    popupClassName: PropTypes.string,
    visible: PropTypes.bool,
    popupBoundary: PropTypes.oneOf(['container', 'viewport']),
    popupMargin: PropTypes.number,
    onVisibleChange: PropTypes.func,
    zIndex: PropTypes.number
};
