import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IRadioGroupProps, IRadioGroupState } from './interface';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import { emptyFn } from '../../utils/helper';
import { renderChildren } from '../Checkbox/util';
import Radio from './Radio';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Group extends React.Component<IRadioGroupProps, IRadioGroupState> {
    static defaultProps: TDefaultProps<IRadioGroupProps>;
    static propTypes: TPropTypes<IRadioGroupProps>;

    state: IRadioGroupState = {
        value: this.props.defaultValue
    };

    static getDerivedStateFromProps(props: IRadioGroupProps, state: IRadioGroupState): Partial<IRadioGroupState> | null {
        if ('value' in props) {
            return { value: props.value };
        }
        return null;
    }

    handleSingleChange = (checked: boolean, e: any) => {
        if (this.props.disabled) {
            return;
        }
        this.setState({ value: e.target.value });
        this.props.onChange!(e.target.value, e);
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const { prefixCls, className, style, itemStyle } = this.props;
        const cls = classNames(prefixCls, className);
        return (
            <div className={cls} style={style}>
                {renderChildren(this.props, [this.state.value], this.handleSingleChange, Radio, itemStyle)}
            </div>
        );
    }
}

Group.defaultProps = {
    prefixCls: 'dui-radio-group',
    disabled: false,
    onChange: emptyFn
};

Group.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    options: PropTypes.array,
    disabled: PropTypes.bool,
    defaultValue: PropTypes.any,
    value: PropTypes.any,
    onChange: PropTypes.func,
    itemStyle: PropTypes.object,
    children: PropTypes.node
};
