import Snackbar from './Snackbar';
import { TSnackbarShowConfig } from './interface';

type TVisibleSnackbar = { instance: Snackbar; marginTop: number };

/**
 * 同一个container里的多个snackbar同时出现时
 * 若旧的Snackbar实例id相同或自动消失，则替换旧实例；否则向下堆叠
 */
interface IHost extends HTMLElement {
    __dui_visible_snackbars__?: TVisibleSnackbar[];
}

const SNACKBAR_GAP = 50;

function getVisibleSnackbars(instance: Snackbar): TVisibleSnackbar[] {
    const host: IHost = instance.getContainerDom();
    host.__dui_visible_snackbars__ = host.__dui_visible_snackbars__ || [];
    return host.__dui_visible_snackbars__;
}

function getIndexOf(instance: Snackbar): number {
    return getVisibleSnackbars(instance).findIndex(elem => elem.instance === instance);
}

// 查找可以被新的Snackbar实例替换的旧实例，只包括通过函数形式调用的Snackbar
function findReplaceableSnackbar(newInstance: Snackbar): TVisibleSnackbar | null {
    const visibleSnackbars = getVisibleSnackbars(newInstance);
    const { id } = newInstance.props as TSnackbarShowConfig;
    // 优先替换id相同的
    if (id) {
        const sameIdIndex = visibleSnackbars.findIndex(elem => (elem.instance.props as TSnackbarShowConfig).id === id);
        if (sameIdIndex >= 0) {
            return visibleSnackbars[sameIdIndex];
        }
    }

    // 其次替换自动消失的
    const autoCloseIndex = visibleSnackbars.findIndex(elem => (elem.instance.props as TSnackbarShowConfig).autoClose);
    if (autoCloseIndex >= 0) {
        return visibleSnackbars[autoCloseIndex];
    }

    return null;
}

// 下一个出现的snackbar应该具有的margin
function getNextMargin(newInstance: Snackbar): number {
    const replacedSnackbar = findReplaceableSnackbar(newInstance);
    if (replacedSnackbar) {
        return replacedSnackbar.marginTop;
    }

    const occupiedMargins = getVisibleSnackbars(newInstance).map(elem => elem.marginTop).sort((a, b) => a - b);
    if (occupiedMargins.length === 0) {
        return 0;
    }

    for (let i = 0; i < occupiedMargins.length; i++) {
        const targetMargin = SNACKBAR_GAP * i;
        const occupiedMargin = occupiedMargins[i];
        if (targetMargin !== occupiedMargin) {
            return targetMargin;
        }
    }
    return occupiedMargins[occupiedMargins.length - 1] + SNACKBAR_GAP;
}

export function updateVisibleSnackbars(instance: Snackbar) {
    const { visible } = instance.props;
    const index = getIndexOf(instance);
    const visibleSnackbars = getVisibleSnackbars(instance);
    if (visible && index === -1) {
        const replacedSnackbar = findReplaceableSnackbar(instance);
        replacedSnackbar && replacedSnackbar.instance.handleClose();    // 只对函数形式生成的Snackbar有效
        visibleSnackbars.push({
            instance,
            marginTop: getNextMargin(instance)
        });
    }
    if (!visible && index !== -1) {
        visibleSnackbars.splice(index, 1);
    }
}

export function getMarginTop(instance: Snackbar): number {
    const index = getIndexOf(instance);
    const visibleSnackbars = getVisibleSnackbars(instance);
    return index !== -1 ? visibleSnackbars[index].marginTop : getNextMargin(instance);
}