import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IComboBoxProps, IComboBoxState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { emptyFn, trueFn } from '../../utils/helper';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

/**
 * 仅供内部使用
 */
export default class ComboBox extends React.Component<IComboBoxProps, IComboBoxState> {
    static defaultProps: TDefaultProps<IComboBoxProps>;
    static propTypes: TPropTypes<IComboBoxProps>;

    state: IComboBoxState = {
        currentSelectedValues: this.props.defaultSelectedValues || this.props.columns.map(col => col[0].value)
    }

    selectedItemRefs = this.props.columns.map(() => React.createRef<HTMLLIElement>());

    handleClick = (colNum: number, value: string) => {
        const selectedValues = this.props.selectedValues || this.state.currentSelectedValues;
        const nextValues = selectedValues.slice();
        nextValues[colNum] = value;
        this.setState({ currentSelectedValues: nextValues });
        this.props.onChange!(nextValues);
    }

    scrollToSelected() {
        this.selectedItemRefs.forEach(ref => {
            const dom = ref.current;
            if (dom) {
                dom.parentElement!.scrollTop = dom.offsetTop;
            }
        });
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, columns, selectedValues: forceSelectedValues, shouldEnableValues
        } = this.props;
        const { currentSelectedValues: stateSelectedValues } = this.state;
        const selectedValues = forceSelectedValues || stateSelectedValues;
        const cls = classNames(prefixCls, className);

        const enableFuncs = shouldEnableValues || columns.map(() => trueFn);

        return (
            <div className={cls} style={style}>
                {columns.map((col, colNum) => (
                    <ul key={colNum} className={classNames(`${prefixCls}-column`)}>
                        {col.map(single => {
                            const isSelected = single.value === selectedValues[colNum];
                            const isDisabled = !enableFuncs[colNum](...selectedValues.slice(0, colNum), single.value);
                            const cellCls = classNames(`${prefixCls}-cell`, {
                                [`${prefixCls}-cell-selected`]: isSelected,
                                [`${prefixCls}-cell-disabled`]: isDisabled
                            });
                            return (
                                <li
                                    key={single.value}
                                    className={cellCls}
                                    onClick={isDisabled ? undefined : () => this.handleClick(colNum, single.value)}
                                    ref={isSelected ? this.selectedItemRefs[colNum] : () => null}
                                >
                                    {single.cell || single.value}
                                </li>
                            );
                        })}
                    </ul>
                ))}
            </div>
        );
    }
}

ComboBox.defaultProps = {
    prefixCls: 'dui-combobox',
    onChange: emptyFn
};

ComboBox.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    columns: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.object)),
    selectedValues: PropTypes.arrayOf(PropTypes.string),
    defaultSelectedValues: PropTypes.arrayOf(PropTypes.string),
    onChange: PropTypes.func,
    shouldEnableValues: PropTypes.arrayOf(PropTypes.func)
};
