import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IInputProps, IInputState } from './interface';
import { emptyFn } from '../../utils/helper';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import Password from './Password';
import { FormItemContext } from '../Form/context';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

type TRenderSingleParams = {
    prop: undefined | string | React.ReactElement<any>;
    description?: string;
    ref?: React.Ref<HTMLDivElement>;
    handler?: React.MouseEventHandler;
};

type TIconPaddings = Pick<React.CSSProperties, 'paddingLeft' | 'paddingRight'>;

export default class Input extends React.Component<IInputProps, IInputState> {

    static defaultProps: TDefaultProps<IInputProps>;
    static propTypes: Partial<TPropTypes<IInputProps>>;
    static Password: typeof Password;
    static contextType = FormItemContext;

    context!: React.ContextType<typeof FormItemContext>;

    leftIconRef = React.createRef<HTMLDivElement>();
    rightIconsRef = React.createRef<HTMLDivElement>();

    state: IInputState = {
        value: this.props.defaultValue!,
    }

    componentDidMount() {
        this.forceUpdate();  // 更新了ref，重新计算
    }

    handleChange: React.ChangeEventHandler<HTMLInputElement> = e => {
        if (this.props.disabled) {
            return;
        }
        const value = e.target.value;
        this.props.onChange!(value, e);     // 必须同步调用，否则无法输入中文
        this.setState({ value });
    }

    preventLoseFocus: React.MouseEventHandler = (e) => {
        e.preventDefault();
    }

    handleClear: React.MouseEventHandler = () => {
        this.setState({ value: '' }, () => {
            this.props.onChange!('');
        });
    }

    getIconPaddings(): TIconPaddings {
        const leftIconDom = this.leftIconRef.current;
        const rightIconsDom = this.rightIconsRef.current;
        const result: TIconPaddings = {};
        const gap = 2;
        if (leftIconDom) {
            result.paddingLeft = `${leftIconDom.getBoundingClientRect().width + gap}px`;
        }
        if (rightIconsDom) {
            result.paddingRight = `${rightIconsDom.getBoundingClientRect().width + gap}px`;
        }
        return result;
    }

    renderSingleIcon({ prop, description, handler, ref }: TRenderSingleParams, classNames: TThemedClassNames) {
        const { prefixCls, underline } = this.props;
        const cls = classNames(`${prefixCls}-icon`, {
            [`${prefixCls}-icon-${description}`]: description,
            [`${prefixCls}-icon-clickable`]: handler,
            [`${prefixCls}-icon-underline`]: underline
        });

        return prop ?
            <div className={cls} onMouseDown={this.preventLoseFocus} onClick={handler} ref={ref}>
                {typeof prop === 'string' ?
                    <span className={classNames(`${prefixCls}-icon-img`)} style={{ backgroundImage: `url(${prop})` }} />
                    : prop}
            </div>
            : null;
    }

    renderRightIcons(clearVisible: boolean, classNames: TThemedClassNames) {
        const { prefixCls, showClear, icon, onClickIcon, underline } = this.props;
        const clearCls = classNames(`${prefixCls}-clear`, `${prefixCls}-icon-clickable`, {
            [`${prefixCls}-clear-visible`]: clearVisible,
            [`${prefixCls}-icon`]: !underline,
            [`${prefixCls}-clear-underline`]: underline
        });

        const containerCls = classNames(`${prefixCls}-icon-container`, {
            [`${prefixCls}-icon-container-underline`]: underline
        });

        return (showClear || icon) ?
            <div className={containerCls} ref={this.rightIconsRef}>
                {showClear ?
                    <span
                        className={clearCls}
                        onMouseDown={this.preventLoseFocus}
                        onClick={this.handleClear}
                    />
                    : null
                }
                {this.renderSingleIcon({
                    prop: icon,
                    handler: onClickIcon
                }, classNames)}
            </div>
            : null;
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, disabled, underline,
            icon, leftIcon, onClickIcon, onClickLeftIcon, onChange,
            inputStyle, showClear, value: forcedValue, defaultValue,
            inputRef, inputClassName, ...rest
        } = this.props;     // 故意忽略defaultValue、showClear

        const { value: stateValue } = this.state;
        const { status } = this.context;

        const value = typeof forcedValue === 'string' ? forcedValue : stateValue;

        const cls = classNames(prefixCls, className, {
            [`${prefixCls}-disabled`]: disabled,
        });

        const inputCls = classNames(
            `${prefixCls}-input`,
            inputClassName,
            `${prefixCls}-input-${status}`,
            { [`${prefixCls}-input-underline`]: underline }
        );

        const indicatorCls = classNames(`${prefixCls}-indicator`, `${prefixCls}-indicator-${status}`);
        const clearVisible = !!(value && !disabled);

        return (
            <div className={cls} style={style}>
                {this.renderSingleIcon({
                    prop: leftIcon,
                    description: 'left',
                    handler: onClickLeftIcon,
                    ref: this.leftIconRef
                }, classNames)
                }
                <input
                    className={inputCls}
                    style={{ ...this.getIconPaddings(), ...inputStyle }}
                    onChange={this.handleChange}
                    value={value}
                    disabled={disabled}
                    ref={inputRef}
                    {...rest}
                />
                {underline ?
                    <span className={indicatorCls} />
                    : null
                }
                {this.renderRightIcons(clearVisible, classNames)}
            </div>
        );
    }

}

Input.defaultProps = {
    prefixCls: 'dui-input',
    onChange: emptyFn,
    showClear: false,
    defaultValue: ''
};

Input.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    disabled: PropTypes.bool,
    underline: PropTypes.bool,
    icon: PropTypes.node,
    onClickIcon: PropTypes.func,
    leftIcon: PropTypes.node,
    onClickLeftIcon: PropTypes.func,
    inputStyle: PropTypes.object,
    inputClassName: PropTypes.string,
    showClear: PropTypes.bool,
    value: PropTypes.string,
    defaultValue: PropTypes.string,
};
