import { IToastProps } from './interface';
import Toast, { TOAST_TRANSITION_DURATION } from './Toast';
import { showWithTransition } from '../../utils/show';

export default function (config: Partial<IToastProps>) {
    return showWithTransition({
        Component: Toast,
        transitionDuration: TOAST_TRANSITION_DURATION,
        autoClose: true,
        props: config
    });
}
