import * as React from 'react';
import ReactDOM from 'react-dom';
import * as PropTypes from 'prop-types';
import classNames from 'classnames';
import { IToastProps, IToastState } from './interface';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import show from './show';
import { decideKeepDom } from '../../utils/keepDom';

export const TOAST_TRANSITION_DURATION = 200;

export default class Toast extends React.Component<IToastProps, IToastState> {

    static defaultProps: TDefaultProps<IToastProps>;
    static propTypes: TPropTypes<IToastProps>;
    static show: typeof show;

    componentDidMount() {
        console.warn('PC端Toast组件不符合交互规则，为避免破坏性更新，当前版本仍将提供。未来会移除此组件，请尽量使用Snackbar组件。');
    }

    render() {
        const { prefixCls, className, style, children, content, visible, mask, position } = this.props;
        const cls = classNames(prefixCls, className, `${prefixCls}-${position}`, {
            [`${prefixCls}-hidden`]: !visible
        });
        const maskCls = classNames(`${prefixCls}-mask`, {
            [`${prefixCls}-mask-display`]: mask,
            [`${prefixCls}-mask-hidden`]: !visible
        });

        if (!decideKeepDom.call(this, TOAST_TRANSITION_DURATION, visible)) {
            return null;
        }

        return ReactDOM.createPortal(
            <div className={maskCls}>
                <div className={cls} style={style}>
                    {children || content}
                </div>
            </div>,
            document.body
        );
    }

}

Toast.defaultProps = {
    prefixCls: 'dui-toast',
    visible: false,
    content: '',
    mask: false,
    position: 'top'
};

Toast.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    visible: PropTypes.bool,
    content: PropTypes.node,
    duration: PropTypes.number,
    mask: PropTypes.bool,
    position: PropTypes.string
};
