import * as React from 'react';
import * as PropTypes from 'prop-types';
// eslint-disable-next-line no-unused-vars
import { ITabsProps, ITabsState, ITabPaneProps } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import TabPane from './TabPane';
import { emptyFn } from '../../utils/helper';
import { ActiveTabIdContext } from './context';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

// https://github.com/gaearon/react-hot-loader/issues/304
const TabPaneElementType = (<TabPane />).type;

export default class Tabs extends React.Component<ITabsProps, ITabsState> {
    static defaultProps: TDefaultProps<ITabsProps>;
    static propTypes: TPropTypes<ITabsProps>;

    static TabPane: typeof TabPane;

    state: ITabsState = {
        currentActiveId: this.props.defaultActiveId!
    }
    activeTabItemRef = React.createRef<HTMLLIElement>();
    indicatorRef = React.createRef<HTMLDivElement>();
    containerRef = React.createRef<HTMLDivElement>();

    handleTabBarClick = (id: string) => {
        this.setState({ currentActiveId: id });
        this.props.onChange!(id);
    }

    updateIndicator(skipAnimation = false) {
        const activeItemDom = this.activeTabItemRef.current;
        const indicatorDom = this.indicatorRef.current;
        if (!activeItemDom || !indicatorDom) {
            return;
        }

        const { offsetLeft: targetLeft, offsetWidth: targetWidth } = activeItemDom;

        indicatorDom.style.left = `${targetLeft}px`;
        indicatorDom.style.width = `${targetWidth}px`;

        if (skipAnimation) {
            indicatorDom.style.transition = 'none';
            setTimeout(() => {
                indicatorDom.style.removeProperty('transition');
            }, 0);
        }
    }

    componentDidUpdate() {
        this.updateIndicator();
    }

    componentDidMount() {
        this.updateIndicator(true);
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children, activeId: forceActiveId,
            tabBarStyle, tabBarItemStyle, indicatorStyle, alignment, activeItemStyle,
            activeItemClassName, indicatorClassName, tabBarClassName, tabBarItemClassName
        } = this.props;
        const { currentActiveId: stateActiveId } = this.state;
        const activeId = forceActiveId || stateActiveId;
        const cls = classNames(prefixCls, className);

        const tabBarContent = React.Children.map(children, (child: React.ReactElement<ITabPaneProps> | React.ReactElement) => {
            if (child.type !== TabPaneElementType) {
                return null;
            }
            const { id, tab, icon, activeIcon } = child.props as ITabPaneProps;
            const isActive = id === activeId;
            const tabBarItemCls = classNames(
                `${prefixCls}-bar-item`,
                tabBarItemClassName,
                isActive && activeItemClassName,
                {
                    [`${prefixCls}-bar-item-active`]: isActive,
                }
            );
            const itemStyle = { ...tabBarItemStyle, ...(isActive ? activeItemStyle : null) };
            const effectiveIcon = isActive ? activeIcon || icon : icon;
            return (
                <li
                    className={tabBarItemCls}
                    style={itemStyle}
                    key={id}
                    onClick={() => this.handleTabBarClick(id)}
                    ref={isActive ? this.activeTabItemRef : () => null}
                >
                    {effectiveIcon ?
                        typeof effectiveIcon === 'string' ?
                            <span className={classNames(`${prefixCls}-icon`)} style={{ backgroundImage: `url(${effectiveIcon})` }} />
                            : effectiveIcon
                        : null}
                    {tab}
                </li>
            );
        });

        const tabBarCls = classNames(`${prefixCls}-bar`, tabBarClassName, `${prefixCls}-bar-${alignment}`);
        const contentCls = classNames(`${prefixCls}-content`);
        return (
            <div className={cls} style={style}>
                {/* 这个div是用来兼容IE的 */}
                <div style={{ position: 'relative' }}>
                    <ul className={tabBarCls} style={tabBarStyle}>
                        {tabBarContent}
                    </ul>
                    <div className={classNames(`${prefixCls}-indicator`, indicatorClassName)} style={indicatorStyle} ref={this.indicatorRef} />
                </div>
                <div className={contentCls} ref={this.containerRef}>
                    <ActiveTabIdContext.Provider value={activeId}>
                        {children}
                    </ActiveTabIdContext.Provider>
                </div>
            </div>
        );
    }
}

Tabs.defaultProps = {
    prefixCls: 'dui-tabs',
    // animated: true,
    // tabPostion: 'top',
    onChange: emptyFn,
    defaultActiveId: '',
    alignment: 'center',
};

Tabs.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    activeId: PropTypes.string,
    // animated: PropTypes.bool,
    defaultActiveId: PropTypes.string,
    onChange: PropTypes.func,
    // tabPostion: PropTypes.string,
    tabBarStyle: PropTypes.object,
    tabBarClassName: PropTypes.string,
    tabBarItemStyle: PropTypes.object,
    tabBarItemClassName: PropTypes.string,
    activeItemStyle: PropTypes.object,
    activeItemClassName: PropTypes.string,
    indicatorStyle: PropTypes.object,
    indicatorClassName: PropTypes.string,
    alignment: PropTypes.oneOf(['center', 'left', 'right']),
};
