import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IDraggerProps } from './interface';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import { emptyFn } from '../../utils/helper';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Dragger extends React.Component<IDraggerProps, {}> {

    static defaultProps: TDefaultProps<IDraggerProps>;
    static propTypes: TPropTypes<IDraggerProps>;

    ref: HTMLDivElement | null = null;
    mouse: boolean;
    startLeft: number;
    startTop: number;
    startX: number;
    startY: number;
    maxLeft: number;
    maxTop: number;
    minLeft: number = 0;
    minTop: number = 0;

    componentDidMount() {
        document.body.addEventListener('mousemove', this.onMouseMove);
        document.body.addEventListener('mouseup', this.onMouseUp);
    }

    componentWillUnmount() {
        document.body.removeEventListener('mousemove', this.onMouseMove);
        document.body.removeEventListener('mouseup', this.onMouseUp);
    }

    setRef = (ref: HTMLDivElement) => {
        this.ref = ref;
    };

    isFromInnerInput(e: React.MouseEvent | KeyboardEvent): boolean {
        const target = e.target as HTMLElement;
        return Boolean(
            /input|textarea/i.test(target.tagName)
            && this.ref?.contains(target)
        );
    }

    onMouseDown = (e: React.MouseEvent) => {
        // 忽略来自input的事件，避免无法鼠标按住拖选文字
        if (!this.props.draggable || !this.ref || this.isFromInnerInput(e)) {
            return;
        }
        this.mouse = true;
        const { left, top, width, height } = this.ref.getBoundingClientRect();
        const { clientX, clientY } = e;
        this.startLeft = left;
        this.startTop = top;
        this.startX = clientX;
        this.startY = clientY;

        this.maxLeft = window.innerWidth - width;
        this.maxTop = window.innerHeight - height;

        this.ref.style.userSelect = 'none';
        /**
         * 父元素的 pointer-events 为 none 的情况下，dragger 的鼠标事件在快速拖动时，有可能会触碰到父元素从而丢失了事件监听，导致拖动出现问题
         * 这里显示将父层元素的 pointer-events 设为 auto 可点击
         */
        this.ref.parentElement!.style.pointerEvents = 'auto';
    };

    onMouseMove = (e: MouseEvent) => {
        if (!this.mouse || !this.ref) {
            return;
        }

        const { clientX, clientY } = e;
        let left = this.startLeft + clientX - this.startX;
        let top = this.startTop + clientY - this.startY;
        left = Math.min(Math.max(this.minLeft, left), this.maxLeft);
        top = Math.min(Math.max(this.minTop, top), this.maxTop);

        this.ref.style.position = 'absolute';
        this.ref.style.left = `${left}px`;
        this.ref.style.top = `${top}px`;
    };

    onMouseUp = (e: MouseEvent) => {
        if (!this.ref) {
            return;
        }
        this.mouse = false;
        this.ref.style.userSelect = 'auto';
        this.ref.parentElement!.style.pointerEvents = '';
        const { onDragEnd, draggable } = this.props;
        if (draggable === true) {
            onDragEnd!(e);
        }
    };

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const { children, className, onClick, prefixCls, style } = this.props;
        const cls = classNames(prefixCls, className);
        return (
            <div
                className={cls}
                style={style}
                ref={this.setRef}
                onMouseDown={this.onMouseDown}
                onClick={onClick}
            >
                {children}
            </div>
        );
    }
}

Dragger.defaultProps = {
    prefixCls: 'dui-dragger',
    onDragEnd: emptyFn,
    onClick: emptyFn,
    draggable: false
};

Dragger.propTypes = {
    prefixCls: PropTypes.string,
    className: PropTypes.string,
    style: PropTypes.object,
    draggable: PropTypes.bool,
    onClick: PropTypes.func,
    onDragEnd: PropTypes.func,
};
