import * as React from 'react';
import * as PropTypes from 'prop-types';
import { ISpinProps } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';

import './style/spin.less';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

const VIEW_BOX_SIZE = 48;   // 和css一致

export default class Spin extends React.PureComponent<ISpinProps, {}> {
    static defaultProps: TDefaultProps<ISpinProps>;
    static propTypes: TPropTypes<ISpinProps>;

    rootRef = React.createRef<HTMLDivElement>();

    // 根据用户设置大小调整动画缩放
    calcContainerTransform(): Pick<React.CSSProperties, 'transform'> | undefined {
        const rootDom = this.rootRef.current;
        if (!rootDom) {
            return;
        }

        // 这种方式会受transform影响，若父组件有transform动画，计算出来的缩放就不对
        // const { width, height } = rootDom.getBoundingClientRect();

        const { clientWidth: width, clientHeight: height } = rootDom;
        const actualSize = Math.min(width, height);
        const scale = actualSize / VIEW_BOX_SIZE;
        return { transform: `translate(-50%,-50%) scale(${scale})` };
    }

    componentDidMount() {
        this.forceUpdate();  // 要第二次render ref才有值
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const { prefixCls, className, style, type, circleClassName, circleStyle } = this.props;
        const cls = classNames(prefixCls, className);
        const circleCls = classNames(`${prefixCls}-circle`, `${prefixCls}-circle-${type}`, circleClassName);
        return (
            <div className={cls} style={style} ref={this.rootRef}>
                <div className={classNames(`${prefixCls}-container`)} style={this.calcContainerTransform()}>
                    <div className={circleCls} style={circleStyle} />
                </div>
            </div>
        );
    }
}

Spin.defaultProps = {
    prefixCls: 'dui-spin',
    type: 'dark'
};

Spin.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    type: PropTypes.oneOf(['light', 'dark', 'primary']),
    circleClassName: PropTypes.string,
    circleStyle: PropTypes.object
};
