import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IItemProps, IItemState, } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';
import Meta from './Meta';

export default class Item extends React.Component<IItemProps, IItemState> {
    static defaultProps: TDefaultProps<IItemProps>;
    static propTypes: TPropTypes<IItemProps>;

    static Meta: typeof Meta;
    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, children
        } = this.props;
        const cls = classNames(prefixCls, className);
        return (
            <li
                className={cls}
                style={style}
            >
                {children}
            </li>
        );
    }
}

Item.defaultProps = {
    prefixCls: 'dui-list-item',
    // id: '',
};

Item.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    // id: PropTypes.string,
};
