import * as React from 'react';
import * as PropTypes from 'prop-types';
import { ISwitchProps, ISwitchState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { emptyFn } from '../../utils/helper';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Switch extends React.Component<ISwitchProps, ISwitchState> {
    static defaultProps: TDefaultProps<ISwitchProps>;
    static propTypes: TPropTypes<ISwitchProps>;

    state: ISwitchState = { currentChecked: this.props.defaultChecked as boolean };

    handleClick = () => {
        const { disabled, onChange, loading } = this.props;
        if (disabled || loading) {
            return;
        }
        const next = !this.isChecked();
        onChange!(next);
        this.setState({ currentChecked: next });
    }

    isChecked(): boolean {
        const { checked: forceChecked } = this.props;
        const { currentChecked: stateChecked } = this.state;
        if (typeof forceChecked === 'boolean') {
            return forceChecked;
        }
        return stateChecked;
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, disabled, loading
        } = this.props;

        const cls = classNames(prefixCls, className, {
            [`${prefixCls}-checked`]: this.isChecked(),
            [`${prefixCls}-disabled`]: disabled,
            [`${prefixCls}-loading`]: loading
        });
        return (
            <button className={cls} style={style} onClick={this.handleClick}>
                {/* 以后扩展支持自定义状态文本和图标 */}
            </button>
        );
    }
}

Switch.defaultProps = {
    prefixCls: 'dui-switch',
    defaultChecked: false,
    disabled: false,
    onChange: emptyFn
};

Switch.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    defaultChecked: PropTypes.bool,
    disabled: PropTypes.bool,
    loading: PropTypes.bool,
    onChange: PropTypes.func,
    checked: PropTypes.bool
};
