import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IPasswordProps, IPasswordState } from './interface';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import Input from './Input';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

const openEyeUrl = require('./style/open-eye.svg');
const closedEyeUrl = require('./style/closed-eye.svg');

export default class Password extends React.Component<IPasswordProps, IPasswordState> {
    static defaultProps: TDefaultProps<IPasswordProps>;
    static propTypes: Partial<TPropTypes<IPasswordProps>>;

    state: IPasswordState = {
        passwordVisible: false
    }

    togglePasswordVisibility: React.MouseEventHandler = () => {
        this.setState(prevState => ({ passwordVisible: !prevState.passwordVisible }));
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const { showToggle, ...rest } = this.props;
        const { passwordVisible } = this.state;
        const iconUrl = passwordVisible ? openEyeUrl : closedEyeUrl;

        return (
            <Input
                icon={showToggle ? iconUrl : undefined}
                inputStyle={{ letterSpacing: passwordVisible ? '2px' : '4px' }}
                onClickIcon={this.togglePasswordVisibility}
                type={passwordVisible ? 'text' : 'password'}
                {...rest}
            />
        );
    }

}

Password.defaultProps = {
    showToggle: true
};

Password.propTypes = {
    showToggle: PropTypes.bool
};
