import * as React from 'react';
import { Subtract } from 'utility-types';
import { CreateOption, CreateForm, Values, CreateState, FieldError, WithCreateProps } from './interface';

export default function create(createOption?: CreateOption) {
    return function <Props extends WithCreateProps>(C: React.ComponentType<Props>) {
        return class extends React.Component<Subtract<Props, WithCreateProps>, CreateState> {
            /** 维护所有表单的状态 */
            state: CreateState = {};

            /**
             * 注入到目标组件内的表单对象
             */
            form: CreateForm = {
                getFieldDecorator: (id, option = {}) => {
                    return E => {
                        const onChange = (value: any, ...args: any[]) => {
                            if (typeof E.props.onChange === 'function') {
                                E.props.onChange(value, ...args);
                            }
                            this.setState(state => ({
                                [id]: {
                                    ...state[id],
                                    touched: true,
                                    value: value
                                }
                            }), () => {
                                if (createOption && typeof createOption.onValuesChange === 'function') {
                                    createOption.onValuesChange(this.form.getFieldsValue([id]), this.form.getFieldsValue());
                                }
                            });
                        };
                        if (this.state[id] === undefined && option.initialValue !== undefined) {
                            // eslint-disable-next-line react/no-direct-mutation-state
                            this.state[id] = {
                                ...this.state[id],
                                value: option.initialValue
                            };
                        }
                        // eslint-disable-next-line react/no-direct-mutation-state
                        this.state[id] = {
                            ...this.state[id],
                            option: option
                        };
                        return React.cloneElement(E, {
                            'data-create-form-payload': {
                                id: id,
                                form: this.form,
                                option: option
                            },
                            onChange,
                            value: this.state[id] && this.state[id].value
                        });
                    };
                },
                getFieldValue: (id) => {
                    return this.state[id] && this.state[id].value;
                },
                getFieldsValue: (ids?) => {
                    if (Array.isArray(ids)) {
                        const values: Values = {};
                        ids.forEach(id => {
                            if (this.state[id]) {
                                values[id] = this.state[id].value;
                            }
                        });
                        return values;
                    } else {
                        const values: Values = {};
                        Object.keys(this.state).forEach(id => {
                            if (this.state[id]) {
                                values[id] = this.state[id].value;
                            }
                        });
                        return values;
                    }
                },
                setFieldsValue: (values) => {
                    Object.keys(values).forEach(id => {
                        if (this.state[id]) {
                            this.setState(state => ({
                                [id]: {
                                    ...state[id],
                                    value: values[id]
                                }
                            }));
                        }
                    });
                },
                resetFields: (ids?) => {
                    if (Array.isArray(ids)) {
                        ids.forEach(id => {
                            if (this.state[id]) {
                                this.setState(state => ({
                                    [id]: {
                                        ...state[id],
                                        touched: false,
                                        value: state[id].option.initialValue
                                    }
                                }));
                            }
                        });
                    } else {
                        Object.keys(this.state).forEach(id => {
                            this.setState(state => ({
                                [id]: {
                                    ...state[id],
                                    touched: false,
                                    value: state[id].option.initialValue
                                }
                            }));
                        });
                    }
                },
                isFieldTouched: (id) => {
                    return !!(this.state[id] && this.state[id].touched);
                },
                validateFields: (cb) => {
                    this.setState(state => {
                        const obj: CreateState = {};
                        Object.keys(state).forEach(k => {
                            obj[k] = {
                                ...state[k],
                                touched: true
                            };
                        });
                        return obj;
                    });
                    cb(this.form.getFieldsError(), this.form.getFieldsValue());
                },
                getFieldError: (id) => {
                    return this.validate(id);
                },
                getFieldsError: (ids) => {
                    let obj: any;
                    if (Array.isArray(ids)) {
                        ids.forEach(id => {
                            obj = { ...obj, ...this.validate(id) };
                        });
                    } else {
                        Object.keys(this.state).forEach(id => {
                            obj = { ...obj, ...this.validate(id) };
                        });
                    }
                    return obj;
                }
            };

            /**
             * 检验表单合法性
             */
            validate = (id: string): FieldError | undefined => {
                if (this.state[id]) {
                    const option = this.state[id].option;
                    const value = this.state[id].value;
                    if (Array.isArray(option.rules)) {
                        const errors = option.rules.filter(rule => {
                            if (rule.required === true) {
                                if (value === undefined || value === '') {
                                    return true;
                                }
                            }
                            if (rule.min !== undefined && !isNaN(rule.min)) {
                                if (value === undefined || value === '') {
                                    if (rule.min > 0) {
                                        return true;
                                    }
                                } else {
                                    if (typeof value === 'string') {
                                        if (rule.min > value.length) {
                                            return true;
                                        }
                                    }
                                }
                            }
                            if (rule.max !== undefined && !isNaN(rule.max)) {
                                if (typeof value === 'string') {
                                    if (rule.max < value.length) {
                                        return true;
                                    }
                                }
                            }
                            if (typeof rule.validator === 'function') {
                                return !rule.validator(value);
                            }
                            return false;
                        }).map(rule => {
                            return {
                                field: id,
                                message: rule.message
                            };
                        });
                        if (errors.length) {
                            return {
                                [id]: {
                                    errors: errors
                                }
                            };
                        }
                    }
                }
                return;
            };

            render() {
                return <C {...this.props as Props} form={this.form} />;
            }
        };
    };
}