import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IFormProps, IFormState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { TThemedClassNames, consumeTheme } from '../../utils/theme';
import Item from './Item';
import create from './create';

export default class Form extends React.Component<IFormProps, IFormState> {
    static defaultProps: TDefaultProps<IFormProps>;
    static propTypes: TPropTypes<IFormProps>;
    static Item: typeof Item;
    static create: typeof create; // 类型细节补充

    render() {
        return consumeTheme(this.themeRender);
    }

    themeRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, layout, labelStyle
        } = this.props;
        const cls = classNames(
            prefixCls, className,
            `${prefixCls}-layout-${layout}`
        );
        return (
            <div className={cls} style={style}>
                {React.Children.map(this.props.children, (child: React.ReactElement) =>
                    React.cloneElement(child, {
                        labelStyle: child.props.labelStyle || labelStyle
                    })
                )}
            </div>
        );
    };
}

Form.defaultProps = {
    prefixCls: 'dui-form',
    layout: 'horizontal'
};

Form.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    form: PropTypes.object,
    labelStyle: PropTypes.object,
    layout: PropTypes.oneOf(['horizontal', 'vertical']),
};
