import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IItemProps, IItemState, TStatus } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import { FormItemContext } from './context';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

function getTextOfStatus(
    message: IItemProps['message'],
    status: TStatus
): string | React.ReactElement | undefined {
    if (React.isValidElement(message) || typeof message === 'string') {
        return status === 'error' ? message : undefined;
    }
    if (typeof message === 'object') {
        return message[status];
    }
}

export default class Item extends React.Component<IItemProps, IItemState> {
    static defaultProps: TDefaultProps<IItemProps>;
    static propTypes: TPropTypes<IItemProps>;

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        let {
            prefixCls, className, style,
            status, message, label, labelStyle,
            required
        } = this.props;
        const cls = classNames(prefixCls, className);
        let text = getTextOfStatus(message, status!);
        let textCls = classNames(`${prefixCls}-text`, `${prefixCls}-text-${status}`);
        const children = Array.isArray(this.props.children) ? this.props.children[0] : this.props.children;
        if (React.isValidElement(children)) {
            const props: any = children.props;
            if (props['data-create-form-payload']) {
                try {
                    const { id, form, option } = props['data-create-form-payload'];
                    const { getFieldError, isFieldTouched } = form;
                    if (Array.isArray(option.rules) &&
                        option.rules.filter((rule: any) => rule.required).length) {
                        required = true;
                    }
                    const accountError = isFieldTouched(id) && getFieldError(id);
                    if (accountError) {
                        status = 'error';
                        text = accountError[id].errors[0].message;
                        textCls = classNames(`${prefixCls}-text`, `${prefixCls}-text-${status}`);
                    }
                } catch (e) {
                    console.error('Item 组件内部错误', e);
                }
            }
        }
        return (
            <FormItemContext.Provider value={{ status }}>
                <div className={cls} style={style}>
                    {label ?
                        <div className={classNames(`${prefixCls}-label`)}
                            style={labelStyle}>
                            {required ? <span className={classNames(`${prefixCls}-label-required`)}>*</span> : null}
                            {label}</div> : null}
                    <div className={classNames(`${prefixCls}-content`)}>
                        {children}
                        {text ? <div className={textCls}>{text}</div> : null}
                    </div>
                </div>
            </FormItemContext.Provider>
        );
    }
}

Item.defaultProps = {
    prefixCls: 'dui-form-item',
    status: 'default',
    required: false
};

Item.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    status: PropTypes.oneOf(['default', 'success', 'error', 'warning']),
    message: PropTypes.oneOfType([PropTypes.string, PropTypes.object, PropTypes.element]),
    label: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    labelStyle: PropTypes.object,
    required: PropTypes.bool
};
