import version from '../common/version';

// 判断是否是浏览器环境
const isBrowser = (typeof window !== 'undefined' && typeof document !== 'undefined');

// eslint-disable-next-line
declare global {
    interface Window {
        // { key: bundledBy[] }
        __dui_style_registry__: Record<string, string[]>;
    }

    // Node 环境下提供插入 Style 的全局方法
    const injectContentBeforeRoot: (content: string) => void | undefined;
}

let bundledBy = 'unknown';
try {
    const where: string = require('../../package.json')._where;
    const arr = where.split('/');
    bundledBy = arr[arr.length - 1];
} catch (error) {
    // 忽略
}

/**
 * 管理 Style 的类，为了隐藏 Node 环境和浏览器环境的差异
 */
class StyleRegistryManage {
    nodeRegistry: Record<string, string[]> = {};

    constructor() {
        if (isBrowser && !window.__dui_style_registry__) {
            window.__dui_style_registry__ = {};
        }
    }

    public get registry() {
        if (isBrowser) {
            return window.__dui_style_registry__;
        } else {
            return this.nodeRegistry;
        }
    }

    public get length() {
        return Object.keys(this.registry).length;
    }

    public set(key: string, bundledsBy: string[]) {
        this.registry[key] = bundledsBy;
    }

    public get(key: string) {
        return this.registry[key];
    }

    public add(key: string, bundledBy: string) {
        if (!this.registry[key]) {
            this.registry[key] = [];
        }
        this.registry[key].push(bundledBy);
    }
}

const styleRegistryManage = new StyleRegistryManage();

/**
 * 向当前文档注入css样式
 * 仅供构建后文件使用
 * @param fileKey css样式对应的文件或组件，避免同一版本的dui样式重复注入，造成覆盖问题，导致样式与官网不一致
 * @param css
 */
export function injectStyle(fileKey: string, css: string) {
    const key = `${version}-${fileKey}`;

    if (Array.isArray(styleRegistryManage.get(key))) {
        // 同一版本重复引入
        styleRegistryManage.add(key, bundledBy);
        warnRedundantInject(key);
        return;
    }

    styleRegistryManage.set(key, [bundledBy]);

    // 不同版本出现覆盖
    const versions = getFileVersions(fileKey);
    if (versions.length > 1) {
        warnMultipleVersions(fileKey, versions);
    }

    if (isBrowser) {
        const styleElement = document.createElement('style');
        styleElement.setAttribute('type', 'text/css');
        styleElement.setAttribute('data-dui-key', key);

        styleElement.innerText = css;
        document.head.appendChild(styleElement);
    } else if (typeof injectContentBeforeRoot === 'function') {
        const styleElement = `<style type="text/css" data-dui-key="${key}">${css}</style>`;
        injectContentBeforeRoot(styleElement);
    }
}


const CONSOLE_DEBOUNCE = 3000;

/**
 * 警告针对同一个文件同一版本的重复引入
 * @param key registry里的key
 */
function warnRedundantInject(key: string) {
    if (!isBrowser || !styleRegistryManage.length || !Array.isArray(styleRegistryManage.get(key))) {
        return;
    }

    const count = styleRegistryManage.get(key).length;
    setTimeout(() => {
        if (styleRegistryManage.get(key).length > count) {
            // 在短时间内有相关的新警告，放弃这次警告
            return;
        }
        console.warn(`[DUI] ${key} 重复引入了 ${count} 次`);
    }, CONSOLE_DEBOUNCE);
}

/**
 * 警告针对同一个文件不同版本的覆盖
 * @param fileKey 文件名key，为registry key的后半部分
 * @param versions 所有版本
 */
function warnMultipleVersions(fileKey: string, versions: string[]) {
    // 非浏览器环境应该是不用提醒的，即使提醒了 log 在服务端，也很少会看到
    if (!isBrowser) {
        return;
    }
    setTimeout(() => {
        if (getFileVersions(fileKey).length > versions.length) {
            // 在短时间内有相关的新警告，放弃这次警告
            return;
        }
        console.error(`[DUI] ${fileKey} 存在多版本覆盖:  ${versions.join('  ')}`);
    }, CONSOLE_DEBOUNCE);
}

/**
 * 获取指定文件的所有版本
 * @param fileKey 文件名key，为registry key的后半部分
 */
function getFileVersions(fileKey: string): string[] {
    if (!styleRegistryManage.length) {
        return [];
    }

    const existingKeys = Object.getOwnPropertyNames(styleRegistryManage.registry);
    return existingKeys.filter(k => k.indexOf(fileKey) !== -1).map(k => k.split('-')[0]);
}