import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IButtonProps, IButtonState } from './interface';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Button extends React.Component<IButtonProps, IButtonState> {
    static defaultProps: TDefaultProps<IButtonProps>;
    static propTypes: Partial<TPropTypes<IButtonProps>>;

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {

        const { prefixCls, className, style, type, size, disabled, icon, ...rest } = this.props;
        const cls = classNames(prefixCls, className, {
            [`${prefixCls}-type-${type}`]: type,
            [`${prefixCls}-size-${size}`]: size,
            [`${prefixCls}-with-icon`]: icon,
            [`${prefixCls}-type-${type}-disabled`]: disabled,
        });
        const containerCls = classNames(`${prefixCls}-container`);
        const iconCls = classNames(`${prefixCls}-icon`);

        return (
            <button {...rest} className={cls} style={style} disabled={disabled}>
                <div className={containerCls}>
                    {icon ?
                        typeof icon === 'string' ?
                            <span className={iconCls} style={{ backgroundImage: `url(${icon})` }} />
                            : icon
                        : null}
                    {this.props.children}
                </div>
            </button>
        );
    }
}

Button.defaultProps = {
    prefixCls: 'dui-button',
    type: 'default',
    size: 'default'
};

Button.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    type: PropTypes.oneOf(['default', 'primary', 'warning', 'plain', 'golden', 'golden-outline']),
    size: PropTypes.oneOf(['default', 'small', 'large']),
    icon: PropTypes.node
};
