import * as React from 'react';
import * as PropTypes from 'prop-types';
import { IContainerProps } from './interface';
import { TDefaultProps, TPropTypes } from '../../common/interface';
import { ContainerContext } from './context';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

export default class Container extends React.Component<IContainerProps> {
    static defaultProps: TDefaultProps<IContainerProps>;
    static propTypes: TPropTypes<IContainerProps>;

    ref = React.createRef<HTMLDivElement>();

    componentDidMount() {
        this.forceUpdate();  // 更新ref current
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const { prefixCls, className, style, children } = this.props;
        const cls = classNames(prefixCls, className);
        return (
            <div
                className={cls}
                style={{ position: 'relative', ...style }}
                ref={this.ref}
            >
                <ContainerContext.Provider value={this.ref.current}>
                    {children}
                </ContainerContext.Provider>
            </div>
        );
    }
}

Container.defaultProps = {
    prefixCls: 'dui-snackbar-container',
};

Container.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
};
