import * as React from 'react';
import * as PropTypes from 'prop-types';
import dayjs, { Dayjs, UnitType } from 'dayjs';
import { ITimePickerProps, ITimePickerState } from './interface';
import { TPropTypes, TDefaultProps } from '../../common/interface';
import Dropdown from '../Dropdown';
import ComboBox from './ComboBox';
import { createTimeColumns, MAX_HOUR, MAX_MINUTE, MAX_SECOND } from './timeCells';
import { emptyFn, castInto, roundByStep, createStringParamFunc, trueFn } from '../../utils/helper';
import { consumeTheme, TThemedClassNames } from '../../utils/theme';

const DROPDOWN_TRANSITION_DURATION = 200; // 与css一致

export default class TimePicker extends React.Component<ITimePickerProps, ITimePickerState> {
    static defaultProps: TDefaultProps<ITimePickerProps>;
    static propTypes: TPropTypes<ITimePickerProps>;

    state: ITimePickerState = {
        currentValue: this.props.defaultValue || this.getFirstValidDate(),
        hourInputFocus: false,
        minuteInputFocus: false,
        secondInputFocus: false
    }

    comboBoxRef = React.createRef<ComboBox>();
    dropDownRef = React.createRef<Dropdown>();

    getFirstValidDate(): Dayjs {
        const { hourStep, minuteStep, secondStep, shouldEnableHour, shouldEnableMinute, shouldEnableSecond } = this.props;
        const hour = this.correctValue(0, hourStep!, MAX_HOUR, shouldEnableHour!);
        const minute = this.correctValue(0, minuteStep!, MAX_MINUTE, shouldEnableMinute!.bind(null, hour));
        const second = this.correctValue(0, secondStep!, MAX_SECOND, shouldEnableSecond!.bind(null, hour, minute));
        return this.createDayjsFromValues([hour, minute, second]);
    }

    handlePickChange = (values: string[]) => {
        if (this.props.disabled) {
            return;
        }
        const nextDate = this.createDayjsFromValues(values);
        this.setState({ currentValue: nextDate });
        this.props.onChange!(nextDate);
    }

    // final表示用户编辑完成，此时才执行回调及纠偏，滚动位置修正
    handleInputChange(type: 'hour' | 'minute' | 'second', newValue: string, final: boolean) {
        const { disabled, disableUserInput, hourStep, minuteStep, secondStep, shouldEnableHour, shouldEnableMinute, shouldEnableSecond } = this.props;
        if (disabled || disableUserInput) {
            return;
        }

        const prevDayjs = this.props.value || this.state.currentValue;
        const [prevHour, prevMinute] = this.getValuesFromDayjs(prevDayjs).map(str => Number(str));
        const value = Number(newValue);
        let nextDayjs: Dayjs | null = null;

        if (isNaN(value)) {
            return;
        }
        if (type === 'hour') {
            const nextHour = castInto(value, [0, MAX_HOUR]);
            nextDayjs = prevDayjs.set(
                'hour',
                final ?
                    this.correctValue(nextHour, hourStep!, MAX_HOUR, shouldEnableHour!)
                    : nextHour
            );
        } else if (type === 'minute') {
            const nextMinute = castInto(value, [0, MAX_MINUTE]);
            nextDayjs = prevDayjs.set(
                'minute',
                final ?
                    this.correctValue(nextMinute, minuteStep!, MAX_MINUTE, shouldEnableMinute!.bind(null, prevHour))
                    : nextMinute
            );
        } else if (type === 'second') {
            const nextSecond = castInto(value, [0, MAX_SECOND]);
            nextDayjs = prevDayjs.set(
                'second',
                final ?
                    this.correctValue(nextSecond, secondStep!, MAX_SECOND, shouldEnableSecond!.bind(null, prevHour, prevMinute))
                    : nextSecond
            );
        }
        this.setState({ currentValue: nextDayjs! });

        if (final) {
            this.props.onChange!(nextDayjs!);
            setTimeout(() => this.scrollComboBoxToSelected(), 0);   // 等子组件render完之后再触发
        }
    }

    // 进行step、disable(!enable)纠偏
    correctValue(value: number, step: number, max: number, isEnabled: (value: number) => boolean): number {
        const roundedValue = roundByStep(value, step);
        if (isEnabled(roundedValue) && 0 <= roundedValue && roundedValue <= max) {
            return roundedValue;
        }
        // 左右搜索最近的可用值
        let next = roundedValue + step;
        let prev = roundedValue - step;
        while (next <= max || prev >= 0) {
            if (next <= max && isEnabled(next)) {
                return next;
            }
            if (prev >= 0 && isEnabled(prev)) {
                return prev;
            }
            next += step;
            prev -= step;
        }
        // 没有可用值，说明条件设置有错
        throw new Error('TimePicker 参数无效，您可能禁用了所有值！');
    }

    handleHourInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        this.handleInputChange('hour', e.target.value, false);
    }

    handleMinuteInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        this.handleInputChange('minute', e.target.value, false);
    }

    handleSecondInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        this.handleInputChange('second', e.target.value, false);
    }

    handleComboBoxClick = (e: React.MouseEvent<HTMLDivElement>) => {
        e.stopPropagation();    // 点选combobox的时候不收回dropdown
    }

    handleHourInputFocus = () => {
        this.setState({ hourInputFocus: true });
    }

    handleHourInputBlur = (e: React.FocusEvent<HTMLInputElement>) => {
        this.setState({ hourInputFocus: false });
        this.handleInputChange('hour', e.target.value, true);
    }

    handleMinuteInputFocus = () => {
        this.setState({ minuteInputFocus: true });
    }

    handleMinuteInputBlur = (e: React.FocusEvent<HTMLInputElement>) => {
        this.setState({ minuteInputFocus: false });
        this.handleInputChange('minute', e.target.value, true);
    }

    handleSecondInputFocus = () => {
        this.setState({ secondInputFocus: true });
    }

    handleSecondInputBlur = (e: React.FocusEvent<HTMLInputElement>) => {
        this.setState({ secondInputFocus: false });
        this.handleInputChange('second', e.target.value, true);
    }

    handleComboBoxVisibleChange = (visible: boolean) => {
        if (!visible) {
            setTimeout(() => this.scrollComboBoxToSelected(), DROPDOWN_TRANSITION_DURATION);  // 等动画先执行完再刷新滚动位置
        }
    }

    handleInputSectionMouseDown = (e: React.MouseEvent<HTMLDivElement>) => {
        if (this.props.disableUserInput) {
            // 禁用用户手动输入时，可以点击输入框弹出combobox
            const dropDown = this.dropDownRef.current;
            if (dropDown) {
                dropDown.toggle();
                e.preventDefault();  // 不触发blur
            }
        }
    }

    scrollComboBoxToSelected() {
        const comboBox = this.comboBoxRef.current;
        if (comboBox) {
            comboBox.scrollToSelected();
        }
    }

    createDayjsFromValues(values: string[] | number[]): Dayjs {
        const resolvedValues: { [k in UnitType]?: number } = {
            hour: Number(values[0]),
            minute: Number(values[1]),
            second: this.props.showSecond ? Number(values[2]) : 0,
            millisecond: 0
        };
        return Object.getOwnPropertyNames(resolvedValues).reduce(
            (time: Dayjs, key: UnitType) => time.set(key, resolvedValues[key]!),
            dayjs()
        );
    }

    /**
     * 从dayjs对象获取小时、分钟、秒的字符串
     */
    getValuesFromDayjs(date: Dayjs): [string, string, string] {
        return [
            String(date.hour()).padStart(2, '0'),
            String(date.minute()).padStart(2, '0'),
            String(date.second()).padStart(2, '0')
        ];
    }

    componentDidMount() {
        this.scrollComboBoxToSelected();
    }

    render() {
        return consumeTheme(this.themedRender);
    }

    themedRender = (classNames: TThemedClassNames) => {
        const {
            prefixCls, className, style, hourStep, minuteStep, secondStep, showSecond, value: forceValue,
            underline, disabled, disableUserInput, shouldEnableHour, shouldEnableMinute, shouldEnableSecond
        } = this.props;
        const { currentValue: stateValue, hourInputFocus, minuteInputFocus, secondInputFocus } = this.state;
        const value = forceValue || stateValue;
        const selectedValues = this.getValuesFromDayjs(value);
        const [hour, minute, second] = selectedValues;
        const columns = createTimeColumns(hourStep!, minuteStep!, secondStep!, showSecond!);

        const focus = hourInputFocus || minuteInputFocus || secondInputFocus;
        const cls = classNames(prefixCls, className, {
            [`${prefixCls}-underline`]: underline,
            [`${prefixCls}${underline ? '-underline' : ''}-focus`]: focus,
            [`${prefixCls}-disabled`]: disabled
        });

        const comboDisableFuncs = [shouldEnableHour!, shouldEnableMinute!, shouldEnableSecond!].map(numEnableFunc => createStringParamFunc(numEnableFunc));
        const dropContent = (
            <div onClick={this.handleComboBoxClick}>
                <ComboBox
                    columns={columns}
                    onChange={this.handlePickChange}
                    selectedValues={selectedValues}
                    ref={this.comboBoxRef}
                    shouldEnableValues={comboDisableFuncs}
                />
            </div>
        );

        return (
            <div className={cls} style={style}>
                <div className={classNames(`${prefixCls}-container`)}>
                    <Dropdown
                        dropContent={dropContent}
                        disabled={disabled}
                        onVisibleChange={this.handleComboBoxVisibleChange}
                        ref={this.dropDownRef}
                    >
                        <div className={classNames(`${prefixCls}-icon-container`)}>
                            <svg className={classNames(`${prefixCls}-icon`)} viewBox="64 64 896 896" fill="currentColor" aria-hidden="true">
                                <path d="M512 64C264.6 64 64 264.6 64 512s200.6 448 448 448 448-200.6 448-448S759.4 64 512 64zm0 820c-205.4 0-372-166.6-372-372s166.6-372 372-372 372 166.6 372 372-166.6 372-372 372z"></path>
                                <path d="M686.7 638.6L544.1 535.5V288c0-4.4-3.6-8-8-8H488c-4.4 0-8 3.6-8 8v275.4c0 2.6 1.2 5 3.3 6.5l165.4 120.6c3.6 2.6 8.6 1.8 11.2-1.7l28.6-39c2.6-3.7 1.8-8.7-1.8-11.2z"></path>
                            </svg>
                        </div>
                    </Dropdown>
                    <div onMouseDown={this.handleInputSectionMouseDown}>
                        <input
                            type="text"
                            value={hour}
                            onChange={this.handleHourInputChange}
                            className={classNames(`${prefixCls}-input`)}
                            onFocus={this.handleHourInputFocus}
                            onBlur={this.handleHourInputBlur}
                            disabled={disabled || disableUserInput}
                        />
                        <span>时</span>
                        <input
                            type="text"
                            value={minute}
                            onChange={this.handleMinuteInputChange}
                            className={classNames(`${prefixCls}-input`)}
                            onFocus={this.handleMinuteInputFocus}
                            onBlur={this.handleMinuteInputBlur}
                            disabled={disabled || disableUserInput}
                        />
                        <span>分</span>
                        {showSecond ?
                            <React.Fragment>
                                <input
                                    type="text"
                                    value={second}
                                    onChange={this.handleSecondInputChange}
                                    className={classNames(`${prefixCls}-input`)}
                                    onFocus={this.handleSecondInputFocus}
                                    onBlur={this.handleSecondInputBlur}
                                    disabled={disabled || disableUserInput}
                                />
                                <span>秒</span>
                            </React.Fragment>
                            : null}
                    </div>
                </div>
            </div >
        );
    }
}

TimePicker.defaultProps = {
    prefixCls: 'dui-timepicker',
    disableUserInput: false,
    onChange: emptyFn,
    hourStep: 1,
    minuteStep: 1,
    secondStep: 1,
    showSecond: true,
    disabled: false,
    shouldEnableHour: trueFn,
    shouldEnableMinute: trueFn,
    shouldEnableSecond: trueFn,
    underline: false
};

TimePicker.propTypes = {
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    className: PropTypes.string,
    defaultValue: PropTypes.object,
    disableUserInput: PropTypes.bool,
    value: PropTypes.object,
    onChange: PropTypes.func,
    hourStep: PropTypes.number,
    minuteStep: PropTypes.number,
    secondStep: PropTypes.number,
    showSecond: PropTypes.bool,
    underline: PropTypes.bool,
    disabled: PropTypes.bool,
    shouldEnableHour: PropTypes.func,
    shouldEnableMinute: PropTypes.func,
    shouldEnableSecond: PropTypes.func
};

