import * as React from 'react';
/* eslint-disable no-unused-vars */
import Checkbox from './index';
import { ICheckboxGroupProps, IObjectOption, ICheckboxProps } from './interface';
import Radio from '../Radio';
import { IRadioProps, IRadioGroupProps } from '../Radio/interface';
/* eslint-enable no-unused-vars */

export function renderChildren(
    props: ICheckboxGroupProps | IRadioGroupProps,
    checkedValues: any[],
    handleSingleChange: (checked: boolean, e: any)=>void,
    Component: typeof Checkbox | typeof Radio,
    itemStyle?: React.CSSProperties
): React.ReactElement<ICheckboxProps>[] | React.ReactElement<IRadioProps>[] {

    const children = props.children;
    const options = props.options || [];
    const disabled = props.disabled;
    const style = {
        paddingBottom: 22, // 默认间距
        ...itemStyle
    };

    if (options.length) {
        return (options as any[]).map((option: string | IObjectOption, index: number) => {
            if (typeof option === 'string') {
                return (
                    <Component
                        key={index}
                        value={option}
                        onChange={handleSingleChange}
                        checked={checkedValues.indexOf(option) !== -1}
                        disabled={disabled}
                        style={{ ...style }}
                    >
                        {option}
                    </Component>
                );
            } else {
                return (
                    // @ts-ignore
                    <Component
                        {...option}
                        key={index}
                        value={option.value}
                        onChange={handleSingleChange}
                        checked={checkedValues.indexOf(option.value) !== -1}
                        disabled={option.disabled || disabled}
                        style={{ ...style, ...option.style }}
                    >
                        {option.label}
                    </Component>
                );
            }
        });
    }

    // @ts-ignore
    return React.Children.map(children, (child: React.ReactElement<ICheckboxProps | IRadioProps>) => {
        return React.cloneElement(child, {
            checked: checkedValues.indexOf(child.props.value) !== -1,
            onChange: handleSingleChange,
            disabled: child.props.disabled || disabled,
            style: {
                ...style,
                ...child.props.style
            }
        });
    });

}
