import * as React from 'react';
import * as PropTypes from 'prop-types';
import { TDefaultProps, TPropTypes, IStyleProps } from '../common/interface';
import { emptyFn } from './helper';
import classNames from 'classnames';
import { DocumentDomContext } from './documentDom';

interface ITriggerEvent extends MouseEvent {
    // eslint-disable-next-line no-use-before-define
    _dui_trigger_instances?: Trigger[];
}

interface ITriggerProps extends IStyleProps {
    action?: () => void;
    disabled?: boolean;
    domRef?: React.Ref<HTMLDivElement>;
    disableContextMenu?: boolean;
}

/**
 * 点击chilren外时触发某种操作
 */
export default class Trigger extends React.Component<ITriggerProps> {
    static defaultProps: TDefaultProps<ITriggerProps>;
    static propTypes: TPropTypes<ITriggerProps>;
    static contextType = DocumentDomContext;

    context!: React.ContextType<typeof DocumentDomContext>;

    componentDidMount() {
        this.doc.addEventListener('click', this.trigger);  // 比react事件后触发
        if (!this.props.disableContextMenu) {
            this.doc.addEventListener('contextmenu', this.trigger);
        }
    }

    componentWillUnmount() {
        this.doc.removeEventListener('click', this.trigger);
        if (!this.props.disableContextMenu) {
            this.doc.removeEventListener('contextmenu', this.trigger);
        }
    }

    get doc(): Document {
        return this.context || document;
    }

    trigger = (e: ITriggerEvent) => {
        if (this.props.disabled) {
            return;
        }
        if (e._dui_trigger_instances) {
            const index = e._dui_trigger_instances.indexOf(this);
            if (index !== -1) {
                e._dui_trigger_instances.splice(index, 1);
                if (e._dui_trigger_instances.length === 0) {
                    delete e._dui_trigger_instances;
                }
                return;
            }
        }
        this.props.action!();
    }

    cancel: React.MouseEventHandler = (e) => {
        if (this.props.disabled) {
            return;
        }
        const event = e.nativeEvent as ITriggerEvent;
        if (event._dui_trigger_instances) {
            event._dui_trigger_instances.push(this);
        } else {
            event._dui_trigger_instances = [this];
        }
    }

    render() {
        const { prefixCls, className, style, children, domRef, disableContextMenu } = this.props;
        const classes = classNames(prefixCls, className);
        return (
            <div
                className={classes}
                style={style}
                onClick={this.cancel}
                onContextMenu={disableContextMenu ? undefined : this.cancel}
                ref={domRef}
            >
                {children}
            </div>
        );
    }
}

Trigger.defaultProps = {
    prefixCls: 'dui-trigger',
    action: emptyFn,
    disabled: false,
    disableContextMenu: false,
};

Trigger.propTypes = {
    className: PropTypes.string,
    prefixCls: PropTypes.string,
    style: PropTypes.object,
    action: PropTypes.func,
    disabled: PropTypes.bool,
    domRef: PropTypes.object,
    disableContextMenu: PropTypes.bool
};

